import React from 'react'
import PropTypes from 'prop-types'
import EditIcon from '@material-ui/icons/Edit'
import {Link} from 'react-router-dom'
import {Button} from 'react'
import styledUpdateCommand  from './StyledUpdateCommand'

const ViewUpdateCommand = (props) => {
    const {id,titulo,pathEditComponentForUpdate,isIcon,permission} = props
    
    let _path = !permission ? '#' : {pathname:pathEditComponentForUpdate,state:{id:id}}
    const classes = styledUpdateCommand()
    
    return(
        <div>
             {isIcon ===true ? 
                (
                    <Link to={_path}  arial-label='link-para-atualizao'>
                        <EditIcon id={id} className={!permission ? classes.colorDisable : classes.color} />
                    </Link>
                )
                :
                (
                    <Link to={_path} arial-label='boto-para-atualizao'>
                        <Button  variant='contained' disabled={permission}>{titulo}</Button>
                    </Link>
                )
            }
        </div>
    )}

    ViewUpdateCommand.propTypes = {
    id: PropTypes.number.isRequired,
    titulo: PropTypes.string.isRequired,
    pathEditComponentForUpdate: PropTypes.string.isRequired,
    isIcon: PropTypes.bool.isRequired
}

 export default ViewUpdateCommand
