import { makeStyles } from '@material-ui/core/styles'
import {grey} from '@material-ui/core/colors'

const StyledReportTableContentHead = makeStyles((theme) => ({
    visuallyHidden: {
        border: 0,
        clip: 'rect(0 0 0 0)',
        height: 1,
        margin: -1,
        overflow: 'hidden',
        padding: 0,
        position: 'absolute',
        top: 20,
        width: 1,
      },
      rowColor: { 
        color: grey[700],
        backgroundColor: grey[200],
      },
      color: {
        color: theme.palette.primary.main
      },
      colorDisable: {
        color: 'gray',
        cursor: 'not-allowed'
      },
       headerTitle: {
         color: grey[700],
         fontSize: '1.2rem',
         [theme.breakpoints.down('md')]: {
            fontSize: '0.8rem'
         },
         width: '100%',
         display:'flex',
         justifyContent:'center',
         margin:'0',
         padding:'0'
      }
}))

export default StyledReportTableContentHead