import { makeStyles } from '@material-ui/core/styles'

const StyledKeyTableHead = makeStyles((theme) => ({
    visuallyHidden: {
        border: 0,
        clip: 'rect(0 0 0 0)',
        height: 1,
        margin: -1,
        overflow: 'hidden',
        padding: 0,
        position: 'absolute',
        top: 20,
        width: 1,
      },
      color: {
        color: theme.palette.primary.main
      },
      colorDisable: {
        color: 'gray',
        cursor: 'not-allowed'
      }
}))

export default StyledKeyTableHead