import React from 'react'
import {Link} from "react-router-dom"
import {Container,Paper,Box,Tooltip,Button} from '@material-ui/core'
import {FormControl,Input,InputLabel} from '@material-ui/core'
import StyledLogin from './StyledLogin'
import LoginImg from '../../Assets/Imgs/Logotipo.svg'
import LockIcon from '@material-ui/icons/Lock'
import MailIcon from '@material-ui/icons/Mail'
import AssignmentIndIcon from '@material-ui/icons/AssignmentInd';
import InputAdornment from '@material-ui/core/InputAdornment'
import DisplayErrosMessage from '../../ComponentsLibrary/DisplayErrorsMessage/DisplayErrorsMessage'
import DisplayInfoMessage from '../../ComponentsLibrary/DisplayInfoMessage/DisplayInfoMessage'
import LinearProgress from '@material-ui/core/LinearProgress'



const ViewLogin = (props) => {
    
    const {handleSubmit,user,
            handleChange,infoErrors,
            serverValidation,handleCloseInfo,
            handleCloseErros,createUsersByAdmin,loading} = props

    const classes = StyledLogin() 

    return(
        <Container maxWidth='xs'>
            <Box  mt={5} >
                <Paper>
                    <Box display="flex" justifyContent="center" m={1} p={1}  >
                        <Box>
                            <p><img alt="locker" src={LoginImg}  width='90%' height='90%'/></p>
                        </Box>
                    </Box>
                    <Box m={1}>
                        {createUsersByAdmin ?
                                <FormControl fullWidth={true}>
                                <InputLabel htmlFor="input-login">Usurio</InputLabel>
                                <Input  autoFocus  className={classes.margin}
                                id='logonUser'
                                name='logonUser'
                                label='Usurio' 
                                autoComplete='off'
                                type='text'
                                onChange={(e)=> {handleChange(e)}}
                                value={user.logonUser}
                                inputProps = {{maxLength:35}}
                                startAdornment={
                                    <InputAdornment position="start">
                                    <AssignmentIndIcon color='primary' />
                                    </InputAdornment>
                                }/>
                            </FormControl>
                        :     
                            <FormControl fullWidth={true}>
                                <InputLabel htmlFor="input-email">E-mail</InputLabel>
                                <Input  autoFocus  className={classes.margin}
                                id='email'
                                name='email'
                                label='email' 
                                autoComplete='off'
                                type='text'
                                onChange={(e)=> {handleChange(e)}}
                                value={user.email}
                                inputProps = {{maxLength:35}}
                                startAdornment={
                                    <InputAdornment position="start">
                                    <MailIcon color='primary' />
                                    </InputAdornment>
                                }/>
                            </FormControl>    
                        }
                        <FormControl fullWidth={true}>
                            <InputLabel htmlFor="input-senha">Senha</InputLabel>
                            <Input className={classes.margin}
                            id='password'
                            name='password'
                            label='senha' 
                            autoComplete='off'
                            type='password'
                            onChange={(e)=> {handleChange(e)}}
                            onKeyPress={(e)=> { if (e.key === 'Enter') {handleSubmit(e)}}}
                            value={user.senha}
                            inputProps = {{maxLength:8}}
                            startAdornment={
                                <InputAdornment position="start">
                                <LockIcon color='primary' />
                                </InputAdornment>
                            }/>
                            
                        </FormControl>
                        <Box display='flex' justifyContent='center'>
                            <Tooltip title="Entrar no sistema" placement='top'>
                                <Button variant="outlined" color="primary" aria-label="enter" className={classes.margin}  onClick={(e)=> {handleSubmit(e)}} onKeyPress={(e)=> { if (e.key === 'Enter') {handleSubmit(e)}}}>
                                    Entrar
                                </Button>
                            </Tooltip>
                        </Box>
                        <Box  justifyContent='center'>
                        </Box>
                        
                        {!createUsersByAdmin &&
                        <>
                            <Box display='flex' justifyContent='center'>
                                <Link to="/recoveryPass" className={classes.linkDecoration} >
                                    Solicitar nova senha
                                </Link>
                            </Box>    
                            <Box display='flex' justifyContent='center'>
                                <Link to="/register" className={classes.linkDecoration} >
                                    Criar nova Conta
                                </Link>
                            </Box>
                        </>
                        }

                        <Box pb={1}>
                            <DisplayErrosMessage infoErrors={infoErrors} handleCloseErros={handleCloseErros}  />
                            <DisplayInfoMessage serverValidation={serverValidation} handleCloseInfo={handleCloseInfo} />
                        </Box>
                    </Box>
                    {loading && 
                        <LinearProgress />      
                    }
                </Paper>
            </Box>
        </Container>
    )}
export default ViewLogin
