function InputValidateTruck(event) {
    
    let ret={msg:"",isError:false}

    switch (event.target.name) {
        case 'at':
            if(event.target.value.length === 0) {
                ret.msg = 'Valor obrigatrio.'
                ret.isError=true
            }
            break
        case 'totalCompart':
            if(event.target.value.length === 0) {
                ret.msg = 'Valor obrigatrio.'
                ret.isError=true
              }
            break
        case 'capacity':
            if(event.target.value.length === 0) {
                ret.msg = 'Valor obrigatrio.'
                ret.isError=true
                }
            break
        case 'compartFirstTrain':
            if(event.target.value.length === 0) {
                ret.msg = 'Valor obrigatrio.'
                ret.isError=true
                }
        break
        default:
            ret.msg=""
            ret.isError=false
        }
        return ret
    }

    export default InputValidateTruck