import React from 'react'
import ViewEnhancedTableHead from './ViewEnhancedTableHead'
import { useSelector } from 'react-redux'
import verifyPermissions from '../../../Assets/js/verifyPermissions'
  
function EnhancedTableHead(props) {
   
    const {order, orderBy, rowCount, onRequestSort,
        headCells,routeName,executeFiltering,pathAddComponentForUpdate,
        showAddRegisterCommand,landscapeView,allRows,titleReport} = props;

    const permissions = useSelector(state=> state.permission)
    const userAuthenticated = useSelector(state=> state.authenticated)
    let permission = verifyPermissions(routeName,'write',userAuthenticated,permissions)

    return(<ViewEnhancedTableHead
        showAddRegisterCommand={showAddRegisterCommand}
        order={order}
        permission={permission}
        orderBy={orderBy}
        rowCount={rowCount}
        onRequestSort={onRequestSort}
        headCells={headCells}
        landscapeView={landscapeView}
        allRows={allRows}
        titleReport={titleReport}
        executeFiltering={executeFiltering}
        pathAddComponentForUpdate={pathAddComponentForUpdate}  />)
}

export default EnhancedTableHead