import React from 'react'
import { useSelector } from 'react-redux'
import { Route,Redirect} from 'react-router-dom'
import verifyAuth from './verifyAuth'
import verifyPermissions from '../../Assets/js/verifyPermissions'

function PrivateRoute({ children, ...rest }) { 
    const permissionList = useSelector(state=> state.permission)
    const userAuthenticated = useSelector(state => state.authenticated)


    //Quando temos diversas tabs abertas ao realizar o logout em uma das tabs,  necessrio 
    //verificar os dados persistidos no localstorage do browser para identificar se usurio 
    //ainda esta logado. Poderimos utilizar o redux. porm o comportamento do redux  diferente 
    //mesmo limpando a store ao fazer o logoout em uma das tabs, nas outras a store ainda contm 
    //os dados. Ento foi necessrio acessar diretamente a localstorege do browser.
    let alive = true 
    if (rest.routeName !== 'Home') { 
        let local_storage = JSON.parse(window.localStorage.getItem('persist:root'))
        alive = local_storage.mnuMounted === 'true' ?  true : false 
    } 

    let auth = verifyAuth(userAuthenticated)

    let routeName = rest.routeName
    let permission = verifyPermissions(routeName,'read',userAuthenticated,permissionList)  

    return(
        auth && alive ? 
            permission  ? 
                <Route {...rest}>{children}</Route>
            :
                <Redirect push  to='/PermissionNotFound' />
        :
            <Redirect push  to='/Login' />
        )
}

export default PrivateRoute