import React from 'react'
import {Box} from '@material-ui/core'
import {Alert} from '@material-ui/lab'

const ViewDisplayErrorsMessage = (props) => {
 
    const {infoErrors,handleCloseErros} = props

    return(
            <>
                {infoErrors.msg !== '' && 
                <Box m={1} >
                    <Alert severity='error' variant='filled' onClose={()=>handleCloseErros()}>{infoErrors.msg}</Alert>
                </Box>
                }
            </>
    ) 
}

export default ViewDisplayErrorsMessage
