import React from 'react'
import {Box} from '@material-ui/core'
import {Alert} from '@material-ui/lab'

const ViewDisplayInfoMessage = (props) => {
 
    const {serverValidation,handleCloseInfo} = props

    return(
        <>
        {serverValidation.msg !== '' && 
        <Box m={1} >
            <Alert severity='info' variant='filled' onClose={()=>handleCloseInfo()}>{serverValidation.msg}</Alert>
        </Box>
        }
    </>
    ) 
}

export default ViewDisplayInfoMessage
