import { makeStyles } from '@material-ui/core/styles'
import {grey} from '@material-ui/core/colors'

const StyledUpdateAvaliableKey = makeStyles((theme) => ({
  root: {
    "& .MuiTextField-root": {
      margin: theme.spacing(2),
    }
  },
  MainBox :{
    display:'flex',
    justifyContent:'center',
    margin: theme.spacing(1),
    padding: theme.spacing(1),
  },
  PaperMain: { 
    width:'80%',
    paddingBottom: theme.spacing(2),
    [theme.breakpoints.down('md')]: { 
      width: '100%' 
  }},
  color: {
    color: theme.palette.primary.main
  },
  ContaineFormControls : {
    display:'Flex',
    justifyContent:'center',
    flexWrap:'wrap',
    flexDirection:'column',
    
  },
  AvaliableKeyTitle: {
    color: grey[900],
    fontSize: '1rem',
    [theme.breakpoints.down('md')]: {
      fontSize: '0.8rem',
    },
    display: 'flex',
    justifyContent:'center',
    width: '100%',
    border:'1px solid black'
  },

  AvaliableKeyValue: {
    color: grey[700],
    fontSize: '1rem',
    [theme.breakpoints.down('md')]: {
      fontSize: '0.8rem',
    },
    paddingLeft: theme.spacing(1),
    paddingRight: theme.spacing(1),
    alignSelf:'center',
    width: '100%',
    border:'1px solid black'
  },
  AvaliableKeyContainerTitle: { 
    display:'flex',
    justifyContent:'center',
    marginTop: theme.spacing(1),
    backgroundColor:  grey[200],
    padding: theme.spacing(1),
  },
  AvaliableKeyContainerValue: { 
    display:'flex',
    justifyContent:'center',
    padding: theme.spacing(1),
  },
   AvaliableKeyBox: {
    display:'flex'
  }
}))


export default StyledUpdateAvaliableKey




