import React from 'react'
import {useState,useEffect} from 'react'
import ViewUpdateConfigParameter from './ViewUpdateConfigParameter'
import {getApi} from '../../../../Assets/js/apiProxy'
import InputValidateConfigParameter from './InputValidateConfigParameter'
import {infoError} from '../../../../Assets/js/infoError'

function UpdateConfigParameter(props) {
   
   const api = getApi('api_features')

   const { location: { state }} = props
   let prop_id = state === undefined ? 0 : state.id

   let configParameterState = {id:prop_id,parameter:'',
                    description:'',
                    parameterValue: ''}

   const [configParameter,editableConfigParameter] = useState(configParameterState)
   const [infoErrors,setInfoErrors] = useState({msg:''})
   const [serverValidation,setServerValidation] = useState({msg:''})
   const [loading, setLoading] = useState(false)

   let validationState = {isError:false,
                          keys:{id:prop_id,
                                parameter:'',
                                description:'',
                                parameterValue:''}}

   const [inputvalidation,setInputValidation] = useState(validationState)


   useEffect(()=> {
        if (configParameterState.id !== 0) {
         api.get(`/editConfigParameter/${configParameterState.id}`)
         .then(function (response) {
         
         if (response.data.registers !== undefined) { 
            let configParameter_data = response.data.registers.shift()
            editableConfigParameter(configParameter_data)
         }
         document.getElementById('parameter').focus()
         })
         .catch(function (error) {  
            let infoErrorsState = infoError(error)
            console.log(infoErrorsState.msg)
            setInfoErrors(infoErrorsState)
         })

         return ()=> { setLoading(false)}
     } 
   },[configParameterState.id,api])

   function handleCloseErros() { 
      setInfoErrors({msg:''})
   }

   function handleCloseInfo() { 
      setServerValidation({msg:''})
   }

   function handleChange(event,hasMask) {
      if (hasMask) {
            event.target.value = event.target.value.replace(/[, ]+/g,'').replace(/\(|\)/g, "") 
      }
      editableConfigParameter({...configParameter,[event.target.name]:event.target.value})
   }

   function handleBlur(event) {
      let ret = InputValidateConfigParameter(event)
      
      //Spread Operator - Converte o objeto inputValidation em Array, e atualiza o item do array 
      //identificado pelo valor originado por event.target.nameo com o novo valor ret.msg.
      const keys = {keys:{...inputvalidation.keys,[event.target.name]:ret.msg}}
      
      //Rest Operator - Pega o Objeto key e expande em propriedades passando como argumentos para o 
      //setInputValidation.
      setInputValidation({isError:ret.isError,...keys})
      
   }
      
  function handleSubmit(event) {
      setLoading(true)
      
      //#region
         //Necessrio para qua ocorr a quebra de linha na tabela de visualizao para campos 
         //tipo multiline.
      let position = 44
      let lines = parseInt(configParameter.description.length /44)
      for (let index = 0; index < lines; index++) {
          configParameter.description = [configParameter.description.slice(0, position), ' ', configParameter.description.slice(position)].join('')
          position=position + 44 
      }
      //#endregion
      
      if (!inputvalidation.isError) {
         if (configParameterState.id !== 0) {
            
            api.put('/updateConfigParameter',configParameter)
            .then(function (response) {
               if (response.data.bsApproved) {
                  props.history.push('/allConfigParameter')
               } else {
                  let serverValidationState = {msg:response.data.msg}
                  setServerValidation(serverValidationState)
               }
            })
            .catch(function (error) {  
               let infoErrorsState = infoError(error)
               console.log(infoErrorsState.msg)
               setInfoErrors(infoErrorsState)
            })
            .finally(()=> {
               setLoading(false)
            })
         }else {
           api.post('/addConfigParameter',configParameter)
               .then(function (response) {
                  if (response.data.bsApproved) {
                     props.history.push('/allConfigParameter')
                  } else {
                     let serverValidationState = {msg:response.data.msg}
                     setServerValidation(serverValidationState)
                  }
            })
            .catch(function (error) {  
               let infoErrorsState = infoError(error)
               console.log(infoErrorsState.msg)
               setInfoErrors(infoErrorsState)
            })
            .finally(()=> {
               setLoading(false)
            })
         }
      } else {
         setLoading(false)
      }
      event.preventDefault()
  }

   return (<ViewUpdateConfigParameter configParameter={configParameter}
          handleChange={handleChange}
          handleSubmit={handleSubmit}
          handleBlur={handleBlur}
          handleCloseErros={handleCloseErros}
          handleCloseInfo={handleCloseInfo}
          inputvalidation={inputvalidation}
          infoErrors={infoErrors}
          serverValidation={serverValidation}
          loading = {loading}
           />)
   
}

export default UpdateConfigParameter