import jwt_decode  from 'jwt-decode'
import {setApiAuth} from '../../Assets/js/apiProxy'

const verifyAuth = (userAuthenticated) => {
    
    let auth = false 
    let expired = false  
   
    if (userAuthenticated !== null) { 
            if (userAuthenticated.auth === true) { 
                
                //Com o usurio autnticado podemos incluir o token no header de todas as apis que estivermos 
                //conversando.
                auth = true 
                setApiAuth(userAuthenticated.token)

                //Verifica se o Token Esta Expirado 
                var decoded = jwt_decode(userAuthenticated.token)
                const  {exp} = decoded
                var  current_time  =  Date.now().valueOf()/1000  //Formato UTC sem fuso.
                if  (  exp <  current_time )  { 
                    expired = true 
                }
            }
        }

    if (auth && !expired ) { 
        return (true)
    }else {
        return (false)
    }
}

export default verifyAuth