import { makeStyles } from '@material-ui/core/styles'

const StyledTableRows = makeStyles((theme) => ({
  
      root: {
        width: '100%',
      },
      table: {
        minWidth: 750,
      },
      profileList: {
        width: '100%',
      },
      description: {
        width: '100%'
      },
      sealNumber: {
        width: '100%'
      },
      marginSave: { 
        marginRight: theme.spacing(1) 
      },
      tableCell: {
        overflowWrap:'break-word',
        whiteSpace:'normal',
        paddingBottom: theme.spacing(1),
        paddingTop: theme.spacing(1),
      },
      saveBtn: {
        cursor:'pointer',
      },
      colorDisable: {
        color: 'gray',
        cursor: 'not-allowed'
      }
}))


export default StyledTableRows