import React from 'react'
import {TextField,InputLabel,Select,Box} from '@material-ui/core'
import StyledUpdateProfile from './StyledUpdateProfile'
import ListIcon from '@material-ui/icons/List'
import BodyForm from '../../../../ComponentsLibrary/Form/BodyForm/BodyForm'


const ViewUpdateProfile =  (props)  => {

    const {handleSubmit,profile,
            handleChange,handleBlur,
            inputvalidation,infoErrors,
            serverValidation,handleCloseInfo,
            handleCloseErros,loading} = props

    const styledUpdateProfile = StyledUpdateProfile()

    return( <BodyForm infoErrors={infoErrors} 
            serverValidation={serverValidation} 
            handleCloseInfo={handleCloseInfo}
            handleCloseErros={handleCloseErros}
            handleSubmit={handleSubmit}
            loading={loading}
            icone={<ListIcon fontSize='large' className={styledUpdateProfile.color} />}
            title='Cadastro de Perfil'
            urlBackCommand='/allProfile'
            showSaveButton={true} showBackButton={true}
            paperMainWidth='30%'>
                    
                    <TextField 
                            id='name' 
                            name='name'
                            label='Perfil' 
                            autoComplete='off'
                            className={styledUpdateProfile.largeTextField}
                            value={profile.name} 
                            onChange={event=> handleChange(event,false)}
                            onBlur={event=> handleBlur(event)}
                            helperText={inputvalidation.keys.name !== '' ? inputvalidation.keys.name  : ''}
                            error={inputvalidation.keys.name !== '' ? true : false}
                            inputProps={{ maxLength: 45 }}
                        />
                        <TextField 
                            id='description' 
                            name='description'
                            label='Descrio'
                            autoComplete='off'
                            className={styledUpdateProfile.largeTextField}
                            value={profile.description} 
                            onChange={event=> handleChange(event,false)}
                            onBlur={event=> handleBlur(event)}
                            helperText={inputvalidation.keys.description !== '' ? inputvalidation.keys.description  : ''}
                            error={inputvalidation.keys.description !== '' ? true : false}
                            inputProps={{ maxLength: 100 }}
                        />
                        <Box display='flex' justifyContent='center' m={2}> 
                            <Box>
                                <InputLabel htmlFor="profile-native-simple">Hierarquia de Acesso</InputLabel>
                                <Box display='flex' justifyContent='center' >
                                    <Select native
                                        value={profile.accessLevel}
                                        onChange={e=>handleChange(e,false)}
                                        inputProps={{
                                            name: 'accessLevel',
                                            id: 'accessLevel-native-simple',
                                        }}>
                                        <option value="-1">Nveis...</option>
                                        <option value="1">1</option>
                                        <option value="2">2</option>
                                        <option value="3">3</option>
                                        <option value="4">4</option>
                                        <option value="5">5</option>
                                    </Select>
                                </Box>
                            </Box>
                        </Box>
                        
    </BodyForm>
    )

}
export default ViewUpdateProfile
