import api_features from '../../Config/api_config'
import api_DriverSms from '../../Config/api_driverSms'
import api_overfill from '../../Config/api_overfill'
import api_multiloadControl from '../../Config/api_multiloadControl'

export const getApi = (host_api) => {

    switch (host_api) {
       case 'api_driverSms':
          return api_DriverSms
       case 'api_overfill':
          return api_overfill
       case 'api_features':
          return api_features
       case 'api_multiloadControl':
          return api_multiloadControl   
       default:
          return api_features  
    }
}

export const setApiAuth = (token) => {

            api_DriverSms.defaults.headers.common['authorization'] = token
            api_overfill.defaults.headers.common['authorization'] = token
            api_features.defaults.headers.common['authorization'] = token
            api_multiloadControl.defaults.headers.common['authorization'] = token
}
