import React from 'react'
import InputMask from 'react-input-mask'
import { FormControl,Box,TextField,Paper, Container} from '@material-ui/core'
import StyledRegister from './StyledRegister'
import HeaderForm from '../../ComponentsLibrary/Form/HeaderForm/HeaderForm'
import FooterForm from '../../ComponentsLibrary/Form/FooterForm/FooterForm'
import DisplayErrosMessage from '../../ComponentsLibrary/DisplayErrorsMessage/DisplayErrorsMessage'
import DisplayInfoMessage from '../../ComponentsLibrary/DisplayInfoMessage/DisplayInfoMessage'
import LinearProgress from '@material-ui/core/LinearProgress'
import PersonOutlinedIcon from '@material-ui/icons/PersonOutline'


const viewRegister =  (props)  => {

    const {handleSubmit,user,
            handleChange,handleBlur,
            inputvalidation,infoErrors,
            serverValidation,handleCloseInfo,
            handleCloseErros,
            loading} = props

    const styledRegister = StyledRegister()

    return(    
        <Container  maxWidth='xs'>
            <Box className={styledRegister.MainBox}>
                    <Paper elevation={3} >
                        <HeaderForm icon={<PersonOutlinedIcon fontSize='large' style={{color:'blue'}}/>} titulo={'Criar nova Conta'}/>
                        <FormControl  className={styledRegister.root}>
                            <div>
                                <TextField  
                                    id='name' 
                                    name='name'
                                    label='Nome' 
                                    autoComplete='off'
                                    className={styledRegister.large}
                                    value={user.name} 
                                    onChange={event=> handleChange(event,false)}
                                    onBlur={event=> 
                                        handleBlur(event)
                                    }
                                    helperText={inputvalidation.keys.name !== '' ? inputvalidation.keys.name  : ''}
                                    error={inputvalidation.keys.name !== '' ? true : false}
                                    inputProps={{ maxLength: 35 }}
                                />
                                
                                <TextField 
                                    id='email' 
                                    name='email'
                                    label='e-mail'
                                    autoComplete='off'
                                    className={styledRegister.large}
                                    value={user.email} 
                                    onChange={event=> handleChange(event,false)}
                                    onBlur={event=> handleBlur(event)}
                                    helperText={inputvalidation.keys.email !== '' ? inputvalidation.keys.email  : ''}
                                    error={inputvalidation.keys.email !== '' ? true : false}
                                    inputProps={{ maxLength: 35 }}
                                />
                            </div>
                            <div>
                                <InputMask 
                                    mask='(99)99999 9999'
                                    maskChar=''
                                    value={user.telefone} 
                                    onChange={event=> handleChange(event,true)}
                                    onBlur={event=> handleBlur(event)}>
                                    {inputProps =>
                                        <TextField
                                        {...inputProps}
                                        id='phone' 
                                        name='phone'
                                        label='Telefone'
                                        autoComplete='off'
                                        className={styledRegister.small}
                                        helperText={inputvalidation.keys.phone !== '' ? inputvalidation.keys.phone  : ''}
                                        error={inputvalidation.keys.phone !== '' ? true : false}
                                        />}
                                </InputMask>
                                <InputMask 
                                    mask='999.999.999.99'
                                    maskChar=''
                                    value={user.cpf} 
                                    onChange={event=> handleChange(event,true)}
                                    onBlur={event=> handleBlur(event)}>
                                    {inputProps =>
                                        <TextField
                                        {...inputProps}
                                        id='cpf' 
                                        name='cpf'
                                        label='CPF'
                                        autoComplete='off'
                                        className={styledRegister.small}
                                        helperText={inputvalidation.keys.cpf !== '' ? inputvalidation.keys.cpf  : ''}
                                        error={inputvalidation.keys.cpf !== '' ? true : false}
                                        />}
                                </InputMask> 
                            </div>
                                <Box display='flex' justifyContent='center'>
                                        <TextField 
                                            id='password' 
                                            name='password'
                                            label='Senha'
                                            autoComplete='off'
                                            className={styledRegister.small}
                                            value={user.password} 
                                            type='password'
                                            onChange={event=> handleChange(event,false)}
                                            onBlur={event=> handleBlur(event)}
                                            helperText={inputvalidation.keys.password !== '' ? inputvalidation.keys.password  : ''}
                                            error={inputvalidation.keys.password !== '' ? true : false}
                                            inputProps={{ maxLength: 8 }}
                                        />
                                </Box>

                            <DisplayErrosMessage infoErrors={infoErrors} handleCloseErros={handleCloseErros}  />
                            <DisplayInfoMessage serverValidation={serverValidation} handleCloseInfo={handleCloseInfo}/>
                            <FooterForm handleSubmit={handleSubmit} urlBackCommand={'/login'} showSaveButton={true} showBackButton={true} />      
                            {loading && 
                                <LinearProgress />      
                            }
                        </FormControl>
                    </Paper>
            </Box>
        </Container>
    )

}
export default viewRegister
