import React from 'react'
import StyledReadMultiloadControl from './StyledReadMultiloadControl'
import { withStyles } from '@material-ui/core/styles'
import {Box,Container,Button,TextField,Tooltip,Link} from '@material-ui/core'
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableContainer from '@material-ui/core/TableContainer'
import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'
import Paper from '@material-ui/core/Paper'
import BuildIcon from '@material-ui/icons/Build'
import LockOpenOutlinedIcon from '@material-ui/icons/LockOpenOutlined'
import LockOutlinedIcon from '@material-ui/icons/LockOutlined'
import NetworkCheckIcon from '@material-ui/icons/NetworkCheck'
import IconButton from '@material-ui/core/IconButton'
import CircularProgress from '@material-ui/core/CircularProgress'
import LinearProgress from '@material-ui/core/LinearProgress'
import Typography from '@material-ui/core/Typography'
import Dialog from '@material-ui/core/Dialog'
import DialogActions from '@material-ui/core/DialogActions'
import DialogContent from '@material-ui/core/DialogContent'
import DialogContentText from '@material-ui/core/DialogContentText'
import DialogTitle from '@material-ui/core/DialogTitle'
import Snackbar from '@material-ui/core/Snackbar'
import CloseIcon from '@material-ui/icons/Close'

  const ViewReadMultiloadControl = (props) => {
        const {MultiloadControl,loading,handleClose,
               infoErrors,handleToggleStatus,open,
               handleSaveReason,handleChange,operationalReason,
               handleCloseSnack,visibility,loadingDialog} = props
     
        const styledReadMultiloadControl=StyledReadMultiloadControl()
        const StyledTableCell = withStyles((theme) => ({
          head: {
            backgroundColor: theme.palette.common.white,
            color: theme.palette.common.grey,
            fontWeight:'bold'
          },
          body: {
            fontSize: 12,
          },
        }))(TableCell)
        

      return(
        <Container maxWidth="lg">
            <Box className={styledReadMultiloadControl.MainBox}>
              <Paper elevation={3} className={styledReadMultiloadControl.PaperMain}>
                  <Box display='flex' justifyContent='flex-start' arial-label='header-title-icon' m={1}>
                    <Box display='flex'arial-label='header-description'>
                        <BuildIcon fontSize='large' className={styledReadMultiloadControl.color} />
                        <Box fontWeight='medium' mt={1} ml={1}>
                          <Typography id="formTitle">
                            Configurar Multiload
                          </Typography>
                        </Box>
                    </Box>
                  </Box>
                  <Box p={1}>
                  <TableContainer component='div'>
                    <Table className={styledReadMultiloadControl.table} aria-label="tabela controle de multiloads">
                      <TableHead>
                        <TableRow>
                          <StyledTableCell align="center">Plataforma</StyledTableCell>
                          <StyledTableCell align="center">Baia</StyledTableCell>
                          <StyledTableCell align="center">Acesso</StyledTableCell>
                          <StyledTableCell align="center">Multiload</StyledTableCell>
                          <StyledTableCell align="center">Aferio</StyledTableCell>
                          <StyledTableCell align="center">Manuteno</StyledTableCell>
                        </TableRow>
                      </TableHead>
                      {!loading && (
                          <TableBody>
                            {MultiloadControl.map((row,i) => (
                                <TableRow key={row.bayNumber} className={ i % 2? styledReadMultiloadControl.TableRowColor : null}>
                                  <StyledTableCell align="center">{row.platform}</StyledTableCell>
                                  <StyledTableCell align="center">
                                    <Link href={`http://${row.ip}`} target="_blank" rel="noopener">
                                      {row.bayName}
                                    </Link>  
                                  </StyledTableCell>
                                  
                             
                                {row.bayStatus  ? 
                                    <StyledTableCell align="center">
                                      <Tooltip title="Acesso ao ML Liberado">
                                           <Button   variant="outlined" onClick={()=> handleToggleStatus('bayStatus',0,row.bayNumber)}>
                                                BLOQUEAR
                                           </Button>                                    
                                        </Tooltip>
                                    </StyledTableCell>
                                  :
                                    <StyledTableCell align="center">
                                      <Tooltip title="Acesso ao ML Bloqueado">
                                           <Button   variant="outlined" onClick={()=> handleToggleStatus('bayStatus',1,row.bayNumber)} >
                                                LIBERAR
                                           </Button> 
                                      </Tooltip>
                                    </StyledTableCell>
                                }  

                                {row.bayStatus  ? 
                                    <StyledTableCell align="center">
                                              <LockOpenOutlinedIcon className={styledReadMultiloadControl.colorPermited}  />
                                    </StyledTableCell>
                                  :
                                    <StyledTableCell align="center">
                                            <LockOutlinedIcon className={styledReadMultiloadControl.colorNotPermited} onClick={()=> handleToggleStatus('bayStatus',1,row.bayNumber)} />
                                    </StyledTableCell>
                                }  

                                  {row.measureStatus  ? 
                                    <StyledTableCell align="center">
                                      <Tooltip title="Aferio Liberada no ML">
                                          <IconButton aria-label="measureStatus" size="large" className={styledReadMultiloadControl.NoPadding}>
                                              <NetworkCheckIcon className={styledReadMultiloadControl.colorPermited} onClick={()=> handleToggleStatus('measureStatus',0,row.bayNumber)} />
                                          </IconButton> 
                                      </Tooltip> 
                                    </StyledTableCell>
                                  :
                                  <StyledTableCell align="center">
                                    <Tooltip title="Aferio Bloqueada no ML">
                                        <IconButton aria-label="measureStatus" size="large" className={styledReadMultiloadControl.NoPadding}>
                                            <NetworkCheckIcon className={styledReadMultiloadControl.colorNotPermited} onClick={()=> handleToggleStatus('measureStatus',1,row.bayNumber)} />
                                        </IconButton> 
                                      </Tooltip>
                                    </StyledTableCell>
                                }  

                                  {row.maintenanceStatus  ? 
                                    <StyledTableCell align="center">
                                      <Tooltip title="Manuteno Liberada no ML">
                                        <IconButton aria-label="maintananceStatus" size="large" className={styledReadMultiloadControl.NoPadding}>
                                            <BuildIcon className={styledReadMultiloadControl.colorPermited} onClick={()=> handleToggleStatus('maintenanceStatus',0,row.bayNumber)} />
                                        </IconButton> 
                                      </Tooltip>
                                    </StyledTableCell>
                                  :
                                    <StyledTableCell align="center">
                                      <Tooltip title="Manuteno Bloqueada no ML">
                                        <IconButton aria-label="maintananceStatus" size="large" className={styledReadMultiloadControl.NoPadding}>
                                            <BuildIcon className={styledReadMultiloadControl.colorNotPermited} onClick={()=> handleToggleStatus('maintenanceStatus',1,row.bayNumber)} />
                                        </IconButton> 
                                      </Tooltip>
                                    </StyledTableCell>
                                }  
                                </TableRow>
                              
                            ))}
                          </TableBody>
                      )} 
                    </Table>
                    {loading && (
                      <Box display="flex" justifyContent="center" m={1} p={1} >
                        <Box p={1}>
                            <CircularProgress/>
                        </Box>
                      </Box>
                    )}
                  </TableContainer>
               </Box>
               {open && (
                 <Box>
                    <Dialog
                        open={open}
                        onClose={handleClose}
                        aria-labelledby="multiload-operational-reason"
                        aria-describedby="multiload-operational-reason">
                
                        <DialogTitle id="operation-reason-dialogTitle">{"Por favor, informe o motivo desta operao:"}</DialogTitle>
                        <DialogContent>
                        <DialogContentText id="alert-dialog-description">
                            <TextField 
                                id='operationalReason' 
                                name='operationalReason'
                                label=''
                                autoComplete='off'
                                value={operationalReason} 
                                onChange={(e)=> handleChange(e)}
                                multiline
                                className={styledReadMultiloadControl.largeTextField}
                                inputProps={{ maxLength: 128 }}
                                inputRef={input => input && input.focus()}
                            />
                        </DialogContentText>
                        </DialogContent>
                        <DialogActions>
                        <Button onClick={handleSaveReason} color="primary" autoFocus>
                            Salvar 
                        </Button>
                        <Button onClick={handleClose} color="primary" autoFocus>
                            Fechar
                        </Button>
                        </DialogActions>
                        {loadingDialog && (
                           <LinearProgress />
                        )}
                    </Dialog>
                </Box>
               )}
              </Paper>
            </Box>
            <Snackbar  anchorOrigin={{vertical: 'bottom',horizontal: 'center'}}
              open={visibility}  autoHideDuration={5000} onClose={handleCloseSnack} message={infoErrors.msg}
              action={
                  <React.Fragment>
                      <IconButton size="small" aria-label="close" color="inherit" onClick={handleCloseSnack}>
                      <CloseIcon fontSize="small" />
                      </IconButton>
                  </React.Fragment>
              } /> 
            
        </Container>
      )}

export default ViewReadMultiloadControl
