import React from 'react'
import StyledReadConfigParameter from './StyledReadConfigParameter'
import {Box,Container} from '@material-ui/core'
import EnhancedTableRows from '../../../../ComponentsLibrary/EnhancedTable/EnhancedTableRows/EnhancedTableRows'
import Snackbar from '@material-ui/core/Snackbar'
import IconButton from '@material-ui/core/IconButton'
import CloseIcon from '@material-ui/icons/Close'
import SettingsIcon from '@material-ui/icons/Settings'
import DisplayErrosMessage from '../../../../ComponentsLibrary/DisplayErrorsMessage/DisplayErrorsMessage'

  const ViewReadConfigParameter = (props) => {
        const {configParameter,DeletedRegister,loading,handleClose,infoErrors,snackInfo,
               handleCloseSnack,visibility} = props
         
        const styledReadConfigParameter=StyledReadConfigParameter()

        //Colunas da Tabela 
        const headCells = [
            { id: 'parameter',disablePadding: false, label: 'Nome', align:'left', width:'20%'},
            { id: 'description',disablePadding: false, label: 'Descrio', align:'left',width:'60%'},
            { id: 'parameterValue',disablePadding: false, label: 'Valor', align:'left', width:'20%'}     
        ]
     
      return(
        <Container maxWidth="lg">
            <Box className={styledReadConfigParameter.MainBox}>
              <EnhancedTableRows 
                  rows={configParameter}
                  headCells={headCells}
                  tableTitle={'Parmetros de Configurao'}
                  icon={<SettingsIcon fontSize='large' className={styledReadConfigParameter.color}/>}
                  DeletedRegister={DeletedRegister}
                  api_url='/deleteConfigParameter'
                  pathEditComponentForUpdate = '/UpdateConfigParameter'
                  pathAddComponentForUpdate = '/addConfigParameter'
                  routeName='ConfigParameter'
                  showAddRegisterCommand={true}  
                  showDeleteRegisterCommand={true}
                  showEditRegisterCommand={true}
                  titleReport='Cadastro de Parmetros de Cofigurao'
                  landscapeView={true}
                  loading={loading}>
                </EnhancedTableRows> 
            </Box>
            <Box className={styledReadConfigParameter.MainBox}>
              <DisplayErrosMessage infoErrors={infoErrors} handleCloseErros={handleClose}  />
            </Box>
            <Snackbar  anchorOrigin={{vertical: 'bottom',horizontal: 'center'}}
                      open={visibility}  autoHideDuration={3000} onClose={handleCloseSnack} message={snackInfo.message}
                      action={
                            <React.Fragment>
                                <IconButton size="small" aria-label="close" color="inherit" onClick={handleCloseSnack}>
                                <CloseIcon fontSize="small" />
                                </IconButton>
                            </React.Fragment>
                         }
                         />
            
        </Container>
      )}

export default ViewReadConfigParameter
