 import React,{useState} from 'react'
import {Link} from "react-router-dom"
import List from '@material-ui/core/List'
import ListItem from '@material-ui/core/ListItem'
import ScreenLockLandscapeIcon from '@material-ui/icons/ScreenLockLandscape'
import VpnKeyIcon from '@material-ui/icons/VpnKey'
import ListItemText from '@material-ui/core/ListItemText'
import styledLinks from './StyledLinks'
import { useSelector } from 'react-redux'
import verifyPermissions from '../../../Assets/js/verifyPermissions'
import Typography from '@material-ui/core/Typography'
import DesktopWindowsIcon from '@material-ui/icons/DesktopWindows'
import ExpandLess from '@material-ui/icons/ExpandLess'
import ExpandMore from '@material-ui/icons/ExpandMore'
import Collapse from '@material-ui/core/Collapse'

const KeyHolderLinks = (props) => {
    
    const classes = styledLinks()
    
    const permissions = useSelector(state=> state.permission)
    const userAuthenticated = useSelector(state=> state.authenticated)

    const [openKeyHolderLinks,setOpenKeyHolderLinks] = useState(false)
    const handleClick = () =>  setOpenKeyHolderLinks(!openKeyHolderLinks)
      

    let permission=false
   

    return(
    <div className={classes.drawerWidth} >
        <List component="nav">
            <ListItem button={true} onClick={handleClick} className={classes.ListItem}>
            <ListItemText><Typography color="primary">Claviculrio</Typography></ListItemText>
            {openKeyHolderLinks ? <ExpandLess /> : <ExpandMore />}
            </ListItem>
            <Collapse in={openKeyHolderLinks} timeout="auto" unmountOnExit>
                {permission = verifyPermissions('KeyHolder','read',userAuthenticated,permissions)}
                    <Link to={permission ? "/allKeyHolder" : "#"} onClick={event=> props.toggleDrawer(verifyPermissions('KeyHolder','read',userAuthenticated,permissions) ? false : true,event)} className={classes.linkDecoration}  >
                        <ListItem button className={classes.nested}>
                            <ScreenLockLandscapeIcon className={classes.color} /><ListItemText  className={permission ? classes.menuText : classes.colorDisable} primary='Cadastrar Claviculrio' />
                        </ListItem>
                    </Link>
                
                
                {permission = verifyPermissions('AvaliableKey','read',userAuthenticated,permissions)}
                    <Link to={permission ? "/updateAvaliableKey" : "#"} onClick={event=> props.toggleDrawer(verifyPermissions('AvaliableKey','read',userAuthenticated,permissions) ? false : true,event)} className={classes.linkDecoration}  >
                        <ListItem button className={classes.nested}>
                        <VpnKeyIcon className={classes.color} /><ListItemText  className={permission ? classes.menuText : classes.colorDisable} primary='Configurao de Chaves' />
                        </ListItem>
                    </Link>
                

                {permission = verifyPermissions('MonitorKey','read',userAuthenticated,permissions)}
                    <Link to={permission ? "/MonitorKey" : "#"} onClick={event=> props.toggleDrawer(verifyPermissions('MonitorKey','read',userAuthenticated,permissions) ? false : true,event)} className={classes.linkDecoration}  >
                        <ListItem button className={classes.nested}>
                        <DesktopWindowsIcon className={classes.color} /><ListItemText  className={permission ? classes.menuText : classes.colorDisable} primary='Monitoramento das Chaves' />
                        </ListItem>
                    </Link>
            </Collapse>
        </List>
    </div>
    )}

export default KeyHolderLinks
