import React from 'react'
import StyledReadUser from './StyledReadUser'
import {Box,Container} from '@material-ui/core'
import EnhancedTableRows from '../../../../ComponentsLibrary/EnhancedTable/EnhancedTableRows/EnhancedTableRows'
import Snackbar from '@material-ui/core/Snackbar'
import IconButton from '@material-ui/core/IconButton'
import CloseIcon from '@material-ui/icons/Close'
import DisplayErrosMessage from '../../../../ComponentsLibrary/DisplayErrorsMessage/DisplayErrorsMessage'
import PersonOutlinedIcon from '@material-ui/icons/PersonOutline'

  const ViewReadUser = (props) => {
        const {users,DeletedRegister,loading,handleClose,infoErrors,snackInfo,
               handleCloseSnack,visibility} = props
        const styledReadUser = StyledReadUser()

        //Colunas da Tabela 
        const headCells = [
            { id: 'name',disablePadding: false, label: 'Nome', align:'left', width:'30%'},
            { id: 'logonUser',disablePadding: false, label: 'Login', align:'left', width:'10%'},
            { id: 'email',disablePadding: false, label: 'E-mail', align:'left', width:'30%'},     
            { id: 'profileName',disablePadding: false, label: 'Perfil', align:'left', width:'20%'},     
            { id: 'rfid',type:'conditionalText', disablePadding: false, label: 'Carto Associado', align:'center', width:'10%'}     
        ]
     
      return(
        <Container maxWidth="lg">
            <Box className={styledReadUser.MainBox}>
              <EnhancedTableRows 
                  rows={users}
                  headCells={headCells}
                  tableTitle={'Usurios'}
                  icon={<PersonOutlinedIcon fontSize='large' className={styledReadUser.color}/>}
                  DeletedRegister={DeletedRegister}
                  api_url='/deleteUser'
                  pathEditComponentForUpdate = '/UpdateUser'
                  pathAddComponentForUpdate = '/addUser'
                  routeName='User'
                  showAddRegisterCommand={true}  
                  showDeleteRegisterCommand={true}
                  showEditRegisterCommand={true}
                  titleReport='Cadastro de Usurios'
                  landscapeView={true}
                  loading={loading}>
                </EnhancedTableRows> 
            </Box>
            <Box className={styledReadUser.MainBox}>
              <DisplayErrosMessage infoErrors={infoErrors} handleCloseErros={handleClose}  />
            </Box>
            <Snackbar  anchorOrigin={{vertical: 'bottom',horizontal: 'center'}}
                      open={visibility}  autoHideDuration={3000} onClose={handleCloseSnack} message={snackInfo.message}
                      action={
                            <React.Fragment>
                                <IconButton size="small" aria-label="close" color="inherit" onClick={handleCloseSnack}>
                                <CloseIcon fontSize="small" />
                                </IconButton>
                            </React.Fragment>
                         }
                         />
            
        </Container>
      )}

export default ViewReadUser
