import React from 'react'
import StyledEnhancedTableToolbar from './StyledEnhancedTableToolbar'
import PropTypes from 'prop-types'
import Typography from '@material-ui/core/Typography'
import {Box,FormControl,Input,InputAdornment} from '@material-ui/core'
import Search from '@material-ui/icons/Search'


const ViewEnhancedTableToolbar = (props) => {
    
    const {tableTitle,searchValue,handleOnChange,icon} = props
    const classes = StyledEnhancedTableToolbar()

    return(
      <Box className={classes.tableToolbar}  m={1}>
        <Box display='flex' m={1}>
          <Box>{icon}</Box>
          <Box mx={1}>
            <Typography className={classes.title} id="tableTitle" component="div" >
                 <Box fontWeight='medium'>{tableTitle} </Box>
            </Typography>
          </Box>
        </Box>

        <Box m={1} >
        <FormControl className={classes.margin} arial-label='form-search'>
                  <Input
                    id="search"
                    value={searchValue}
                    onChange={handleOnChange}
                    size='small'
                    startAdornment={
                      <InputAdornment className={classes.color} position="start">
                        <Search className={classes.color} />
                      </InputAdornment>
                    }
                  />
          </FormControl>
        </Box>
      </Box>
    )}

  ViewEnhancedTableToolbar.propTypes = {
    tableTitle:PropTypes.string.isRequired
}
export default ViewEnhancedTableToolbar
