import React from 'react'
import InputMask from 'react-input-mask'
import {Container,Paper,Box,Button} from '@material-ui/core'
import {FormControl,Input,InputLabel} from '@material-ui/core'
import HeaderForm from '../../ComponentsLibrary/Form/HeaderForm/HeaderForm'
import AssignmentIndIcon from '@material-ui/icons/AssignmentInd'
import MailIcon from '@material-ui/icons/Mail'
import LockOpenIcon from '@material-ui/icons/LockOpen'
import InputAdornment from '@material-ui/core/InputAdornment'
import DisplayErrosMessage from '../../ComponentsLibrary/DisplayErrorsMessage/DisplayErrorsMessage'
import DisplayInfoMessage from '../../ComponentsLibrary/DisplayInfoMessage/DisplayInfoMessage'
import LinearProgress from '@material-ui/core/LinearProgress'

const ViewRecoveryPass = (props) => {
    
    const {handleSubmit,user,
            handleChange,infoErrors,
            serverValidation,handleCloseInfo,
            handleCloseErros,
            loading} = props

    return(
        <Container maxWidth='xs'>
            <Box m={2}  >
                <Paper>
                    <HeaderForm icon={<LockOpenIcon fontSize='large' style={{color:'blue'}}/>} titulo={'Solicitar Nova Senha'}/>
                    <Box m={2}>
                        <Box m={2}>
                            <FormControl >
                                <InputLabel htmlFor="input-email">E-mail</InputLabel>
                                <Input  autoFocus   style={{width:'35ch'}}
                                id='email'
                                name='email'
                                label='email' 
                                autoComplete='off'
                                type='text'
                                onChange={(e)=> {handleChange(e)}}
                                value={user.email}
                                inputProps = {{maxLength:35}}
                                startAdornment={
                                    <InputAdornment position="start">
                                    <MailIcon color='primary' />
                                    </InputAdornment>
                                }/>
                            </FormControl> 
                        </Box>
                        <Box m={2}>
                            <FormControl>
                                <InputLabel htmlFor="input-cpf">CPF</InputLabel>
                                <InputMask  
                                    mask='999.999.999.99'
                                    maskChar=''
                                    value={user.cpf} 
                                    onChange={event=> handleChange(event,true)}>
                                    {inputProps =>
                                        <Input id='cpf' name='cpf' label='cpf' autoComplete='off'
                                        startAdornment={
                                            <InputAdornment position="start">
                                            <AssignmentIndIcon color='primary' />
                                            </InputAdornment>
                                        }/>}
                                </InputMask> 
                            </FormControl>
                        </Box>  
                    </Box>
                    <Box display='flex' justifyContent='center' mt={5}>
                        <Button type='submit' size='small' variant='contained' 
                                color='primary' onClick={(e)=> {handleSubmit(e)}}>
                                Confirmar
                        </Button>
                    </Box>
                    <Box pb={1}>
                        <DisplayErrosMessage infoErrors={infoErrors} handleCloseErros={handleCloseErros}  />
                        <DisplayInfoMessage serverValidation={serverValidation} handleCloseInfo={handleCloseInfo} />
                    </Box>
                    {loading && <LinearProgress /> }
                </Paper>
            </Box>
        </Container>
    )}
export default ViewRecoveryPass
