import React from 'react'
import {TextField} from '@material-ui/core'
import StyledUpdateConfigParameter from './StyledUpdateConfigParameter'
import SettingsIcon from '@material-ui/icons/Settings'
import BodyForm from '../../../../ComponentsLibrary/Form/BodyForm/BodyForm'


const ViewUpdateConfigParameter =  (props)  => {

    const {handleSubmit,configParameter,
            handleChange,handleBlur,
            inputvalidation,infoErrors,
            serverValidation,handleCloseInfo,
            handleCloseErros,loading} = props

    const styledUpdateConfigParameter = StyledUpdateConfigParameter()

return( <BodyForm infoErrors={infoErrors} 
        serverValidation={serverValidation} 
        handleCloseInfo={handleCloseInfo}
        handleCloseErros={handleCloseErros}
        handleSubmit={handleSubmit}
        loading={loading}
        icone={<SettingsIcon fontSize='large' className={styledUpdateConfigParameter.color} />}
        title='Cadastro de Parmetros'
        urlBackCommand='/allConfigParameter'
        showSaveButton={true} showBackButton={true}
        paperMainWidth='50%'>
            <TextField 
                    id='parameter' 
                    name='parameter'
                    label='Parmetro' 
                    autoComplete='off'
                    className={styledUpdateConfigParameter.mediumTextField}
                    value={configParameter.parameter} 
                    onChange={event=> handleChange(event,false)}
                    onBlur={event=> handleBlur(event)}
                    helperText={inputvalidation.keys.parameter !== '' ? inputvalidation.keys.parameter  : ''}
                    error={inputvalidation.keys.parameter !== '' ? true : false}
                    inputProps={{ maxLength: 45 }}
                />
                 <TextField 
                    id='parameterValue' 
                    name='parameterValue'
                    label='Valor' 
                    autoComplete='off'
                    className={styledUpdateConfigParameter.mediumTextField}
                    value={configParameter.parameterValue} 
                    onChange={event=> handleChange(event,true)}
                    onBlur={event=> handleBlur(event)}
                    helperText={inputvalidation.keys.parameterValue !== '' ? inputvalidation.keys.parameterValue  : ''}
                    error={inputvalidation.keys.parameterValue !== '' ? true : false}
                    inputProps={{ maxLength: 50 }}
                />
                <TextField 
                    id='description' 
                    name='description'
                    label='Descrio'
                    autoComplete='off'
                    multiline
                    rowsMax={10}
                    className={styledUpdateConfigParameter.largeTextField}
                    value={configParameter.description} 
                    onChange={event=> handleChange(event,false)}
                    onBlur={event=> handleBlur(event)}
                    helperText={inputvalidation.keys.description !== '' ? inputvalidation.keys.description  : ''}
                    error={inputvalidation.keys.description !== '' ? true : false}
                    inputProps={{ maxLength: 3000 }}
                />
</BodyForm>
      
    )

}
export default ViewUpdateConfigParameter
