//#region - imports
    import React from 'react'
    import {Box,InputLabel,Select,Paper} from '@material-ui/core'
    import Typography from '@material-ui/core/Typography'
    import StyledMonitorKey from './StyledMonitorKey'
    import DisplayErrosMessage from '../../../ComponentsLibrary/DisplayErrorsMessage/DisplayErrorsMessage'
    import DisplayInfoMessage from '../../../ComponentsLibrary/DisplayInfoMessage/DisplayInfoMessage'
    import KeyIcon from '@material-ui/icons/VpnKey'
    import FormControl from '@material-ui/core/FormControl'
//#endregion

const ViewMonitorKey =  (props)  => {

const {     keyHolderId,
            keyHolderList,
            handleChangeKeyHolder,
            infoErrors,serverValidation,handleCloseInfo,
            handleCloseErros,
            avaliableKeyList,
            } = props


    const styledMonitorKey = StyledMonitorKey()
    return( 
           <Box className={styledMonitorKey.MainBox}>
            <Paper elevation={3} className={styledMonitorKey.PaperMain}>
                <Box display='flex' flexDirection='column' justifyContent='center'>
                    <Box display='flex' flexDirection='row' justifyContent='center' m={1}>
                        <FormControl>
                            <InputLabel htmlFor="profile-native-simple">Claviculrio</InputLabel>
                            <Select native
                            value={keyHolderId}
                            onChange={e=>handleChangeKeyHolder(e)}
                            inputProps={{
                                name: 'keyHolderId',
                                id: 'keyHolder-select',
                            }}
                            >
                            <option value="-1">Opes...</option>
                            {
                                keyHolderList.map((e,i) =>  { 
                                    return (<option key={i} value={e.id}>{e.identification}</option>)
                                })
                            }   
                        </Select>
                        </FormControl>
                    </Box>
                    <Box display='flex' flexDirection='row' m={1} justifyContent='center' flexWrap='wrap'>
                         {avaliableKeyList.map((e,i)=> {
                            return(
                                <Box key={i}>
                                    <Paper elevation={0} className={e.exception === 1 ? [styledMonitorKey.paperKey,styledMonitorKey.exception] : styledMonitorKey.paperKey}>
                                        
                                        <Box display='flex' justifyContent='center' className={styledMonitorKey.userKey} >
                                            <Typography className={styledMonitorKey.textKey}>   
                                                {e.user}
                                            </Typography>
                                        </Box>
                                        
                                        <Box display='flex' justifyContent='center'><KeyIcon className={e.situation === 1 ? styledMonitorKey.Posicionada : styledMonitorKey.Retirada}/></Box>
                                              
                                        <Box display='flex' justifyContent='center'>
                                            <Typography className={styledMonitorKey.textKey}>
                                                {e.number}
                                            </Typography>
                                        </Box>
                                    
                                    </Paper>
                                </Box>)
                        })}
                    </Box>
                    <Box>
                        <DisplayErrosMessage infoErrors={infoErrors} handleCloseErros={handleCloseErros}  />
                        <DisplayInfoMessage serverValidation={serverValidation} handleCloseInfo={handleCloseInfo}/>
                    </Box>            
                </Box>
            </Paper>
           </Box>
        

    )
}
export default ViewMonitorKey
