import React, { useEffect } from 'react'
import ViewKeyTableRows from './ViewKeyTableRows'

function KeyTableRows(props) {

    const {rows,headCells,headCellsReport,landscapeView,titleReport,loading,
           profileList,handleChange,handleSave} = props
    
    const [order, setOrder] = React.useState('asc')
    const [orderBy, setOrderBy] = React.useState('id')
    const [page, setPage] = React.useState(0)
    const [rowsPerPage, setRowsPerPage] = React.useState(5)

    //#region - Controle de Busca
    const [filterRows, setFilterRows] = React.useState(rows)
    useEffect(()=> {
          setFilterRows(rows)
    },[rows])
    function filterIt(arr, searchKey) {
      return arr.filter((obj) => {
          return (Object.keys(obj).some((key) => { return (key !=='id' ? String(obj[key]).toLowerCase().includes(searchKey) : false) }))
      })
    }
    const handleSearching = (searchValue) => {
      if (searchValue !== '') { 
        setFilterRows((filterIt(filterRows,searchValue.toLowerCase())))
        setPage(0)
      }else {
        setFilterRows(rows)
      }
    }
    //#endregion

    const handleRequestSort = (event, property) => {
      const isAsc = orderBy === property && order === 'asc'
      setOrder(isAsc ? 'desc' : 'asc')
      setOrderBy(property)
    }
         
    const handleChangePage = (event, newPage) => {
      setPage(newPage)
    }
  
    const handleChangeRowsPerPage = (event) => {
      setRowsPerPage(parseInt(event.target.value, 10))
      setPage(0)
    }
      
    //#region - Controle de Comparao e Ordenao das Colunas 
        function descendingComparator(a, b, orderBy) {
            if (b[orderBy] < a[orderBy]) {
            return -1;
            }
            if (b[orderBy] > a[orderBy]) {
            return 1;
            }
            return 0;
        }
        function getComparator(order, orderBy) {
            return order === 'desc'
            ? (a, b) => descendingComparator(a, b, orderBy)
            : (a, b) => -descendingComparator(a, b, orderBy);
        }
        function stableSort(array, comparator) {
            const stabilizedThis = array.map((el, index) => [el, index]);
            stabilizedThis.sort((a, b) => {
            const order = comparator(a[0], b[0]);
            if (order !== 0) return order;
            return a[1] - b[1];
            });
            return stabilizedThis.map((el) => el[0]);
        }
    //#endregion


    return(<ViewKeyTableRows
            handleRequestSort={handleRequestSort}
            handleChangePage={handleChangePage}
            handleChangeRowsPerPage={handleChangeRowsPerPage}
            getComparator={getComparator}
            stableSort={stableSort}
            rows={filterRows}
            order={order}
            orderBy={orderBy}
            rowsPerPage={rowsPerPage}
            page={page}
            headCells={headCells}
            headCellsReport={headCellsReport}
            handleSearching={handleSearching}
            titleReport={titleReport}
            landscapeView={landscapeView}
            loading={loading}
            profileList={profileList}
            handleChange={handleChange}
            handleSave={handleSave}
    />)
}

export default KeyTableRows