import React from 'react'
import ViewKeyTableHead from './ViewKeyTableHead'
  
function KeyTableHead(props) {
   
    const {order, orderBy, rowCount, onRequestSort,
        headCells,headCellsReport,executeFiltering,
        landscapeView,allRows,titleReport} = props;
   
    return(<ViewKeyTableHead
        order={order}
        orderBy={orderBy}
        rowCount={rowCount}
        onRequestSort={onRequestSort}
        headCells={headCells}
        headCellsReport={headCellsReport}
        landscapeView={landscapeView}
        allRows={allRows}
        titleReport={titleReport}
        executeFiltering={executeFiltering}
  />)
}

export default KeyTableHead