import { makeStyles } from '@material-ui/core/styles'

const styledToolbar = makeStyles((theme) => ({

    root: {
        primary: '#782878' 
    }, 
    grow: {
      flexGrow: 1,
    },
    menuButton: {
      marginRight: theme.spacing(2),
    },
    title: {
      display: 'none',
      [theme.breakpoints.up('sm')]: {
        display: 'block',
      },
    },
    sectionDesktop: {
      display: 'none',
      [theme.breakpoints.up('md')]: {
        display: 'flex',
      },
    },
    sectionMobile: {
      display: 'flex',
      [theme.breakpoints.up('md')]: {
        display: 'none',
      },
    },
    drawerWidth: {
      width: '22rem'
    },
    linkDecoration: {
      textDecoration: 'none', 
      color:'inherit'
    },
    listItemStyled: {
        paddingTop:theme.spacing(0),
        paddingBottom:theme.spacing(0)
    },
    color: {
      color: theme.palette.primary.main
    }
  }))

  export default styledToolbar
