import React from 'react'
import {useState,useEffect} from 'react'
import ViewReadRoutePermission from './ViewReadRoutePermission'
import {getApi} from '../../../../Assets/js/apiProxy'
import {infoError} from '../../../../Assets/js/infoError'

function ReadRoutePermission(props) {
    
    const api = getApi('api_features')

    const [loading,setLoading] = useState(false)
    const [routeState,allRoute] = useState([])
    const [infoErrors,setInfoErrors] = useState({msg:''})
    
    let snackInfoState = {message:''}
    const [snackInfo,setSnackInfo] = useState(snackInfoState)
    const [visibility, setVisibility] = useState(false)
      
   
    useEffect(()=> {
        setLoading(true)
        api.get('/allRoute')
          .then(function (response) {
            allRoute(response.data.registers.filter((e)=> e.internal ===0))
            setLoading(false)
          })
          .catch(function (error) {
            let infoErrorsState = infoError(error)
            console.log(infoErrorsState.msg)
            setInfoErrors(infoErrorsState)
             setLoading(false)
            
          })
      },[api])

      const  handleCloseSnack = ()=> {
        setVisibility(false)
      }

      const DeletedRegister = (idDeleted,error) => {
        if (error === undefined) {
          routeState.splice(routeState.findIndex((e)=>  e.id === idDeleted),1)

          snackInfoState.message = 'Registro excludo com sucesso.' 
          setSnackInfo(snackInfoState)
          setVisibility(true)

        }else {
          snackInfoState.message = 'Erro ao tentar excluir registro.' 
          snackInfoState.type= 'error'
          setSnackInfo(snackInfoState)
          setVisibility(true)
        }
      }

      function handleClose() { 
        setInfoErrors({msg:''})
     }
      
    return (<ViewReadRoutePermission route={routeState} 
            DeletedRegister={DeletedRegister}
            loading={loading} 
            infoErrors={infoErrors} 
            snackInfo={snackInfo}
            handleCloseSnack={handleCloseSnack}
            visibility={visibility}
            handleClose={handleClose}/>)
}

export default ReadRoutePermission