import React from 'react'
import {useState,useEffect} from 'react'
import ViewUpdateProfile from './ViewUpdateProfile'
import {getApi} from '../../../../Assets/js/apiProxy'
import InputValidateProfile from './InputValidateProfile'
import {infoError} from '../../../../Assets/js/infoError'

function UpdateProfile(props) {
    
   const api = getApi('api_features')

   const { location: { state }} = props
   let prop_id = state === undefined ? 0 : state.id

   let profileState = {id:prop_id,name:'',
                    description:'',
                    profileNumber: '',
                    accessLevel: ''}

   const [profile,editableProfile] = useState(profileState)
   const [infoErrors,setInfoErrors] = useState({msg:''})
   const [serverValidation,setServerValidation] = useState({msg:''})
   const [loading, setLoading] = useState(false)

   let validationState = {isError:false,
                          keys:{id:prop_id,
                                name:'',
                                description:'',
                                profileNumber:'',
                                accessLevel: ''}}

   const [inputvalidation,setInputValidation] = useState(validationState)


   useEffect(()=> {
        if (profileState.id !== 0) {
         api.get(`/editProfile/${profileState.id}`)
         .then(function (response) {
         
         if (response.data.registers !== undefined) { 
            let profile_data = response.data.registers.shift()
            editableProfile(profile_data)
         }
         document.getElementById('name').focus()
         })
         .catch(function (error) {  
            let infoErrorsState = infoError(error)
            console.log(infoErrorsState.msg)
            setInfoErrors(infoErrorsState)
         })

         return ()=> { setLoading(false)}

     } 
   },[profileState.id,api])

   function handleCloseErros() { 
      setInfoErrors({msg:''})
   }

   function handleCloseInfo() { 
      setServerValidation({msg:''})
   }

   function handleChange(event,hasMask) {
      if (hasMask) {
            event.target.value = event.target.value.replace(/[, ]+/g,'').replace(/\(|\)/g, "") 
      }
      editableProfile({...profile,[event.target.name]:event.target.value})
   }

   function handleBlur(event) {
      let ret = InputValidateProfile(event)
      
      //Spread Operator - Converte o objeto inputValidation em Array, e atualiza o item do array 
      //identificado pelo valor originado por event.target.nameo com o novo valor ret.msg.
      const keys = {keys:{...inputvalidation.keys,[event.target.name]:ret.msg}}
      
      //Rest Operator - Pega o Objeto key e expande em propriedades passando como argumentos para o 
      //setInputValidation.
      setInputValidation({isError:ret.isError,...keys})
      
   }
      
  function handleSubmit(event) {
      setLoading(true)
      
      if (!inputvalidation.isError) {
         if (profileState.id !== 0) {
            
            api.put('/updateProfile',profile)
            .then(function (response) {
               if (response.data.bsApproved) {
                  props.history.push('/allProfile')
               } else {
                  let serverValidationState = {msg:response.data.msg}
                  setServerValidation(serverValidationState)
               }
            })
            .catch(function (error) {  
               let infoErrorsState = infoError(error)
               console.log(infoErrorsState.msg)
               setInfoErrors(infoErrorsState)
            })
            .finally(()=> {
               setLoading(false)
            })
         }else {
           api.post('/addProfile',profile)
               .then(function (response) {
                  if (response.data.bsApproved) {
                     props.history.push('/allProfile')
                  } else {
                     let serverValidationState = {msg:response.data.msg}
                     setServerValidation(serverValidationState)
                  }
            })
            .catch(function (error) {  
               let infoErrorsState = infoError(error)
               console.log(infoErrorsState.msg)
               setInfoErrors(infoErrorsState)
            })
            .finally(()=> {
               setLoading(false)
            })
         }
      } else {
         setLoading(false)
      }
      event.preventDefault()
  }

   return (<ViewUpdateProfile profile={profile}
          handleChange={handleChange}
          handleSubmit={handleSubmit}
          handleBlur={handleBlur}
          handleCloseErros={handleCloseErros}
          handleCloseInfo={handleCloseInfo}
          inputvalidation={inputvalidation}
          infoErrors={infoErrors}
          serverValidation={serverValidation}
          loading = {loading}
           />)
   
}

export default UpdateProfile