import React,{useState} from 'react'
import {Link} from "react-router-dom"
import List from '@material-ui/core/List'
import ListItem from '@material-ui/core/ListItem'
import ListItemText from '@material-ui/core/ListItemText'
import styledLinks from './StyledLinks'
import Typography from '@material-ui/core/Typography'
import { useSelector } from 'react-redux'
import verifyPermissions from '../../../Assets/js/verifyPermissions'
import LibraryBooksIcon from '@material-ui/icons/LibraryBooks'
import ExpandLess from '@material-ui/icons/ExpandLess'
import ExpandMore from '@material-ui/icons/ExpandMore'
import Collapse from '@material-ui/core/Collapse'

const ReportLinks = (props) => {
    
    const classes = styledLinks()

    const permissions = useSelector(state=> state.permission)
    const userAuthenticated = useSelector(state=> state.authenticated)
    
    const [openReportLinks,setOpenReportLinks] = useState(false)
    const handleClick = () => setOpenReportLinks(!openReportLinks)
    
    let permission=false
    

    return(
    <div className={classes.drawerWidth} >
        <List component="nav">
            <ListItem button={true} onClick={handleClick} className={classes.ListItem}>
            <ListItemText><Typography color="primary">Relatrios</Typography></ListItemText>
            {openReportLinks ? <ExpandLess /> : <ExpandMore />}
            </ListItem>
            <Collapse in={openReportLinks} timeout="auto" unmountOnExit>
                {permission = verifyPermissions('KeyMonitorReport','read',userAuthenticated,permissions)}
                <Link to={permission ? "/KeyMonitorReport" : "#"} onClick={event=> props.toggleDrawer(verifyPermissions('KeyMonitorReport','read',userAuthenticated,permissions) ? false :true,event)} className={classes.linkDecoration}  >
                        <ListItem button className={classes.nested}>
                        <LibraryBooksIcon className={classes.color} /><ListItemText className={permission ? classes.menuText : classes.colorDisable} primary='Movimentao de Chaves' />
                    </ListItem>
                </Link>
                {permission = verifyPermissions('OverfillMonitorReport','read',userAuthenticated,permissions)}
                <Link to={permission ? "/OverFillMonitorReport" : "#"} onClick={event=> props.toggleDrawer(verifyPermissions('OverfillMonitorReport','read',userAuthenticated,permissions) ? false :true,event)} className={classes.linkDecoration}  >
                        <ListItem button className={classes.nested}>
                        <LibraryBooksIcon className={classes.color} /><ListItemText className={permission ? classes.menuText : classes.colorDisable} primary='Monitoramento de Overfill' />
                    </ListItem>
                </Link>
                {permission = verifyPermissions('MultiloadControlReport','read',userAuthenticated,permissions)}
                <Link to={permission ? "/MultiloadControlReport" : "#"} onClick={event=> props.toggleDrawer(verifyPermissions('MultiloadControlReport','read',userAuthenticated,permissions) ? false :true,event)} className={classes.linkDecoration}  >
                        <ListItem button className={classes.nested}>
                        <LibraryBooksIcon className={classes.color} /><ListItemText className={permission ? classes.menuText : classes.colorDisable} primary='Acesso ao Multiload' />
                    </ListItem>
                </Link>
            </Collapse>
        </List>
    </div>
    )}

export default ReportLinks
