import React from 'react'
import StyledReadTruck from './StyledReadTruck'
import {Box,Container} from '@material-ui/core'
import EnhancedTableRows from '../../../../ComponentsLibrary/EnhancedTable/EnhancedTableRows/EnhancedTableRows'
import Snackbar from '@material-ui/core/Snackbar'
import IconButton from '@material-ui/core/IconButton'
import CloseIcon from '@material-ui/icons/Close'
import AtIcon from '@material-ui/icons/LocalShipping'
import DisplayErrosMessage from '../../../../ComponentsLibrary/DisplayErrorsMessage/DisplayErrorsMessage'

  const ViewReadTruck = (props) => {
        const {truck,DeletedRegister,loading,handleClose,infoErrors,snackInfo,
               handleCloseSnack,visibility} = props
         
        const styledReadTruck=StyledReadTruck()

        //Colunas da Tabela 
        const headCells = [
            { id: 'at',type:'truckLicense', disablePadding: false, label: 'Placa', align:'center', width:'10%'},
            { id: 'totalCompart',disablePadding: false, label: 'Total de Cps.', align:'center',width:'10%'},
            { id: 'capacity',type:'numberFormat', disablePadding: false, label: 'Capacidade', align:'center',width:'10%'},
            { id: 'b_train',type:'bool', disablePadding: false, label: 'B-Trem', align:'center',width:'10%'},
            { id: 'compartFirstTrain',disablePadding: false, label: 'Cpts. Pri. Carreta', align:'center',width:'30%'},
            { id: 'compartSecondTrain',disablePadding: false, label: 'Cpts. Seg. Carreta', align:'center',width:'30%'},
        ]
     
      return(
        <Container maxWidth="lg">
            <Box className={styledReadTruck.MainBox}>
              <EnhancedTableRows 
                  rows={truck}
                  headCells={headCells}
                  tableTitle={'Cadastro de AT'}
                  icon={<AtIcon fontSize='large' className={styledReadTruck.color}/>}
                  DeletedRegister={DeletedRegister}
                  api_url='/deleteTruck'
                  host_api='api_overfill'
                  pathEditComponentForUpdate = '/UpdateTruck'
                  pathAddComponentForUpdate = '/addTruck'
                  routeName='Truck'
                  showAddRegisterCommand={true}  
                  showDeleteRegisterCommand={true}
                  showEditRegisterCommand={true}
                  titleReport='Cadastro de AT'
                  landscapeView={true}
                  loading={loading}>
                </EnhancedTableRows> 
            </Box>
            <Box className={styledReadTruck.MainBox}>
              <DisplayErrosMessage infoErrors={infoErrors} handleCloseErros={handleClose}  />
            </Box>
            <Snackbar  anchorOrigin={{vertical: 'bottom',horizontal: 'center'}}
                      open={visibility}  autoHideDuration={3000} onClose={handleCloseSnack} message={snackInfo.message}
                      action={
                            <React.Fragment>
                                <IconButton size="small" aria-label="close" color="inherit" onClick={handleCloseSnack}>
                                <CloseIcon fontSize="small" />
                                </IconButton>
                            </React.Fragment>
                         }
                         />
            
        </Container>
      )}

export default ViewReadTruck
