import { makeStyles } from '@material-ui/core/styles'
import {grey} from '@material-ui/core/colors'

const StyledOverfillMonitorReport = makeStyles((theme) => ({
    mainPaper: {
      width:'50%',
    },
    largeTextField: {
        width:'90%'
      },
      mediumTextField: {
        width:'60%'
      },
      smallTextField: {
        width:'25%'
      },
      color: {
        color: theme.palette.primary.main
      },
      filter: {
        color: grey[800],
        fontSize: '1rem',
        fontWeight: 400,
        '&:hover': {
          '& #fade': { 
            display: 'flex',
          }
        }  
      },
      paperMain: {
        marginTop:'1vh',
        marginLeft:'0.5vw',
        marginRight:'0.5vw',
        backgroundColor: grey[300]
      },
      switchText: { 
        color: grey[600],
      },
}))

export default StyledOverfillMonitorReport