import React from 'react'
import {useState,useEffect} from 'react'
import ViewReadMultiloadControl from './ViewReadMultiloadControl'
import {getApi} from '../../../../Assets/js/apiProxy'
import {infoError} from '../../../../Assets/js/infoError'
import { useSelector } from 'react-redux'


function ReadMultiloadControl(props) {


    const [loading,setLoading] = useState(false)
    const [loadingDialog,setLoadingDialog] = useState(false)
    const [MultiloadControl,allMultiloadControl] = useState([])
    const [infoErrors,setInfoErrors] = useState({msg:''})
    const [open, setOpen] = useState(false)
    const [recharge, setRecharge] = useState(false)
    const [targetField,setTarget] = useState('')
    const [targetValue,setTargetValue] = useState(null)
    const [bayNumber,setBayNumber] = useState(null)
    const [operationalReason, setOperationalReason] = useState('')
    
    const [visibility, setVisibility] = useState(false)

    const userAuthenticated = useSelector(state => state.authenticated)
    
    const api_multiload = getApi('api_multiloadControl')

    useEffect(()=> { 

      api_multiload.get('/allMultiload')
        .then(function (response) {
              allMultiloadControl(response.data.registers)
              setLoading(false)
        })
        .catch(function (error) {
          let infoErrorsState = infoError(error)
          console.log(infoErrorsState.msg)
          setInfoErrors(infoErrorsState)
          setVisibility(true)
          setLoading(false)
          
        })
   
    },[api_multiload,open,recharge])

         
     function handleClose() { 
        setInfoErrors({msg:''})
        setOperationalReason('')
        setLoadingDialog(false)
        setOpen(false)

     }

     function handleChange(e) { 
      setOperationalReason(e.target.value)
     }

     const  handleCloseSnack = ()=> {
      setVisibility(false)
   }

   function handleToggleStatus(target,value,bayNumber) { 

        setTarget(target) 
        setTargetValue(value)
        setBayNumber(bayNumber)
        if (value === 1) { 
            setOpen(true)  
        } else {
            api_multiload.put(`/setStatusMultiload/${target}/${value}`,{bayNumber:bayNumber,user:userAuthenticated.logonUser,reason:operationalReason})
            .then(function (response) {
               if (!response.data.bsApproved) {
                  setInfoErrors(response.data.msg)
               }
            })
            .catch(function (error) {  
               let infoErrorsState = infoError(error)
               console.log(infoErrorsState.msg)
               setInfoErrors(infoErrorsState)
               setVisibility(true)
            })
            .finally(()=> {
               setRecharge(!recharge)
            })
         }
    }

    function handleSaveReason() { 
      
      if (operationalReason.length === 0) { 
         setInfoErrors({msg:' muito importante que voc informe a razo desta operao.'})
         setVisibility(true)
      }
      else {
      
         setLoadingDialog(true)
         api_multiload.put(`/setStatusMultiload/${targetField}/${targetValue}`,{bayNumber:bayNumber,user:userAuthenticated.logonUser,reason:operationalReason})
            .then(function (response) {
               if (!response.data.bsApproved) {
                  setInfoErrors(response.data.msg)
               }
            })
            .catch(function (error) {  
               let infoErrorsState = infoError(error)
               console.log(infoErrorsState.msg)
               setInfoErrors(infoErrorsState)
               setVisibility(true)
            })
            .finally(()=> {
               setLoadingDialog(false)
               setOpen(false)
               setOperationalReason('')
            })
      }
    }

    return (<ViewReadMultiloadControl MultiloadControl={MultiloadControl} 
             loading={loading} 
             infoErrors={infoErrors} 
             handleToggleStatus={handleToggleStatus}
             open={open}
             handleSaveReason={handleSaveReason}
             handleChange={handleChange}
             operationalReason={operationalReason}
             visibility={visibility}
             handleCloseSnack={handleCloseSnack}
             loadingDialog={loadingDialog}
             handleClose={handleClose}/>)
}

export default ReadMultiloadControl