import React from 'react'
import StyledReportTableContentRows from './StyledReportTableContentRows' 
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableContainer from '@material-ui/core/TableContainer'
import TableRow from '@material-ui/core/TableRow'
import ReportTableContentHead from '../ReportTableContentHead/ReportTableContentHead'
import '../../../Assets/css/viewReportTableContentRows.css'
import formater from '../../../Assets/js/formater'

const ViewReportTableContentRows = (props) => {
    
    const {handleRequestSort, 
           getComparator,stableSort,rows,
          order,orderBy,headCells,titleReport,landscapeView,id} = props
    
    const classes = StyledReportTableContentRows()
   
      function typeConverter(value,type) {
        let ret=''
        switch (type) {
          case 'bool':
            ret = value === 1 ? 'Sim' : 'No'
            break
          case 'conditionalText':
            ret = value && value.trim().length > 0 ? 'Sim' : 'No'
            break
          case 'numberFormat':
            ret = value ? formater(parseInt(value),'numberFormat') : value
            break
          case 'truckLicense':
            ret = value && value.trim().length > 0 ? value  : value
            break
          case 'keyStatus':
            ret = value && parseInt(value) === 0 ? 'Retirada' : 'Disponvel'
            break
          case 'blockedUnblocked':
            ret = value && parseInt(value) === 0 ? 'Bloqueado' : 'Liberado'
            break
          default:
            ret= value 
            break
        }
        return ret
    }
    
  return(
    <>

         { landscapeView ? <style type="text/css" media="print">{`@page {size:landscape;}`}</style> : null }
        <TableContainer className={classes.container}>
        <Table stickyHeader   
          id={id}
          className={classes.table}
          aria-labelledby={titleReport}
          size={'small'} //'medium'
          aria-label="ReportTableContentRows"
          style={{whiteSpace:'nowrap'}}>
          <ReportTableContentHead
            classes={classes}
            order={order}
            titleReport={titleReport}
            orderBy={orderBy}
            onRequestSort={handleRequestSort}
            headCells={headCells}/>
          <TableBody>
              {stableSort(rows, getComparator(order, orderBy))
                .map((row) => {
                  return (
                      <TableRow hover={false} id={'row_' + row.id} tabIndex={-1}  key={row.id} >
                          {Object.keys(row).map((key)  => {
                              const properties = headCells.filter(x=> x.id === key)
                              return(
                                  key !== 'id' && headCells.findIndex(e=> e.id === key) >= 0 ? (
                                    <TableCell className={classes.rowColor}  key={Math.random()}  style={{ overflowWrap:'break-word',whiteSpace:'normal'}}  align={properties.length > 0 ? properties[0].align : 'left'} >
                                        {typeConverter(row[key], properties.length > 0 ? properties[0].type : 'text')}
                                    </TableCell>
                                  ): null 
                              )
                          })} 
                    </TableRow>
                  )
                })}
            </TableBody>
        </Table>
      </TableContainer>
    </>      
    )

 
}
export default ViewReportTableContentRows
