function InputValidateDriver(event) {
    
    let ret={msg:"",isError:false}

    switch (event.target.name) {
        case 'code':
            if(event.target.value.length === 0) {
                ret.msg = 'Cdigo  obrigatrio.'
                ret.isError=true
            }
            break
        case 'phoneNumber':
            if(event.target.value.length === 0) {
                ret.msg = 'Nmero do telefone  obrigatrio.'
                ret.isError=true
              }
            break
        default:
            ret.msg=""
            ret.isError=false
        }
        return ret
    }

    export default InputValidateDriver