import { Container,Box,Paper,Tooltip,Button} from '@material-ui/core'
import React from 'react'
import Alert from '@material-ui/lab/Alert'
import {Link} from 'react-router-dom'

function NotFound(props) {
    
    return( 
        <Container maxWidth='xs'>
        <Paper>
            <Box display='flex' justifyContent='center' mt={6} p={2}  >
                <Alert variant="filled" severity="info">
                        O Recurso solicitado no foi encontrado.
                </Alert>
            </Box>
            <Box display='flex' justifyContent='center'>
                <Tooltip title="Entrar no sistema" placement='bottom'>
                    <Link to='/login' style={{textDecoration:'none',paddingBottom:'2vh'}}>
                            <Button variant="outlined" color="primary" aria-label="enter">
                                Login
                            </Button>
                    </Link>
                </Tooltip>
            </Box>
        </Paper>
        </Container>
    )
}

export default NotFound