import { makeStyles } from '@material-ui/core/styles'
import {grey} from '@material-ui/core/colors'


const StyledUpdateTruck = makeStyles((theme) => ({
  largeTextField: {
    width:'60%'
  },
  mediumTextField: {
    width:'30%'
  },
  smallTextField: {
    width:'20%'
  },
  color: {
    color: theme.palette.primary.main
  },
  switchText: { 
    color: grey[600],
  },
}))


export default StyledUpdateTruck




