import React from 'react'
import StyledKeyTableRows from './StyledKeyTableRows' 
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableContainer from '@material-ui/core/TableContainer'
import TablePagination from '@material-ui/core/TablePagination'
import TableRow from '@material-ui/core/TableRow'
import {Box} from '@material-ui/core'
import KeyTableHead from '../keyTableHead/keyTableHead'
import CircularProgress from '@material-ui/core/CircularProgress'
import NativeSelect from '@material-ui/core/NativeSelect'
import SaveIcon from '@material-ui/icons/Save'
import IconButton from '@material-ui/core/IconButton'
import TextField from '@material-ui/core/TextField'

const ViewKeyTableRows = (props) => {
    
    const {handleRequestSort,handleChangePage,handleChangeRowsPerPage,
           getComparator,stableSort,rows,order,orderBy,rowsPerPage,
           page,headCells,headCellsReport,titleReport, landscapeView,loading,
           handleSave,handleChange,profileList} = props
    
    const classes = StyledKeyTableRows()
    
  return(
    <>
      <TableContainer>
        <Table
          className={classes.table}
          aria-labelledby="Tabela apresentao das chaves"
          size={'small'} //'medium'
          aria-label="KeyTableRows"
          style={{whiteSpace:'nowrap'}}>

          <KeyTableHead
            classes={classes}
            order={order}
            orderBy={orderBy}
            onRequestSort={handleRequestSort}
            headCells={headCells}
            headCellsReport={headCellsReport}
            titleReport={titleReport}
            allRows={rows}
            landscapeView={landscapeView}/>
          {!loading && (
          <TableBody>
              {stableSort(rows, getComparator(order, orderBy))
                .slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
                .map((row) => {
                  return (
                    <TableRow hover tabIndex={-1}    key={row.id} >
                        <TableCell  className={classes.tableCell}>
                            {row.number}
                        </TableCell>
                        <TableCell  className={classes.tableCell}>
                              <NativeSelect className={classes.profileList}
                                id={`select_${row.id}`}
                                name='profile_id'
                                value={row.profile_id}
                                onChange={(e)=> handleChange(e,row.id)} >
                                <option aria-label="select-options-profile-avaliableKeys" value="" />
                                {profileList.map((profile)=> {
                                    return (<option  key={`${row.id}_${profile.id}`} value={profile.id}>{profile.Number}{profile.name}-{profile.description}</option>)         
                                })}
                              </NativeSelect>
                        </TableCell>
                        <TableCell className={classes.tableCell}>
                            <TextField name='description' className={classes.description} value={row.description} color="primary" onChange={(e)=> handleChange(e,row.id)} />
                        </TableCell>
                        <TableCell className={classes.tableCell}>
                            <TextField name='sealNumber' className={classes.sealNumber} value={row.sealNumber} color="primary" onChange={(e)=> handleChange(e,row.id)} />
                        </TableCell>
                        <TableCell className={classes.tableCell}>
                          <IconButton color="primary" aria-label="save-avaliable-key" onClick={()=> handleSave(row.id)} size='small' >
                                  <SaveIcon size="inherit" className={classes.saveBtn} />
                          </IconButton>
                        </TableCell>
                    </TableRow>    
                  )}) 
              }
            </TableBody>
          )} 
        </Table>
        {loading && (
              <Box display="flex" justifyContent="center" m={1} p={1} >
              <Box p={1}>
                <CircularProgress/>
              </Box>
            </Box>
        )}     
      </TableContainer>
      <TablePagination
        rowsPerPageOptions={[5, 10]}
        labelRowsPerPage = 'Linhas por pgina:'
        labelDisplayedRows={({ from, to, count }) => `${from}-${to} de ${count} `}
        component="div"
        count={rows.length}
        rowsPerPage={rowsPerPage}
        page={page}
        onChangePage={handleChangePage}
        onChangeRowsPerPage={handleChangeRowsPerPage}
      />
    </>      
    )
}
export default ViewKeyTableRows
