import React from 'react'
import ReportViewer from '../../../ComponentsLibrary/ReportViewer/ReportViewer'
import PropTypes from 'prop-types'

const ViewGeneralReport = (props) => {

    const {titleReport,headCells,landscapeView,allRows} = props 

    return(
    <div>
       <ReportViewer 
            titleReport={titleReport} 
            headCells={headCells}
            landscapeView={landscapeView}
            allRows={allRows}
        />
    </div>
    )}

    ViewGeneralReport.propTypes = {
        headCells: PropTypes.array.isRequired,
        titleReport: PropTypes.string.isRequired,
        landscapeView: PropTypes.bool.isRequired,
        allRows: PropTypes.array.isRequired
    }


export default ViewGeneralReport
