import React from 'react'
import StyledReadProfile from './StyledReadProfile'
import {Box,Container} from '@material-ui/core'
import EnhancedTableRows from '../../../../ComponentsLibrary/EnhancedTable/EnhancedTableRows/EnhancedTableRows'
import Snackbar from '@material-ui/core/Snackbar'
import IconButton from '@material-ui/core/IconButton'
import CloseIcon from '@material-ui/icons/Close'
import ListIcon from '@material-ui/icons/List'
import DisplayErrosMessage from '../../../../ComponentsLibrary/DisplayErrorsMessage/DisplayErrorsMessage'

  const ViewReadProfile = (props) => {
        const {profile,DeletedRegister,loading,handleClose,infoErrors,snackInfo,
               handleCloseSnack,visibility} = props
         
        const styledReadProfile=StyledReadProfile()

        //Colunas da Tabela 
        const headCells = [
            { id: 'name',disablePadding: false, label: 'Nome', align:'left', width:'20%'},
            { id: 'description',disablePadding: false, label: 'Descrio', align:'left',width:'40%'},
            { id: 'profileNumber',disablePadding: false, label: 'Nmero do Perfil', align:'left', width:'20%'},     
            { id: 'accessLevel',disablePadding: false, label: 'Hierarquia de Acesso', align:'left', width:'20%'}     
        ]
     
      return(
        <Container maxWidth="lg">
            <Box className={styledReadProfile.MainBox}>
              <EnhancedTableRows 
                  rows={profile}
                  headCells={headCells}
                  tableTitle={'Perfil de Usurio'}
                  DeletedRegister={DeletedRegister}
                  api_url='/deleteProfile'
                  icon={<ListIcon fontSize='large' className={styledReadProfile.color}/>}
                  pathEditComponentForUpdate = '/UpdateProfile'
                  pathAddComponentForUpdate = '/addProfile'
                  routeName='Profile'
                  showAddRegisterCommand={true}  
                  showDeleteRegisterCommand={true}
                  showEditRegisterCommand={true}
                  titleReport='Cadastro de perfis'
                  landscapeView={true}
                  loading={loading}>
                </EnhancedTableRows> 
            </Box>
            <Box className={styledReadProfile.MainBox}>
              <DisplayErrosMessage infoErrors={infoErrors} handleCloseErros={handleClose}  />
            </Box>
            <Snackbar  anchorOrigin={{vertical: 'bottom',horizontal: 'center'}}
                      open={visibility}  autoHideDuration={3000} onClose={handleCloseSnack} message={snackInfo.message}
                      action={
                            <React.Fragment>
                                <IconButton size="small" aria-label="close" color="inherit" onClick={handleCloseSnack}>
                                <CloseIcon fontSize="small" />
                                </IconButton>
                            </React.Fragment>
                         }
                         />
            
        </Container>
      )}

export default ViewReadProfile
