import React from 'react'
import ViewUpdateCommand from './ViewUpdateCommand'
import { useSelector } from 'react-redux'
import verifyPermissions from '../../Assets/js/verifyPermissions'

function UpdateCommand(props) {

   const {pathEditComponentForUpdate,id,titulo,isIcon,routeName} = props
   const permissions = useSelector(state=> state.permission)
   const userAuthenticated = useSelector(state=> state.authenticated)

   const permission = verifyPermissions(routeName,'write',userAuthenticated,permissions)
     
      
      return (<ViewUpdateCommand 
            id={id} 
            permission={permission}
            titulo={titulo}
            pathEditComponentForUpdate={pathEditComponentForUpdate}
            isIcon={isIcon} />)
   
}

export default UpdateCommand 