import React from 'react'
import {Container,Paper,Box,Button} from '@material-ui/core'
import {FormControl,TextField} from '@material-ui/core'
import HeaderForm from '../../ComponentsLibrary/Form/HeaderForm/HeaderForm'
import VpnLockIcon from '@material-ui/icons/VpnLock'
import DisplayErrosMessage from '../../ComponentsLibrary/DisplayErrorsMessage/DisplayErrorsMessage'
import DisplayInfoMessage from '../../ComponentsLibrary/DisplayInfoMessage/DisplayInfoMessage'
import StyledChangePasswordUserByAdmin from '../ChangePasswordUserByAdmin/ChangePasswordUserByAdmin'
import FormHelperText from '@material-ui/core/FormHelperText'

const ViewChangePasswordUserByAdmin = (props) => {
    
    const {handleSubmit,user,
            handleChange,infoErrors,
            serverValidation,handleCloseInfo,
            handleCloseErros} = props

    const styledChangePasswordUserByAdmin = StyledChangePasswordUserByAdmin()
    
    return(
        <Container maxWidth='xs'>
            <Box m={2}  >
                <Paper>
                    <HeaderForm icon={<VpnLockIcon fontSize='large' style={{color:'blue'}}/>} titulo={'Definir nova senha'}/>
                    <Box m={2}>
                        <Box m={2}>
                            <FormControl >
                            <TextField  autoFocus
                                id='password' 
                                name='password'
                                label='Senha'
                                autoComplete='off'
                                className={styledChangePasswordUserByAdmin.small}
                                value={user.senha} 
                                type='password'
                                onChange={event=> handleChange(event,false)}
                                inputProps={{ maxLength: 8 }}/>
                                 <FormHelperText id="component-helper-text">Mnimo 6 e mximo 9 dgitos, entre letras maisculas, minsculas e nmeros.</FormHelperText>
                            </FormControl> 
                        </Box>
                        <Box m={2}>
                            <FormControl>
                            <TextField 
                                id='confirmPassword' 
                                name='confirmPassword'
                                label='Confirmar Senha'
                                autoComplete='off'
                                className={styledChangePasswordUserByAdmin.small}
                                value={user.confirmPassword} 
                                type='password'
                                onChange={event=> handleChange(event,false)}
                                inputProps={{ maxLength: 8 }}/>
                                 <FormHelperText id="component-helper-text">Mnimo 6 e mximo 9 dgitos, entre letras maisculas, minsculas e nmeros.</FormHelperText>
                            </FormControl>
                        </Box>  
                    </Box>
                    <Box display='flex' justifyContent='center' mt={5}>
                        <Button type='submit' size='small' variant='contained' 
                                color='primary' onClick={(e)=> {handleSubmit(e)}}>
                                Salvar
                        </Button>
                    </Box>
                    <Box pb={1}>
                        <DisplayErrosMessage infoErrors={infoErrors} handleCloseErros={handleCloseErros}  />
                        <DisplayInfoMessage serverValidation={serverValidation} handleCloseInfo={handleCloseInfo} />
                    </Box>
                </Paper>
            </Box>
        </Container>
    )}
export default ViewChangePasswordUserByAdmin
