import React from 'react'
import StyledReadDriver from './StyledReadDriver'
import {Box,Container} from '@material-ui/core'
import EnhancedTableRows from '../../../../ComponentsLibrary/EnhancedTable/EnhancedTableRows/EnhancedTableRows'
import Snackbar from '@material-ui/core/Snackbar'
import IconButton from '@material-ui/core/IconButton'
import CloseIcon from '@material-ui/icons/Close'
import PersonIcon from '@material-ui/icons/Person'
import DisplayErrosMessage from '../../../../ComponentsLibrary/DisplayErrorsMessage/DisplayErrorsMessage'

  const ViewReadDriver = (props) => {
        const {driver,DeletedRegister,loading,handleClose,infoErrors,snackInfo,
               handleCloseSnack,visibility} = props
         
        const styledReadDriver=StyledReadDriver()

        //Colunas da Tabela 
        const headCells = [
            { id: 'code',disablePadding: false, label: 'Cdigo Motorista', align:'left', width:'50%'},
            { id: 'phoneNumber',disablePadding: false, label: 'Telefone', align:'left',width:'50%'},
        ]
     
      return(
        <Container maxWidth="lg">
            <Box className={styledReadDriver.MainBox}>
              <EnhancedTableRows 
                  rows={driver}
                  headCells={headCells}
                  tableTitle={'Cadastro de Motorista'}
                  icon={<PersonIcon fontSize='large' className={styledReadDriver.color}/>}
                  DeletedRegister={DeletedRegister}
                  api_url='/deleteDriver'
                  host_api='api_driverSms'
                  pathEditComponentForUpdate = '/UpdateDriver'
                  pathAddComponentForUpdate = '/addDriver'
                  routeName='Driver'
                  showAddRegisterCommand={true}  
                  showDeleteRegisterCommand={true}
                  showEditRegisterCommand={true}
                  titleReport='Cadastro de Motoristas'
                  landscapeView={true}
                  loading={loading}>
                </EnhancedTableRows> 
            </Box>
            <Box className={styledReadDriver.MainBox}>
              <DisplayErrosMessage infoErrors={infoErrors} handleCloseErros={handleClose}  />
            </Box>
            <Snackbar  anchorOrigin={{vertical: 'bottom',horizontal: 'center'}}
                      open={visibility}  autoHideDuration={3000} onClose={handleCloseSnack} message={snackInfo.message}
                      action={
                            <React.Fragment>
                                <IconButton size="small" aria-label="close" color="inherit" onClick={handleCloseSnack}>
                                <CloseIcon fontSize="small" />
                                </IconButton>
                            </React.Fragment>
                         }
                         />
            
        </Container>
      )}

export default ViewReadDriver
