 import React from 'react'
 import AdministrativeLinks from '../../Features/Administrative/Links/AdminstrativeLinks'
 import ReporLinks from '../../Features/Report/Links/ReportsLinks'
 import KeyHolderLinks from '../../Features/Operational/Links/KeyHolderLinks'
 import PagerAndSMSLinks from '../../Features/Operational/Links/PagerAndSMSLinks'
 import OverFillLinks from '../../Features/Operational/Links/OverfillLinks'
 import MultiloadControlLinks from '../../Features/Operational/Links/MultiloadControlLinks'
 import { Drawer} from '@material-ui/core'
 import Toolbar from '../Toolbar/Toolbar'
 import ListItem from '@material-ui/core/ListItem'
 import StyledMenu from './StyledMenu'
     
function viewMenu(props) {
    
    const {state,toggleDrawer} = props

    const classes = StyledMenu() 

    const menu = (
        <div>
            <Toolbar toggleDrawer={toggleDrawer}/>
                <Drawer anchor='left'  variant='temporary' open={state}  onClose={event => toggleDrawer(false, event)}>
                    <ListItem className={classes.menuInitialItem}  arial-label='inicio-menu'></ListItem >
                    <AdministrativeLinks toggleDrawer={toggleDrawer} /> 
                    <KeyHolderLinks toggleDrawer={toggleDrawer} /> 
                    <PagerAndSMSLinks toggleDrawer={toggleDrawer} /> 
                    <OverFillLinks toggleDrawer={toggleDrawer} /> 
                    <MultiloadControlLinks toggleDrawer={toggleDrawer} /> 
                    <ReporLinks toggleDrawer={toggleDrawer} /> 
                </Drawer>
        </div>)
 return(menu)

}
export default viewMenu
