import React from 'react'
import ViewGeneralReport from './ViewGeneralReport'


function GeneralReport(props) {

    const locationProps = props.location.props 

    let _headCells = locationProps !== undefined ? locationProps.headCells : [{}] 
    let _landscapeView = locationProps ? locationProps.landscapeView : false
    let _titleReport = locationProps ? locationProps.titleReport: ''
    let _allRows = locationProps ? locationProps.allRows : [{}] 

    return(<ViewGeneralReport 
        headCells={_headCells}
        landscapeView={_landscapeView}
        titleReport={_titleReport} 
        allRows={_allRows}
        />)
}

export default GeneralReport