import React from 'react'
import AppBar from '@material-ui/core/AppBar'
import Toolbar from '@material-ui/core/Toolbar'
import Typography from '@material-ui/core/Typography'
// import logo from '../../Assets/Imgs/logo.png'
import MenuIcon from '@material-ui/icons/Menu'
import MoreIcon from '@material-ui/icons/MoreVert'
import AccountCircle from '@material-ui/icons/AccountCircle'
//import NotificationsIcon from '@material-ui/icons/Notifications'
import IconButton from '@material-ui/core/IconButton'
//import Badge from '@material-ui/core/Badge'
import StyledUserLinks from './StyledToolbar'
import {Link} from 'react-router-dom'
import {useSelector} from 'react-redux'
import {userAuthenticated} from '../Store/userAuthenticated/userAuthenticated.selector'
import {Box} from '@material-ui/core'

const ViewToolbar = (props) => {
    
    const classes = StyledUserLinks()
    const userAuth = useSelector(userAuthenticated)

    return(
        <div className={classes.grow}>
        <AppBar className={classes.root} position="static">
          <Toolbar>
            {props.userAuth && !props.restartPasswordActivated ? 
            <IconButton
              edge="start"
              className={classes.menuButton}
              color="inherit"
              aria-label="Abre Menu"
              onClick={(event)=>{props.toggleDrawer(true,event)}}
            >
             {/* <img  alt='Logotipo' src={logo} /> */}
             <MenuIcon/>

            </IconButton> : null}

              <Link to={props.userAuth ? '/Home' : '/Login'}  style={{textDecoration:'none',color:'inherit'}}>
                <Box display='flex' flexDirection='column'>
                  <Box>
                    <Typography className={classes.title} variant="h6" noWrap>
                      R.A.I.S - Raizen Automation Interface System
                    </Typography>
                  </Box>
                  <Box>
                    <Typography className={classes.title} variant="caption" noWrap>
                        <u>Terminal 4.0</u>
                    </Typography>
                  </Box>
                </Box>
              </Link>

            <div className={classes.grow} />
            <div className={classes.sectionDesktop}>
              {/* <IconButton aria-label="Exibe at 11 Notificaes" color="inherit">
              <Badge badgeContent={11} color="secondary" >
                  <NotificationsIcon />
              </Badge>
              </IconButton> */}
              <div style={{marginLeft:'20px',marginRight:'5px'}}><h5>{userAuth !== null ? userAuth.name : ''}</h5></div>
                {props.userAuth ?
                <IconButton
                  edge="end"
                  aria-label="conta usurio logado"
                  aria-controls={props.menuId}
                  aria-haspopup="true"
                  onClick={props.handleProfileMenuOpen}
                  color="inherit"
                >
                  <AccountCircle />
                </IconButton> : null}
            </div>
            
            <div className={classes.sectionMobile}>
                <IconButton
                  aria-label="menu mais info"
                  aria-controls={props.mobileMenuId}
                  aria-haspopup="true"
                  onClick={props.handleMobileMenuOpen}
                  color="inherit"
                >
                  <MoreIcon />
                </IconButton>
            </div>
            
          </Toolbar>
        </AppBar>
        {props.renderMobileMenu}
        {props.renderMenu}
      </div>
    )}
export default ViewToolbar
