import React from 'react'
import StyledEnhancedTableRows from './StyledEnhancedTableRows' 
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableContainer from '@material-ui/core/TableContainer'
import TablePagination from '@material-ui/core/TablePagination'
import TableRow from '@material-ui/core/TableRow'
import {Paper,Box} from '@material-ui/core'
import EnhancedTableHead from '../EnhancedTableHead/EnhancedTableHead'
import EnhancedTableToolbar from '../EnhancedTableToolbar/EnhancedTableToolbar'
import Delete from '../../DeleteCommand/DeleteCommand'
import Update from '../../UpdateCommand/UpdateCommand'
import CircularProgress from '@material-ui/core/CircularProgress'
import formater from '../../../Assets/js/formater'

const ViewEnhancedTableRows = (props) => {
    
    const {handleRequestSort,showAddRegisterCommand=true,
           showDeleteRegisterCommand=true,
           showEditRegisterCommand=true,
           handleChangePage,handleChangeRowsPerPage,
           getComparator,stableSort,emptyRows,rows,
          order,orderBy,rowsPerPage,page,headCells,
          tableTitle,icon,DeletedRegister,api_url,
          host_api,
          pathEditComponentForUpdate,
          pathAddComponentForUpdate,
          handleSearching,routeName,
          titleReport, landscapeView,
          loading} = props
    
    const classes = StyledEnhancedTableRows()
    
      function typeTreatemet(value,type) {
        let ret=''
        switch (type) {
          case 'bool':
            ret = value === 1 ? 'Sim' : 'No'
            break
          case 'conditionalText':
            ret = value && value.trim().length > 0 ? 'Sim' : 'No'
            break
          case 'numberFormat':
            ret = value ? formater(parseInt(value),'numberFormat') : value
            break
          case 'truckLicense':
            ret = value && value.trim().length > 0 ? value  : value
            break
          case 'keyStatus':
            ret = value && parseInt(value) === 0 ? 'Retirada' : 'Disponvel'
            break
          case 'blockedUnblocked':
            ret = value && parseInt(value) === 0 ? 'Bloqueado' : 'Liberado'
            break
          default:
            ret= value 
            break
        }
        return ret
    }
    
  return(
    <>
        <Paper className={classes.paper} elevation={3}>
          <EnhancedTableToolbar 
              tableTitle={tableTitle} 
              icon={icon}
              handleSearching={handleSearching}
              />
          <TableContainer>
            <Table
              className={classes.table}
              aria-labelledby="Tabela apresentao de dados para edio"
              size={'small'} //'medium'
              aria-label="EnhancedTableRows"
              style={{whiteSpace:'nowrap'}}>
             

              <EnhancedTableHead
                showAddRegisterCommand={showAddRegisterCommand}
                classes={classes}
                order={order}
                orderBy={orderBy}
                onRequestSort={handleRequestSort}
                headCells={headCells}
                titleReport={titleReport}
                allRows={rows}
                landscapeView={landscapeView}
                routeName={routeName}
                pathAddComponentForUpdate={pathAddComponentForUpdate}/>
              {!loading && (
              <TableBody>
                  {stableSort(rows, getComparator(order, orderBy))
                    .slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
                    .map((row) => {
                      return (
                          <TableRow id={'row_' + row.id}  hover tabIndex={-1}  key={row.id} >
                              <TableCell  key={Math.random()} align='right' ml={3} >
                                  {showEditRegisterCommand &&
                                    <Update id={row.id}  titulo={'Salvar'} isIcon={true} pathEditComponentForUpdate={pathEditComponentForUpdate} routeName={routeName} />
                                  }  
                              </TableCell>
                            
                              {Object.keys(row).map((key)  => {
                                  const properties = headCells.filter(x=> x.id === key)
                                  return(
                                      key !== 'id' && headCells.findIndex(e=> e.id === key) >= 0 ? (
                                        <TableCell  key={Math.random()}  style={{ overflowWrap:'break-word',whiteSpace:'normal'}}  align={properties.length > 0 ? properties[0].align : 'left'} >
                                            {typeTreatemet(row[key], properties.length > 0 ? properties[0].type : 'text')}
                                        </TableCell>
                                      ): null 
                                  )
                              })} 
                              
                              <TableCell key={Math.random()} align='right' size='small'>
                                {showDeleteRegisterCommand &&
                                  <Delete id={row.id} titulo={'Excluir'} api_url={api_url} host_api={host_api} routeName={routeName}  DeletedRegister={DeletedRegister} isIcon={true}  />
                                }
                              </TableCell>
                        </TableRow>
                      )
                    })}
                  {emptyRows > 0 && (
                    <TableRow style={{ height:33 * emptyRows }}>
                      <TableCell colSpan={6} />
                    </TableRow>
                  )}
                </TableBody>
              )} 
           </Table>
           {loading && (
                 <Box display="flex" justifyContent="center" m={1} p={1} >
                 <Box p={1}>
                    <CircularProgress/>
                 </Box>
               </Box>
            )}     
          </TableContainer>
          <TablePagination
            rowsPerPageOptions={[5, 10]}
            labelRowsPerPage = 'Linhas por pgina:'
            labelDisplayedRows={({ from, to, count }) => `${from}-${to} de ${count} `}
            component="div"
            count={rows.length}
            rowsPerPage={rowsPerPage}
            page={page}
            onChangePage={handleChangePage}
            onChangeRowsPerPage={handleChangeRowsPerPage}
          />
        </Paper>
      </>      
    )

 
}
export default ViewEnhancedTableRows
