import { Container,Box,Paper,IconButton,Tooltip } from '@material-ui/core'
import React from 'react'
import Alert from '@material-ui/lab/Alert'
import {Link} from 'react-router-dom'
import PlayCircleOutlineIcon from '@material-ui/icons/PlayCircleOutline'

function RegisterConfirmation(props) {
    
    return( 
        <Container maxWidth='xs'>
        <Paper>
            <Box display='flex' justifyContent='center' mt={6} p={2}  >
                <Alert variant="filled" severity="info">
                        Enviamos um e-mail para confirmao de seu cadastro. 
                        Verifique sua caixa de e-mail e realize a confirmao.
                        
                </Alert>
            </Box>
            <Box display='flex' justifyContent='center'>
                <Tooltip title="Entrar no sistema" placement='bottom'>
                    <Link to='/login'>
                        <IconButton aria-label="enter">
                            <PlayCircleOutlineIcon fontSize="large" color='primary' />
                        </IconButton>    
                    </Link>
                </Tooltip>
            </Box>
        </Paper>
        </Container>
    )
}

export default RegisterConfirmation