import React from 'react'
import StyledReadKeyHolder from './StyledReadKeyHolder'
import {Box,Container} from '@material-ui/core'
import EnhancedTableRows from '../../../../ComponentsLibrary/EnhancedTable/EnhancedTableRows/EnhancedTableRows'
import Snackbar from '@material-ui/core/Snackbar'
import IconButton from '@material-ui/core/IconButton'
import CloseIcon from '@material-ui/icons/Close'
import ScreenLockLandscapeIcon from '@material-ui/icons/ScreenLockLandscape'
import DisplayErrosMessage from '../../../../ComponentsLibrary/DisplayErrorsMessage/DisplayErrorsMessage'

  const ViewReadKeyHolder = (props) => {
        const {keyHolder,DeletedRegister,loading,handleClose,infoErrors,snackInfo,
               handleCloseSnack,visibility} = props
         
        const styledReadKeyHolder=StyledReadKeyHolder()

        //Colunas da Tabela 
        const headCells = [
            { id: 'number',disablePadding: false, label: 'Nmero', align:'left', width:'10%'},
            { id: 'identification',disablePadding: false, label: 'Identificao', align:'left',width:'80%'},
            { id: 'qtdeKeys',disablePadding: false, label: 'Quantidade de Chaves', align:'left', width:'10%'}
        ]
     
      return(
        <Container maxWidth="lg">
            <Box className={styledReadKeyHolder.MainBox}>
              <EnhancedTableRows 
                  rows={keyHolder}
                  headCells={headCells}
                  tableTitle={'Cadastro de Claviculrio'}
                  icon={<ScreenLockLandscapeIcon fontSize='large' className={styledReadKeyHolder.color}/>}
                  DeletedRegister={DeletedRegister}
                  api_url='/deleteKeyHolder'
                  pathEditComponentForUpdate = '/UpdateKeyHolder'
                  pathAddComponentForUpdate = '/addKeyHolder'
                  routeName='KeyHolder'
                  showAddRegisterCommand={true}  
                  showDeleteRegisterCommand={true}
                  showEditRegisterCommand={true}
                  titleReport='Cadastro de Claviculrio'
                  landscapeView={true}
                  loading={loading}>
                </EnhancedTableRows> 
            </Box>
            <Box className={styledReadKeyHolder.MainBox}>
              <DisplayErrosMessage infoErrors={infoErrors} handleCloseErros={handleClose}  />
            </Box>
            <Snackbar  anchorOrigin={{vertical: 'bottom',horizontal: 'center'}}
                      open={visibility}  autoHideDuration={3000} onClose={handleCloseSnack} message={snackInfo.message}
                      action={
                            <React.Fragment>
                                <IconButton size="small" aria-label="close" color="inherit" onClick={handleCloseSnack}>
                                <CloseIcon fontSize="small" />
                                </IconButton>
                            </React.Fragment>
                         }
                         />
            
        </Container>
      )}

export default ViewReadKeyHolder
