import React from 'react'
import ViewDeleteCommand from './ViewDeleteCommand'
import { useSelector } from 'react-redux'
import verifyPermissions from '../../Assets/js/verifyPermissions'
import {getApi} from '../../Assets/js/apiProxy'

function DeleteCommand(props) {
      
   const {isIcon,DeletedRegister,routeName,host_api}= props
   const permissions = useSelector(state=> state.permission)
   const userAuthenticated = useSelector(state=> state.authenticated)
   const  permission = verifyPermissions(routeName,'write',userAuthenticated,permissions)

   let apiHostBackEnd = getApi(host_api)
  
   const handleDeleteCommand = () => {
      if (props.id === 0) {
         return null
      }
      
      apiHostBackEnd.post(props.api_url,{id:props.id})
      .then((response)=> {
         DeletedRegister(props.id)
      })
      .catch((error)=> {
         DeletedRegister(props.id,error)
         console.log(error)
      })
   }

   return (
   <ViewDeleteCommand id={props.id}  
      titulo={props.titulo} 
      permission={permission}
      userAuthenticated={userAuthenticated}
      api_url={props.api_url}
      handleDeleteCommand={handleDeleteCommand} 
      routeName={routeName}
      isIcon={isIcon} />)
   
}

export default DeleteCommand 