import React from 'react'
import StyledReadMonitorOverfill from './StyledReadMonitorOverfill'
import { withStyles } from '@material-ui/core/styles'
import {Box,TextField,Button,Link} from '@material-ui/core'
import Table from '@material-ui/core/Table'
import TableBody from '@material-ui/core/TableBody'
import TableCell from '@material-ui/core/TableCell'
import TableContainer from '@material-ui/core/TableContainer'
import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'
import Paper from '@material-ui/core/Paper'
import DesktopMacIcon from '@material-ui/icons/DesktopMac'
import CircularProgress from '@material-ui/core/CircularProgress'
import DisplayErrosMessage from '../../../../ComponentsLibrary/DisplayErrorsMessage/DisplayErrorsMessage'
import Typography from '@material-ui/core/Typography'
import LinearProgress from '@material-ui/core/LinearProgress'
import Dialog from '@material-ui/core/Dialog'
import DialogActions from '@material-ui/core/DialogActions'
import DialogContent from '@material-ui/core/DialogContent'
import DialogContentText from '@material-ui/core/DialogContentText'
import DialogTitle from '@material-ui/core/DialogTitle'
import InputMask from 'react-input-mask'

  const ViewReadMonitorOverfill = (props) => {
        const {monitorOverfill,loading,handleClose,infoErrors,handleChange,
          compartmentNumber,selectedTruck,open,handleSaveCompartmentNumber,
          handleDialogClose,loadingDialog,handleClick,licenseNumber} = props
     
        const styledReadMonitorOverfill=StyledReadMonitorOverfill()
        const StyledTableCell = withStyles((theme) => ({
          head: {
            backgroundColor: theme.palette.common.white,
            color: theme.palette.common.black,
            fontWeight:'bold',
          },
          body: {
            fontSize: 12,
          },
        }))(TableCell);

       
      return(
        <>
            <Box className={styledReadMonitorOverfill.MainBox}>
              <Paper elevation={3} className={styledReadMonitorOverfill.PaperMain}>
                  <Box display='flex' justifyContent='flex-start' arial-label='header-title-icon' m={1}>
                    <Box display='flex'arial-label='header-description'>
                        <DesktopMacIcon fontSize='large' className={styledReadMonitorOverfill.color} />
                        <Box fontWeight='medium' mt={1} ml={1}>
                          <Typography id="formTitle">
                            Condies dos Overfills
                          </Typography>
                        </Box>
                    </Box>
                  </Box>
                  <Box p={1}>
                  <TableContainer component='div'>
                    <Table className={styledReadMonitorOverfill.table} aria-label="tabela monitor overfill">
                      <TableHead>
                        <TableRow>
                          <StyledTableCell align="center">N Baia</StyledTableCell>
                          <StyledTableCell align="center">Baia</StyledTableCell>
                          <StyledTableCell align="center"><Box>Tipo da</Box><Box>Baia</Box></StyledTableCell>
                          <StyledTableCell align="center">Placa</StyledTableCell>
                          <StyledTableCell align="center">Viagem</StyledTableCell>
                          <StyledTableCell align="center">B-Trem</StyledTableCell>
                          <StyledTableCell align="center"><Box>Compartimentos</Box><Box>1Carreta</Box></StyledTableCell>
                          <StyledTableCell align="center"><Box>Compartimentos</Box><Box>2Carreta</Box></StyledTableCell>
                          <StyledTableCell align="center"><Box>Total de</Box><Box>Compartimentos</Box></StyledTableCell>
                          <StyledTableCell align="center"><Box>Compartimentos</Box><Box>Validados</Box></StyledTableCell>
                          <StyledTableCell align="center">Situao</StyledTableCell>
                        </TableRow>
                      </TableHead>
                      {!loading && (
                          <TableBody>
                            {monitorOverfill.map((row,i) => (
                              <TableRow key={row.bayNumber} className={ i % 2? styledReadMonitorOverfill.TableRowColor : null}>
                                <StyledTableCell align="center">{row.bayNumber}</StyledTableCell>
                                <StyledTableCell align="center">{row.bayName}</StyledTableCell>
                                <StyledTableCell align="center">{row.bayType}</StyledTableCell>
                                <StyledTableCell align="center">{row.at}</StyledTableCell>
                                <StyledTableCell align="center">{row.tripNumber}</StyledTableCell>
                                <StyledTableCell align="center">{row.b_train ? 'SIM' : 'NO'}</StyledTableCell>
                                <StyledTableCell align="center"><Link href='#' onClick={(e)=> handleClick(e,row.id,1,row.at,row.compartSecondTrain)}>{row.compartFirstTrain}</Link></StyledTableCell>
                                <StyledTableCell align="center"><Link href='#' onClick={(e)=> handleClick(e,row.id,2,row.at,row.compartFirstTrain)}>{row.compartSecondTrain}</Link></StyledTableCell>
                                <StyledTableCell align="center">{row.totalCompart}</StyledTableCell>
                                <StyledTableCell align="center">{row.compartAudited}</StyledTableCell>
                                <StyledTableCell align="center">{row.at ? row.status ? 'LIBERADO' : 'BLOQUEADO' : ''}</StyledTableCell>
                              </TableRow>
                            ))}
                          </TableBody>
                      )} 
                    </Table>
                    {loading && (
                      <Box display="flex" justifyContent="center" m={1} p={1} >
                        <Box p={1}>
                            <CircularProgress/>
                        </Box>
                      </Box>
                    )}
                  </TableContainer>
               </Box>
              </Paper>
            </Box>  
            <Box className={styledReadMonitorOverfill.MainBox}>
              <DisplayErrosMessage infoErrors={infoErrors} handleCloseErros={handleClose}  />
            </Box>
            {open && (
                 <Box>
                    <Dialog
                        open={open}
                        onClose={handleClose}
                        aria-labelledby="update-compartment-number"
                        aria-describedby="update-compartment-number">
                
                        <DialogTitle id="update-compartment-number-dialogTitle">{`Informe o nmero de compartimentos na carreta ${selectedTruck} do AT ${licenseNumber}:`}</DialogTitle>
                        <DialogContent>
                        <DialogContentText id="alert-update-compartment-number">
                        <InputMask 
                            mask='99'
                            maskChar=''
                            value={compartmentNumber} 
                            onChange={event=> handleChange(event,true)}>
                            {inputProps =>
                                <TextField
                                {...inputProps}
                                inputRef={input => input && input.focus()}
                                id='compartmentNumber' 
                                name='compartmentNumber'
                                autoComplete='off'/>}
                           
                        </InputMask>
                        </DialogContentText>
                        </DialogContent>
                        <DialogActions>
                        <Button onClick={handleSaveCompartmentNumber} color="primary" autoFocus>
                            Salvar 
                        </Button>
                        <Button onClick={handleDialogClose} color="primary" autoFocus>
                            Fechar
                        </Button>
                        </DialogActions>
                        {loadingDialog && (
                           <LinearProgress />
                        )}
                    </Dialog>
                </Box>
               )}
            
        </>
      )}

export default ViewReadMonitorOverfill
