import phoneNumber from './formatPhoneNumber'
import yyyymmdd from './dateStringConvert'
import numberFormat from './numberFormat'

const formater = (value,type)=>  { 

    switch(type) {
        case 'telefone': {
                return phoneNumber(value)
        }
        case 'date': {
            return yyyymmdd(value)
        }
        case 'numberFormat': {
            return numberFormat(value)
        }
        default:
            return value
    }

}

export default formater