import {createStore,combineReducers} from 'redux'
import {persistStore,persistReducer} from 'redux-persist'
import storage from 'redux-persist/lib/storage'
import userAuthenticated from './userAuthenticated/userAuthenticated.reducer'
import mnuMounted from './mnuMounted/mnuMounted.reducer'
import permission from './Permission/permission.reducer'

const rootReducer = combineReducers ({
    authenticated:  userAuthenticated,
    mnuMounted: mnuMounted,
    permission: permission
})

const persistedReducer = persistReducer({
    key: 'root',
    storage
},rootReducer)

 /* eslint-disable no-underscore-dangle */
    export const mainStore = createStore(persistedReducer, window.__REDUX_DEVTOOLS_EXTENSION__ && window.__REDUX_DEVTOOLS_EXTENSION__())
    export const persistedStore = persistStore(mainStore)
/* eslint-enable */    

