import React from 'react'
import {TextField,Switch,Box} from '@material-ui/core'
import FormControlLabel from '@material-ui/core/FormControlLabel'
import InputMask from 'react-input-mask'
import StyledUpdateTruck from './StyledUpdateTruck'
import AtIcon from '@material-ui/icons/LocalShipping'
import BodyForm from '../../../../ComponentsLibrary/Form/BodyForm/BodyForm'
import Card from '@material-ui/core/Card'
import CardContent from '@material-ui/core/CardContent'
import Typography from '@material-ui/core/Typography'


const ViewUpdateTruck =  (props)  => {

    const {handleSubmit,truck,
            handleChange,handleBlur,
            inputvalidation,infoErrors,
            serverValidation,handleCloseInfo,
            handleCloseErros,loading,bTrainState} = props

    const styledUpdateTruck = StyledUpdateTruck()

return( <BodyForm infoErrors={infoErrors} 
        serverValidation={serverValidation} 
        handleCloseInfo={handleCloseInfo}
        handleCloseErros={handleCloseErros}
        handleSubmit={handleSubmit}
        loading={loading}
        icone={<AtIcon fontSize='large' className={styledUpdateTruck.color} />}
        title='Cadastro de AT'
        urlBackCommand='/allTruck'
        showSaveButton={true} showBackButton={true}
        paperMainWidth='50%'>
                    
                <InputMask 
                mask='aaa9999'
                maskChar=''
                value={truck.at} 
                onChange={event=> handleChange(event,true)}
                onBlur={event=> handleBlur(event)}>
                {inputProps =>
                    <TextField
                    {...inputProps}
                    id='at' 
                    name='at'
                    label='Placa'
                    autoComplete='off'
                    inputProps={{style:{textAlign:'left'}}}
                    className={styledUpdateTruck.smallTextField}
                    helperText={inputvalidation.keys.at !== '' ? inputvalidation.keys.at  : ''}
                    error={inputvalidation.keys.at !== '' ? true : false}
                    />}
            </InputMask>
            <InputMask 
                mask='99.999'
                maskChar=''
                value={truck.capacity} 
                onChange={event=> handleChange(event,true)}
                onBlur={event=> handleBlur(event)}>
                {inputProps =>
                    <TextField
                    {...inputProps}
                    id='capacity' 
                    name='capacity'
                    label='Capacidade'
                    autoComplete='off'
                    inputProps={{style:{textAlign:'left'}}}
                    className={styledUpdateTruck.smallTextField}
                    helperText={inputvalidation.keys.capacity !== '' ? inputvalidation.keys.capacity  : ''}
                    error={inputvalidation.keys.capacity !== '' ? true : false}
                    />}
            </InputMask>
            <Box display='flex' justifyContent='left' ml={2}>
                    <FormControlLabel
                        control={<Switch id='b_train'
                        name='b_train' 
                        checked={Boolean(truck.b_train)}
                        onChange={e=> handleChange(e)}/>  }
                        label={
                            <Box component="div" fontSize={15}>
                               Se o AT for B-Trem, informe o total de Compartimentos da segunda Carreta.
                             </Box>
                       }
                        className={styledUpdateTruck.switchText}
                    />
            </Box>
            <Box display='flex' flexDirection='row' justifyContent='center' m={2}>
                <Card  variant="outlined">
                    <CardContent>
                    <Typography gutterBottom >
                        <Box fontWeight="fontWeightLight" fontSize={15} className={styledUpdateTruck.switchText}>
                            Defina o Nmero de Compartimentos
                        </Box>
                    </Typography>
                        <Box display='flex' flexDirection='row' justifyContent='center'>
                            <InputMask 
                                mask='99'
                                maskChar=''
                                value={truck.compartFirstTrain} 
                                onChange={event=> handleChange(event,true)}
                                onBlur={event=> handleBlur(event)}>
                                {inputProps =>
                                    <TextField
                                    {...inputProps}
                                    id='compartFirstTrain' 
                                    name='compartFirstTrain'
                                    label='Primeira Carreta'
                                    autoComplete='off'
                                    inputProps={{style:{textAlign:'left'}}}
                                    helperText={inputvalidation.keys.compartFirstTrain !== '' ? inputvalidation.keys.compartFirstTrain  : ''}
                                    error={inputvalidation.keys.compartFirstTrain !== '' ? true : false}
                                    />}
                            </InputMask>
                        </Box>
                        {bTrainState ?
                            <Box display='flex' flexDirection='row' justifyContent='center'>
                                <InputMask 
                                    mask='99'
                                    maskChar=''
                                    value={truck.compartSecondTrain} 
                                    onChange={event=> handleChange(event,true)}
                                    onBlur={event=> handleBlur(event)}>
                                    {inputProps =>
                                        <TextField
                                        {...inputProps}
                                        id='compartSecondTrain' 
                                        name='compartSecondTrain'
                                        label='Segunda Carreta'
                                        autoComplete='off'
                                        inputProps={{style:{textAlign:'left'}}}
                                        helperText={inputvalidation.keys.compartSecondTrain !== '' ? inputvalidation.keys.compartSecondTrain  : ''}
                                        error={inputvalidation.keys.compartSecondTrain !== '' ? true : false}
                                        />}
                                </InputMask>
                            </Box>
                        : null}
                    </CardContent>
                </Card>
            </Box>
</BodyForm>
      
    )

}
export default ViewUpdateTruck
