import React from 'react'
import { FormControl,Box,TextField,InputLabel,Tooltip,IconButton,Button} from '@material-ui/core'
import StyledUpdateUser from './StyledUpdateUser'
import PersonOutlinedIcon from '@material-ui/icons/PersonOutline'
import Switch from '@material-ui/core/Switch'
import FormControlLabel from '@material-ui/core/FormControlLabel'
import Select from '@material-ui/core/Select'
import BodyForm from '../../../../ComponentsLibrary/Form/BodyForm/BodyForm'
import WifiIcon from '@material-ui/icons/Wifi'
import Dialog from '@material-ui/core/Dialog'
import DialogActions from '@material-ui/core/DialogActions'
import DialogContent from '@material-ui/core/DialogContent'
import DialogContentText from '@material-ui/core/DialogContentText'
import DialogTitle from '@material-ui/core/DialogTitle'
import LinearProgress from '@material-ui/core/LinearProgress'

const ViewUpdateUser =  (props)  => {

    const {handleSubmit,user,
            handleChange,handleBlur,
            inputvalidation,infoErrors,
            serverValidation,handleCloseInfo,
            handleCloseErros,handleChangeActivated,
            handleChangeResetPassword,profileNumber,
            handleClose,open,handleClickOpen,RFID,
            profileList,loading,loadingRFID,editNotPermited} = props

    const styledUpdateUser = StyledUpdateUser()

    return(<BodyForm infoErrors={infoErrors} 
        serverValidation={serverValidation} 
        handleCloseInfo={handleCloseInfo}
        handleCloseErros={handleCloseErros}
        handleSubmit={handleSubmit}
        loading={loading}
        icone={<PersonOutlinedIcon fontSize='large' className={styledUpdateUser.color} />}
        title='Cadastro de Usurios'
        urlBackCommand='/allUser'
        showSaveButton={true} showBackButton={true}
        paperMainWidth='40%'>
            <Box>
            <TextField 
                id='name' 
                name='name'
                label='Nome' 
                autoComplete='off'
                className={styledUpdateUser.largeTextField}
                value={user.name} 
                onChange={event=> handleChange(event,false)}
                onBlur={event=> 
                    handleBlur(event)
                }
                helperText={inputvalidation.keys.name !== '' ? inputvalidation.keys.name  : ''}
                error={inputvalidation.keys.name !== '' ? true : false}
                inputProps={{ maxLength: 35 }}
            />
            <TextField 
                disabled={editNotPermited ? true : false}
                id='email' 
                name='email'
                label='e-mail'
                autoComplete='off'
                className={styledUpdateUser.largeTextField}
                value={user.email} 
                onChange={event=> handleChange(event,false)}
                onBlur={event=> handleBlur(event)}
                helperText={inputvalidation.keys.email !== '' ? inputvalidation.keys.email  : ''}
                error={inputvalidation.keys.email !== '' ? true : false}
                inputProps={{ maxLength: 35 }}
            />
        </Box>
        <Box>
            <TextField 
                disabled={editNotPermited ? true : false}
                id='logonUser' 
                name='logonUser'
                label='Usurio'
                autoComplete='off'
                value={user.logonUser} 
                onChange={event=> handleChange(event,false)}
                onBlur={event=> handleBlur(event)}
                helperText={inputvalidation.keys.logonUser !== '' ? inputvalidation.keys.logonUser  : ''}
                error={inputvalidation.keys.logonUser !== '' ? true : false}
                inputProps={{ maxLength: 20 }}
            />
            <TextField 
                id='password' 
                name='password'
                label='Senha'
                autoComplete='off'
                type='password'
                value={user.password} 
                onChange={event=> handleChange(event,false)}
                onBlur={event=> handleBlur(event)}
                helperText={inputvalidation.keys.password !== '' ? inputvalidation.keys.password  : ''}
                error={inputvalidation.keys.password  !== '' ? true : false}
                inputProps={{ maxLength: 9 }}
            />
        </Box>
           

        
        {profileNumber || profileNumber === -1?
            
                <Box display='flex' direction='row' justifyContent='space-evenly'  m={3}>
                    <Box>
                            <FormControl>
                            <InputLabel htmlFor="profile-native-simple">Perfil</InputLabel>
                                <Select native
                                    value={user.profile_id}
                                    onChange={handleChange}
                                    inputProps={{
                                        name: 'profile_id',
                                        id: 'profile-native-simple',
                                    }}
                                    >
                                    <option value="-1">Opes...</option>
                                    {profileList.map((e,i) =>  { 
                                        return (<option key={i} value={e.id}>{e.name}</option>)
                                    })}   
                                </Select>
                            </FormControl>
                    </Box> 
                </Box> 
            
        :null}

        {profileNumber > 0 || profileNumber === -1 ? 
                <Box ml={1}>
                    <FormControlLabel
                        control={<Switch color="primary" checked={Boolean(user.activated)} onChange={handleChangeActivated} name="activated" />}
                        label="Ativado"/>                                            
                </Box> 
        : null}
        {profileNumber > 0 || profileNumber === -1 ? 
            <Box ml={1}>
                <FormControlLabel
                    control={<Switch color="primary" checked={Boolean(user.restartPassword)} onChange={handleChangeResetPassword} name="restartPassword" />}
                    label="Reinicializar senha"/>
            </Box>
        : null}
        {user.id > 0? 
            <Box display='flex' justifyContent='center'>
                <Tooltip title="Associar carto RFID ao usurio" placement='top'>
                    <IconButton aria-label="Associar" onClick={handleClickOpen}>
                    <WifiIcon fontSize="large" color='primary' />
                    </IconButton>
                </Tooltip>
            </Box>
        : null}
        <Box>
            <Dialog
                open={open}
                onClose={handleClose}
                aria-labelledby="Associao de RFID"
                aria-describedby="Associando RFID ao usurio">
        
                <DialogTitle id="RFIDAssociation">{"Associando carto RFID ao usurio."}</DialogTitle>
                <DialogContent>
                <DialogContentText id="alert-dialog-description">
                    Passe o carto no leitor e aguarde a concluso da associao com seu usurio. 
                    Voc poder cancelar este processo a qualquer momento acionando o boto fechar abaixo.
                    <Box display='flex' justifyContent='center'><Box m={2} className={styledUpdateUser.RFIDMsg}>Status: {RFID ? 'Carto associado com sucesso.' : 'Lendo dados do carto, Aguarde...'}</Box></Box>
                </DialogContentText>
                </DialogContent>
                <DialogActions>
                <Button onClick={handleClose} color="primary" autoFocus>
                    Fechar
                </Button>
                </DialogActions>
                {loadingRFID ?
                    <LinearProgress color="primary" />
                :
                    null
                }
            </Dialog>
        </Box>
</BodyForm>                            
                        
    )

}
export default ViewUpdateUser
