import React,{useState} from 'react'
import {Link} from "react-router-dom"
import List from '@material-ui/core/List'
import ListItem from '@material-ui/core/ListItem'
import ListItemText from '@material-ui/core/ListItemText'
import PermIdentity from '@material-ui/icons/PermIdentity'
import SettingsIcon from '@material-ui/icons/Settings'
import SecurityIcon from '@material-ui/icons/Security'
import ListIcon from '@material-ui/icons/List'
import FeaturedPlayListIcon from '@material-ui/icons/FeaturedPlayList'
import styledLinks from './StyledLinks'
import { useSelector } from 'react-redux'
import verifyPermissions from '../../../Assets/js/verifyPermissions'
import Typography from '@material-ui/core/Typography'
import ExpandLess from '@material-ui/icons/ExpandLess'
import ExpandMore from '@material-ui/icons/ExpandMore'
import Collapse from '@material-ui/core/Collapse'

const AdministrativeLinks = (props) => {
    
    const classes = styledLinks()
    
    const permissions = useSelector(state=> state.permission)
    const userAuthenticated = useSelector(state=> state.authenticated)

    const [openAdministrativeLinks,setOpenAdministrativeLinks] = useState(false)
    const handleClick = () => setOpenAdministrativeLinks(!openAdministrativeLinks)

    let permission=false

    return(
    <div className={classes.drawerWidth} >
        <List component="nav">
            <ListItem button={true} onClick={handleClick} className={classes.ListItem}>
                        <ListItemText><Typography color="primary">Configurao do Sistema</Typography></ListItemText>
                        {openAdministrativeLinks ? <ExpandLess /> : <ExpandMore />}
            </ListItem>
            <Collapse in={openAdministrativeLinks} timeout="auto" unmountOnExit>
                {permission = verifyPermissions('ConfigParameter','read',userAuthenticated,permissions)}
                <Link to={ permission ? "/allConfigParameter" : "#"} onClick={event=> props.toggleDrawer(verifyPermissions('ConfigParameter','read',userAuthenticated,permissions) ? false :true,event)} className={classes.linkDecoration}  >
                    <ListItem button className={classes.nested}>
                        <SettingsIcon className={classes.color}  /><ListItemText className={permission ? classes.menuText : classes.colorDisable} primary='Parmetros de Configurao' />
                    </ListItem>
                </Link>

                {permission = verifyPermissions('Profile','read',userAuthenticated,permissions)}
                    <Link to={permission ? "/allProfile" : "#"} onClick={event=> props.toggleDrawer(verifyPermissions('Profile','read',userAuthenticated,permissions) ? false : true,event)} className={classes.linkDecoration}  >
                        <ListItem button className={classes.nested}>
                            <ListIcon className={classes.color} /><ListItemText className={permission ? classes.menuText : classes.colorDisable} primary='Perfil' />
                        </ListItem>
                    </Link>

                {permission = verifyPermissions('Route','read',userAuthenticated,permissions)}
                    <Link to={permission ? "/allRoute" : "#"} onClick={event=> props.toggleDrawer(verifyPermissions('Route','read',userAuthenticated,permissions) ? false : true,event)} className={classes.linkDecoration}  >
                        <ListItem button className={classes.nested}>
                            <FeaturedPlayListIcon className={classes.color} /><ListItemText className={permission ? classes.menuText : classes.colorDisable} primary='Funcionalidades' />
                        </ListItem>
                    </Link>

                {permission = verifyPermissions('Permission','read',userAuthenticated,permissions)}
                    <Link to={permission ? "/UpdatePermission" : "#"} onClick={event=> props.toggleDrawer(verifyPermissions('Permission','read',userAuthenticated,permissions) ? false : true,event)} className={classes.linkDecoration}  >
                        <ListItem button className={classes.nested}>
                            <SecurityIcon className={classes.color} /><ListItemText className={permission ? classes.menuText : classes.colorDisable}  primary='Permisses' />
                        </ListItem>
                    </Link>

                {permission = verifyPermissions('User','read',userAuthenticated,permissions)}
                    <Link to={permission ? "/allUser" : "#"} onClick={event=> props.toggleDrawer(verifyPermissions('User','read',userAuthenticated,permissions) ? false : true,event)} className={classes.linkDecoration}  >
                        <ListItem button className={classes.nested}>
                            <PermIdentity className={classes.color} /><ListItemText className={permission ? classes.menuText : classes.colorDisable}  primary='Usurios' />
                        </ListItem>
                    </Link>
            </Collapse>
        </List>
    </div>
    )}

export default AdministrativeLinks
