import React from 'react'
import propTypes from 'prop-types'
import TableCell from '@material-ui/core/TableCell'
import TableHead from '@material-ui/core/TableHead'
import TableRow from '@material-ui/core/TableRow'
//import TableSortLabel from '@material-ui/core/TableSortLabel'
import StyledEnhancedTableHead from './StyledEnhancedTableHead'
import {Box} from '@material-ui/core'
import { Link } from  'react-router-dom'  
import Tooltip from '@material-ui/core/Tooltip'
import AddOutlinedIcon from '@material-ui/icons/AddOutlined'
import IconButton from '@material-ui/core/IconButton'
import PrintIcon from '@material-ui/icons/Print'

const ViewEnhancedTableHead = (props) => {
    
    const classes =  StyledEnhancedTableHead()
    // Incluir esta prop se voltar com a ordenao. (onRequestSort)
    const {order, orderBy,headCells,
           titleReport,landscapeView,allRows,
           pathAddComponentForUpdate,permission,
           showAddRegisterCommand} = props

    let _path = !permission ? '#' : {pathname:pathAddComponentForUpdate}

    // const createSortHandler = (property) => (event) => {
    //     onRequestSort(event, property)
    // }
      
    return(
        <TableHead>
        <TableRow>
        <TableCell arial-label='comando-criar' style={{paddingRight:'10px'}} >
            {showAddRegisterCommand && 
              <Tooltip title="Criar um novo registro">
                        <Link to={_path}  arial-label='link-para-criao'>
                          <IconButton  p={1} arial-label='create-register'>
                              <AddOutlinedIcon className={!permission ? classes.colorDisable : classes.color}/>
                          </IconButton>
                        </Link>
              </Tooltip>
            }
        </TableCell>
          {headCells.map((headCell) => (
            <TableCell  
              width={headCell.width}
              arial-label='coluna-tabela'
              key={headCell.id}
              align={headCell.align ? headCell.align : 'left'}
              padding={headCell.disablePadding ? 'none' : 'default'}
              sortDirection={orderBy === headCell.id ? order : false}>
                    <Box>
                      {/* <TableSortLabel active={orderBy === headCell.id} direction={orderBy === headCell.id ? order : 'asc'}
                            onClick={createSortHandler(headCell.id)}>
                                {orderBy === headCell.id ? (
                                <span className={classes.visuallyHidden}>
                                  {order === 'desc' ? 'sorted descending' : 'sorted ascending'}
                                </span>
                              ) : null}
                      </TableSortLabel> */}
                      <strong>{headCell.label}</strong>
                        
                    </Box>
            </TableCell>
          ))}
           <TableCell>
            <Link to={{pathname:'/GeneralReport',props:{titleReport:titleReport,allRows:allRows,headCells:headCells,landscapeView:landscapeView}}}>
                  <IconButton color="primary" aria-label="Imprimir" size="small" >
                      <PrintIcon size="inherit" />
                  </IconButton>
              </Link>
           </TableCell>
        </TableRow>
      </TableHead>
    )
}

ViewEnhancedTableHead.propTypes = {
    onRequestSort: propTypes.func.isRequired,
    order: propTypes.oneOf(['asc', 'desc']).isRequired,
    orderBy: propTypes.string.isRequired,
    pathAddComponentForUpdate:propTypes.string.isRequired
}

export default ViewEnhancedTableHead
