import React,{useState} from 'react'
import ViewReportViewer from './ViewReportViewer'

function ReportViewer(props) {

    const {titleReport,allRows,landscapeView,headCells} = props

    let _allRows = allRows
   
    const rowsPerPage = 20
    const totalPages= Math.ceil(_allRows.length/rowsPerPage)
    const [page, setPage] = useState(1)
    const [rows,setRows] = useState(_allRows.slice(0,20))
    let actualPage = page
   
       const exportToExcel = () => {
        const exportToExcelcmd = document.getElementById('table-xls-button')
        exportToExcelcmd.click()
    }

        const pagination = (pageNumber)=> {
            _allRows = _allRows.slice((pageNumber*rowsPerPage)-rowsPerPage,actualPage*rowsPerPage)
            setRows(_allRows)
        }

        const goFirstPage = () =>  {
            setPage(1)
            _allRows = _allRows.slice(0,20)
            setRows(_allRows)
        }

        const goNextPage = () =>  {
            if ((page+1) <= totalPages) { 
                actualPage = page + 1
                setPage(actualPage)
                pagination(actualPage)
            }
        }

        const goLastPage = () =>  {
            setPage(totalPages)
            pagination(totalPages)
        }
        
        const goSpecificPage = (e) =>  {
            let specificPage = e.target.value
            if (!isNaN(specificPage) && parseInt(specificPage) > totalPages) { 
                alert('Esta pgina no existe')
                goFirstPage()
            } else {
                setPage(specificPage)
                pagination(specificPage)
            }
        }

        const goPreviousPage = () =>  {
            if ((page-1)  >= 1) {
                actualPage = page - 1
                setPage(actualPage)
                pagination(actualPage)
             }
        }


        return(<ViewReportViewer 
        rows = {rows}
        allRows = {_allRows}
        landscapeView={landscapeView}
        page = {page}
        rowsPerPage = {rowsPerPage}
        totalPages = {totalPages}
        exportToExcel={exportToExcel}
        goFirstPage = {goFirstPage}
        goNextPage = {goNextPage}
        goLastPage = {goLastPage}
        goSpecificPage = {goSpecificPage}
        goPreviousPage = {goPreviousPage}
        headCells = {headCells}
        titleReport ={titleReport}
    />)
}

export default ReportViewer