import React from 'react'
import ViewFooterForm from './ViewFooterForm'

function FooterForm(props) {

    const {handleSubmit,urlBackCommand, showSaveButton,showBackButton} = props
    
    return(<ViewFooterForm 
                handleSubmit={handleSubmit}
                urlBackCommand={urlBackCommand}
                showSaveButton={showSaveButton}
                showBackButton={showBackButton}
                />)
}

export default FooterForm