import React from 'react'
import {Box,Paper} from '@material-ui/core'
import StyledFormBody from './StyledBodyForm'
import HeaderForm from '../../Form/HeaderForm/HeaderForm'
import FooterForm from '../../Form/FooterForm/FooterForm'
import DisplayErrosMessage from '../../DisplayErrorsMessage/DisplayErrorsMessage'
import DisplayInfoMessage from '../../DisplayInfoMessage/DisplayInfoMessage'
import LinearProgress from '@material-ui/core/LinearProgress'

const ViewFormBody =  (props)  => {

    const {_rest} = props

    const styled = StyledFormBody(props)

    return(    
    <Box className={styled.MainBox}>
        <Paper elevation={3} className={styled.PaperMain}>
                <HeaderForm icon={_rest.icone} titulo={_rest.title}/>
                <Box display='flex' justifyContent='center'>
                    <form className={styled.root} noValidate autoComplete="off" style={{width:'90%'}}>
                            <Box className={styled.ContaineFormControls}>
                                {props.children}
                            </Box>
                    </form>
                </Box>
            <DisplayErrosMessage infoErrors={_rest.infoErrors} handleCloseErros={_rest.handleCloseErros}  />
            <DisplayInfoMessage serverValidation={_rest.serverValidation} handleCloseInfo={_rest.handleCloseInfo}/>
            <FooterForm handleSubmit={_rest.handleSubmit} 
                        urlBackCommand={_rest.urlBackCommand}
                        showSaveButton={_rest.showSaveButton} 
                        showBackButton={_rest.showBackButton}/>            
                
            {_rest.loading && 
                <LinearProgress />      
            }       
        </Paper>
    </Box>

    )

}
export default ViewFormBody
