function InputValidateDriverSMS(event) {
    
    let ret={msg:"",isError:false}

    switch (event.target.code) {
        case 'code':
            if(event.target.value.length === 0) {
                ret.msg = 'Cdigo  obrigatrio.'
                ret.isError=true
            }
            break
        case 'phoneNumber':
            if(isNaN(event.target.value) || event.target.value === '') {
                ret.msg = 'Telefone com DDD  obrigatrio.'
                ret.isError=true
            }
            break
        default:
            ret.msg=""
            ret.isError=false
        }
        return ret
    }
   

    export default InputValidateDriverSMS