import {useState} from 'react'
import React from 'react'
import ViewMenu from './ViewMenu'
//import configGlobalMoment from '../../Config/momentjs_config'


function Menu(props) {
    
    //configGlobalMoment()

    let anchorState = false
    const [anchor,setAnchor] = useState(anchorState)

    const toggleDrawer =  (open,event) => {
        if (event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
            return;
        }
        anchorState = open
        setAnchor(anchorState);
    }

    return(<ViewMenu state={anchor} toggleDrawer={toggleDrawer} />)
}

export default Menu