//#region - imports
    import React from 'react'
    import {Box,Paper,InputLabel,Select} from '@material-ui/core'
    import StyledUpdatePermission from './StyledUpdatePermission'
    import HeaderForm from '../../../../ComponentsLibrary/Form/HeaderForm/HeaderForm'
    import DisplayErrosMessage from '../../../../ComponentsLibrary/DisplayErrorsMessage/DisplayErrorsMessage'
    import DisplayInfoMessage from '../../../../ComponentsLibrary/DisplayInfoMessage/DisplayInfoMessage'
    import SecurityIcon from '@material-ui/icons/Security'
    import LinearProgress from '@material-ui/core/LinearProgress'
    import Switch from '@material-ui/core/Switch'
   
//#endregion

const ViewUpdatePermission =  (props)  => {

    const {permission,
            handleChangeSwitchWrite,
            handleChangeSwitchRead,
            handleChangeProfile,
            profileList,
            infoErrors,serverValidation,handleCloseInfo,
            reduxPermissionVerfied,
            handleCloseErros,loading} = props

    const styledUpdatePermission = StyledUpdatePermission()
    
    const getDisabled = val => {
        if (!val) return { disabled: true };
        return {};
    }

    return( 
           <Box className={styledUpdatePermission.MainBox}>
                <Paper elevation={3} className={styledUpdatePermission.PaperMain}>
                    <HeaderForm icon={<SecurityIcon fontSize='large' className={styledUpdatePermission.color} />} titulo={'Permisses'}/>
                        <Box display='flex' justifyContent='center' style={{marginBottom:'5vh'}}>
                            <Box>
                                <form noValidate autoComplete="off"> 
                                    <InputLabel htmlFor="profile-native-simple">Perfil</InputLabel>
                                    <Select native
                                        {...getDisabled(reduxPermissionVerfied)}
                                        value={permission.profile_id}
                                        onChange={e=>handleChangeProfile(e)}
                                        inputProps={{
                                            name: 'profile_id',
                                            id: 'profile-native-simple',
                                        }}
                                        >
                                        <option value="-1">Opes...</option>
                                        {reduxPermissionVerfied && 
                                            profileList.map((e,i) =>  { 
                                                return (<option key={i} value={e.id}>{e.name}</option>)
                                        })}   
                                    </Select>
                                </form>
                            </Box>
                        </Box>
                        
                        
                        <Box className={styledUpdatePermission.SwitchContainerTitle}>
                                <Box className={styledUpdatePermission.SwitchBox} justifyContent='center' style={{width:'60%'}}> 
                                    <Box className={styledUpdatePermission.SwitchTitle}>Funcionalidade</Box>            
                                </Box>
                                <Box className={styledUpdatePermission.SwitchBox} justifyContent='center'  style={{width:'20%'}}> 
                                    <Box className={styledUpdatePermission.SwitchTitle}>Visualizao</Box>            
                                </Box>
                                <Box className={styledUpdatePermission.SwitchBox}  justifyContent='center' style={{width:'20%'}}> 
                                    <Box className={styledUpdatePermission.SwitchTitle}>Edio</Box>            
                                </Box>
                                {/* <Box className={styledUpdatePermission.SwitchBox}  justifyContent='center' style={{width:'20%'}}> 
                                    <Box className={styledUpdatePermission.SwitchTitle}>Excluso</Box>            
                                </Box> */}
                        </Box> 
                        
                        {permission.map(function (e) {
                            return (              
                            <Box  key={e.id} display='flex' flexDirection='column'>
                                <Box className={styledUpdatePermission.SwitchContainerValue} >
                                    <Box className={styledUpdatePermission.SwitchBox} justifyContent='left'  style={{width:'60%'}}> 
                                        <Box className={styledUpdatePermission.SwitchValue} >{e.description}</Box>            
                                    </Box>
                                    <Box className={styledUpdatePermission.SwitchBox}  justifyContent='center'  style={{width:'20%'}}> 
                                        <Box className={styledUpdatePermission.SwitchValue}><Switch id={e.id.toString()} name={e.route} checked={Boolean(e.read)} onChange={e=> handleChangeSwitchRead(e)}/></Box>            
                                    </Box>
                                    <Box className={styledUpdatePermission.SwitchBox}   justifyContent='center' style={{width:'20%'}}> 
                                        <Box className={styledUpdatePermission.SwitchValue}><Switch id={e.id.toString()} name={e.route} checked={Boolean(e.write)} onChange={e=> handleChangeSwitchWrite(e)}/></Box>            
                                    </Box>
                                    {/* <Box className={styledUpdatePermission.SwitchBox}   justifyContent='center' style={{width:'20%'}}> 
                                        <Box className={styledUpdatePermission.SwitchValue}><Switch id={e.id.toString()} name={e.route} checked={Boolean(e.delete)} onChange={e=> handleChangeSwitchDelete(e)}/></Box>            
                                    </Box> */}
                                </Box>
                            </Box>        
                            )
                        })}
  
                        <DisplayErrosMessage infoErrors={infoErrors} handleCloseErros={handleCloseErros}  />
                        <DisplayInfoMessage serverValidation={serverValidation} handleCloseInfo={handleCloseInfo}/>
                        {loading && 
                            <LinearProgress />      
                        }
                </Paper>
        </Box>
    )

}
export default ViewUpdatePermission
