import React from 'react'
import {TextField} from '@material-ui/core'
import InputMask from 'react-input-mask'
import StyledUpdateDriver from './StyledUpdateDriver'
import PersonIcon from '@material-ui/icons/Person'
import BodyForm from '../../../../ComponentsLibrary/Form/BodyForm/BodyForm'


const ViewUpdateDriver =  (props)  => {

    const {handleSubmit,driver,
            handleChange,handleBlur,
            inputvalidation,infoErrors,
            serverValidation,handleCloseInfo,
            handleCloseErros,loading} = props

    const styledUpdateDriver = StyledUpdateDriver()

return( <BodyForm infoErrors={infoErrors} 
        serverValidation={serverValidation} 
        handleCloseInfo={handleCloseInfo}
        handleCloseErros={handleCloseErros}
        handleSubmit={handleSubmit}
        loading={loading}
        icone={<PersonIcon fontSize='large' className={styledUpdateDriver.color} />}
        title='Cadastro de Motoristas'
        urlBackCommand='/allDriver'
        showSaveButton={true} showBackButton={true}
        paperMainWidth='50%'>
            <InputMask 
                mask='9999'
                maskChar=''
                value={driver.code} 
                onChange={event=> handleChange(event,true)}
                onBlur={event=> handleBlur(event)}>
                {inputProps =>
                    <TextField
                    {...inputProps}
                    id='code' 
                    name='code'
                    label='Cdigo'
                    autoComplete='off'
                    inputProps={{style:{textAlign:'left'}}}
                    className={styledUpdateDriver.smallTextField}
                    helperText={inputvalidation.keys.code !== '' ? inputvalidation.keys.code  : ''}
                    error={inputvalidation.keys.code !== '' ? true : false}
                    />}
            </InputMask>
            <InputMask 
                mask='(99)999999999'
                maskChar=''
                value={driver.phoneNumber} 
                onChange={event=> handleChange(event,true)}
                onBlur={event=> handleBlur(event)}>
                {inputProps =>
                    <TextField
                    {...inputProps}
                    id='phoneNumber' 
                    name='phoneNumber'
                    label='(DDD) + Celular'
                    autoComplete='off'
                    inputProps={{style:{textAlign:'left'}}}
                    className={styledUpdateDriver.mediumTextField}
                    helperText={inputvalidation.keys.phoneNumber !== '' ? inputvalidation.keys.phoneNumber  : ''}
                    error={inputvalidation.keys.phoneNumber !== '' ? true : false}
                    />}
            </InputMask>
</BodyForm>
      
    )

}
export default ViewUpdateDriver
