function InputValidateUser(event,editNotPermited) {
    
    let ret={msg:"",isError:false}

    switch (event.target.name) {
        case 'name':
            if(event.target.value.length === 0) {
                ret.msg = 'Nome  obrigatrio.'
                ret.isError=true
            }
            break
        case 'email':
            if (!editNotPermited) { 
                if(event.target.value.length === 0) {
                    ret.msg = 'E-mail  obrigatrio.'
                    ret.isError=true
                }
            } 
            break
        case 'logonUser':
            if (!editNotPermited) { 
                if(event.target.value.length === 0) {
                    ret.msg = 'Usurio  obrigatrio.'
                    ret.isError=true
                    }
                break
            } 
            break
        case 'password':
            if(event.target.value.length === 0) {
                ret.msg = 'Senha invlida.'
                ret.isError=true
            }
            break
        default:
            ret.msg=""
            ret.isError=false
        }
        return ret
    }
   

    export default InputValidateUser