 import React,{useState} from 'react'
import {Link} from "react-router-dom"
import List from '@material-ui/core/List'
import ListItem from '@material-ui/core/ListItem'
import AtIcon from '@material-ui/icons/LocalShipping'
import DesktopMacIcon from '@material-ui/icons/DesktopMac'
import ListItemText from '@material-ui/core/ListItemText'
import styledLinks from './StyledLinks'
import { useSelector } from 'react-redux'
import verifyPermissions from '../../../Assets/js/verifyPermissions'
import Typography from '@material-ui/core/Typography'
import ExpandLess from '@material-ui/icons/ExpandLess'
import ExpandMore from '@material-ui/icons/ExpandMore'
import Collapse from '@material-ui/core/Collapse'

const OverFillLinks = (props) => {
    
    const classes = styledLinks()
    
    const permissions = useSelector(state=> state.permission)
    const userAuthenticated = useSelector(state=> state.authenticated)

    const [openOverfill,setOpenOverfillLinks] = useState(false)
    const handleClick = () =>  setOpenOverfillLinks(!openOverfill)
      

    let permission=false
   

    return(
    <div className={classes.drawerWidth} >
        <List component="nav">
            <ListItem button={true} onClick={handleClick} className={classes.ListItem}>
            <ListItemText><Typography color="primary">Monitoramento de OverFill</Typography></ListItemText>
            {openOverfill ? <ExpandLess /> : <ExpandMore />}
            </ListItem>
            <Collapse in={openOverfill} timeout="auto" unmountOnExit>
                {permission = verifyPermissions('Truck','read',userAuthenticated,permissions)}
                <Link to={permission ? "/allTruck" : "#"} onClick={event=> props.toggleDrawer(verifyPermissions('Driver','read',userAuthenticated,permissions) ? false : true,event)} className={classes.linkDecoration}  >
                        <ListItem button className={classes.nested}>
                        <AtIcon className={classes.color} /><ListItemText  className={permission ? classes.menuText : classes.colorDisable} primary='Cadastro de AT' />
                    </ListItem>
                </Link>
                {permission = verifyPermissions('MonitorOverfill','read',userAuthenticated,permissions)}
                <Link to={permission ? "/allBay" : "#"} onClick={event=> props.toggleDrawer(verifyPermissions('MonitorOverfill','read',userAuthenticated,permissions) ? false : true,event)} className={classes.linkDecoration}  >
                        <ListItem button className={classes.nested}>
                        <DesktopMacIcon className={classes.color} /><ListItemText  className={permission ? classes.menuText : classes.colorDisable} primary='Condies dos Overfills' />
                    </ListItem>
                </Link>
            </Collapse>
        </List>
    </div>
    )}

export default OverFillLinks
