import React from 'react'
import {useState,useEffect} from 'react'
import ViewUpdatePermission from './ViewUpdatePermission'
import {getApi} from '../../../../Assets/js/apiProxy'
import { useSelector } from 'react-redux'
import verifyPermissions from '../../../../Assets/js/verifyPermissions'
import {infoError} from '../../../../Assets/js/infoError'

function UpdatePermission(props) {

   const api = getApi('api_features')

   //#region  - Verifica se ususrio tem permisso para controla a edio das permisses.
      const reduxPermission = useSelector(state=> state.permission)
      const userAuthenticated = useSelector(state=> state.authenticated)
      const reduxPermissionVerfied = verifyPermissions('Permission','write',userAuthenticated,reduxPermission)
   //#endregion

   const [permission,getPermission] = useState([])
   const [profile,setProfile] = useState(-1)
   const [checked,setChecked] = useState(false)
   const [infoErrors,setInfoErrors] = useState({msg:''})
   const [serverValidation,setServerValidation] = useState({msg:''})
   const [loading, setLoading] = useState(false)
   const [profileList,getAllProfiles] = useState([])
  
   useEffect(()=> {
      if (reduxPermissionVerfied) { 
         api.post(`/getPermissionByProfile`,{profile_id:profile})
         .then(function (response) {
            if (response.data.registers !== undefined) { 
            let data = response.data.registers.filter((e)=> e.internal === 0)
            getPermission(data)}
            })
         .catch(function (error) {  
               let infoErrorsState = infoError(error)
               console.log(infoErrorsState.msg)
               setInfoErrors(infoErrorsState)
            })
      }

     },[checked,profile,reduxPermissionVerfied,api])

     useEffect(()=> {
         api.get(`/allProfile`)
         .then(function (response) {
         
         if (response.data.registers !== undefined) { 
            let profiles = response.data.registers
            getAllProfiles(profiles)}
         })
         .catch(function (error) {  
            let infoErrorsState = infoError(error)
            console.log(infoErrorsState.msg)
            setInfoErrors(infoErrorsState)
         })

         return ()=> { setLoading(false)}

     },[api])

   function handleCloseErros() { 
      setInfoErrors({msg:''})
   }

   function handleCloseInfo() { 
      setServerValidation({msg:''})
   }

   function handleChangeProfile(e) {
      setProfile(e.target.value)
   }

   function handleChangeSwitchRead(e) { 
      setLoading(true)
      
      let obj =permission.find((w)=> {
          return (w.id === parseInt(e.target.id))
      })

      if (obj !== undefined) {
         
         obj.read = e.target.checked
         
         //Caso o usurio desligue a permisso de leitura  porm o mesmo possui permisso de escrita,
         //devemos desliga-la automticamente.
         if (Boolean(obj.write) === true && Boolean(obj.read) === false) { 
             obj.write = false
             obj.delete = false
         }
        
         api.put('/updatePermission',{id:obj.id,profile_id:obj.profile_id,route_id:obj.route_id,read:obj.read,write:obj.write,delete:obj.delete})
         .then(function (response) {
               setChecked(!checked)
         })
         .catch(function (error) {  
            let infoErrorsState = infoError(error)
            console.log(infoErrorsState.msg)
            setInfoErrors(infoErrorsState)
         })
         .finally(()=> {
            setLoading(false)
         })
         
      } else {
         setInfoErrors({msg: 'Falha ao tentar alterar as permisses. Entre em contato com o Administrador do sistema.'})  
         setLoading(false)
      }
   }

   function handleChangeSwitchWrite(e) { 
      setLoading(true)
      
      let obj =permission.find((w)=> {
          return (w.id === parseInt(e.target.id))
      })

      if (obj !== undefined) {
         
         obj.write = e.target.checked

         //Caso o usurio ligue o write, automticamente o mesmo passa a ter permisso de leitura.
         if (Boolean(obj.write === true)) {obj.read = true}
            
            api.put('/updatePermission',{id:obj.id,profile_id:obj.profile_id,route_id:obj.route_id,read:obj.read,write:obj.write,delete:obj.delete})
            .then(function (response) {
                  setChecked(!checked)
            })
            .catch(function (error) {  
               let infoErrorsState = infoError(error)
               console.log(infoErrorsState.msg)
               setInfoErrors(infoErrorsState)
            })
            .finally(()=> {
               setLoading(false)
            })

      } else {
         setInfoErrors({msg: 'Falha ao tentar alterar as permisses. Entre em contato com o Administrador do sistema.'})  
         setLoading(false)
      }
   }

   // function handleChangeSwitchDelete(e) { 
   //    setLoading(true)
      
   //    let obj =permission.find((w)=> {
   //        return (w.id === parseInt(e.target.id))
   //    })

   //    if (obj !== undefined) {
         
   //       obj.delete = e.target.checked

   //       //Caso o usurio ligue o delete, automticamente o mesmo passa a ter permisso de leitura.
   //       if (Boolean(obj.delete === true)) {obj.read = true}
            
   //          api.put('/updatePermission',{id:obj.id,profile_id:obj.profile_id,route_id:obj.route_id,read:obj.read,write:obj.write,delete:obj.delete})
   //          .then(function (response) {
   //                setChecked(!checked)
   //          })
   //          .catch(function (error) {  
   //             let infoErrorsState = infoError(error)
   //             console.log(infoErrorsState.msg)
   //             setInfoErrors(infoErrorsState)
   //          })
   //          .finally(()=> {
   //             setLoading(false)
   //          })

   //    } else {
   //       setInfoErrors({msg: 'Falha ao tentar alterar as permisses. Entre em contato com o Administrador do sistema.'})  
   //       setLoading(false)
   //    }
   // }

   return (<ViewUpdatePermission permission={permission}
         handleChangeSwitchRead={handleChangeSwitchRead}
         handleChangeSwitchWrite={handleChangeSwitchWrite}
         // handleChangeSwitchDelete={handleChangeSwitchDelete}
         handleChangeProfile={handleChangeProfile}
         profileList={profileList}
         handleCloseErros={handleCloseErros}
         handleCloseInfo={handleCloseInfo}
         infoErrors={infoErrors}
         serverValidation={serverValidation}
         reduxPermissionVerfied={reduxPermissionVerfied}
         loading = {loading}
           />)
   
}

export default UpdatePermission