import { makeStyles } from '@material-ui/core/styles'
import {grey} from '@material-ui/core/colors'

const StyledReportViewer = makeStyles((theme) => ({
   rptViewerHeader: {
      margin: theme.spacing(1)
   },
   headerTextCmd: {
      margin: theme.spacing(1),
      marginTop: theme.spacing(1.5),
      color: grey[700],
      fontSize: '1rem',
      fontWeight: 400,
      [theme.breakpoints.down('md')]: {
         fontSize: '0.8rem',
      },
   },
   headerPageControlCmd: {
      marginTop: theme.spacing(1)
   },
   headerPageControl: {
      marginTop: theme.spacing(1),
      marginLeft: theme.spacing(1),
   },
   headerPageInput: {
      marginTop: theme.spacing(1),
      textAlign: 'center',
      fontSize: '1rem',
      fontWeight: 400,
      [theme.breakpoints.down('md')]: {
         fontSize: '0.8rem',
      },
   },
   reportContent: { 
      '@media print': { backgroundColor: 'white' },
   },
   reportContentNoPaging: {
       display:'none',
      '@media print': { display: 'block' },
   }
}))

export default StyledReportViewer