function InputValidateProfile(event) {
    
    let ret={msg:"",isError:false}

    switch (event.target.name) {
        case 'name':
            if(event.target.value.length === 0) {
                ret.msg = 'Nome   obrigatrio.'
                ret.isError=true
            }
            break
        case 'description':
            if(event.target.value.length === 0) {
                ret.msg = 'Descrio  obrigatrio.'
                ret.isError=true
              }
            break
        case 'profileNumber':
            if(!event.target.value && !event.target.value > 0) {
                ret.msg = 'Nmero do Perfil  obrigatrio.'
                ret.isError=true
                }
            break
        case 'accessLevel':
            if(!event.target.value || !event.target.value > 0) {
                ret.msg = 'Hierarquia de acesso  obrigatrio.'
                ret.isError=true
                }
            break
        default:
            ret.msg=""
            ret.isError=false
        }
        return ret
    }
   

    export default InputValidateProfile