import React from 'react'
import {useState,useEffect} from 'react'
import ViewUpdateAvaliableKey from './ViewUpdateAvaliableKey'
import {getApi} from '../../../../Assets/js/apiProxy'
import {infoError} from '../../../../Assets/js/infoError'

function UpdateAvaliableKey(props) {
   
   const api = getApi('api_features')

   const [keyHolderId,setKeyHolderId] = useState(-1)
   const [keyHolderList,getAllKeyHolder] = useState([])
   const [avaliableKeyList,setAvaliableKeyList] = useState([])
   const [profileList,getProfileList] = useState([])
   const [infoErrors,setInfoErrors] = useState({msg:''})
   const [serverValidation,setServerValidation] = useState({msg:''})
   const [loading, setLoading] = useState(false)

    const [snackInfo,setSnackInfo] = useState('')
    const [visibility, setVisibility] = useState(false)


   useEffect(()=> {
        getAvaliableKeyByKeyHolder(keyHolderId,api)
     },[keyHolderId,api])

     useEffect(()=> {
         api.get(`/allKeyHolder`)
         .then(function (response) {
         
         if (response.data.registers !== undefined) { 
            let keyHolders = response.data.registers
            getAllKeyHolder(keyHolders)}
         })
         .catch(function (error) {  
            let infoErrorsState = infoError(error)
            console.log(infoErrorsState.msg)
            setInfoErrors(infoErrorsState)
         })

         api.get(`/allProfile`)
         .then(function (response) {
         
         if (response.data.registers !== undefined) { 
            let profiles = response.data.registers
            getProfileList(profiles)}
         })
         .catch(function (error) {  
            let infoErrorsState = infoError(error)
            console.log(infoErrorsState.msg)
            setInfoErrors(infoErrorsState)
         })

         return ()=> { setLoading(false)}

     },[api])

   const  handleCloseSnack = ()=> {
      setVisibility(false)
   }

   function getAvaliableKeyByKeyHolder(keyHolderId,api) {
      api.post(`/getAvaliableKeyByKeyHolder`,{id:keyHolderId})
      .then(function (response) {
         if (response.data.registers !== undefined) { 
         let data = response.data.registers
         setAvaliableKeyList(data)}
         })
      .catch(function (error) {  
            let infoErrorsState = infoError(error)
            console.log(infoErrorsState.msg)
            setInfoErrors(infoErrorsState)
         })
   }

   function handleChange(e,avaliableKeyId) { 
      let list = avaliableKeyList.map((myKey) => {
                  if (myKey.id === avaliableKeyId) {
                     myKey = {...myKey,[e.target.name]:e.target.value}
                  } 
                  return(myKey)
               })
      setAvaliableKeyList(list)
   }
     
   function handleCloseErros() { 
      setInfoErrors({msg:''})
   }

   function handleCloseInfo() { 
      setServerValidation({msg:''})
   }

   function handleChangeKeyHolder(e) {
      setKeyHolderId(e.target.value)
   }

   function handleSave(avaliableKeyId) { 
      let avaliableKey =  avaliableKeyList.find(e=> e.id === avaliableKeyId)
      api.put('/updateAvaliableKey',avaliableKey)
         .then(function (response) {
            if (!response.data.bsApproved) {
               let serverValidationState = {msg:response.data.msg}
               setServerValidation(serverValidationState)
            } else {
               setSnackInfo(response.data.msg)
               setVisibility(true)
            }
         })
         .catch(function (error) {  
            let infoErrorsState = infoError(error)
            console.log(infoErrorsState.msg)
            setInfoErrors(infoErrorsState)
         })
         .finally(()=> {
            setLoading(false)
         })
   }

   return (<ViewUpdateAvaliableKey 
         keyHolderId={keyHolderId}
         avaliableKeyList={avaliableKeyList}
         handleChangeKeyHolder={handleChangeKeyHolder}
         keyHolderList={keyHolderList}
         handleCloseErros={handleCloseErros}
         handleCloseInfo={handleCloseInfo}
         infoErrors={infoErrors}
         serverValidation={serverValidation}
         loading = {loading}
         handleSave={handleSave}
         handleChange={handleChange}
         profileList={profileList}
         handleCloseSnack={handleCloseSnack}
         visibility={visibility}
         snackInfo={snackInfo}
   />)
   
}

export default UpdateAvaliableKey