import { makeStyles } from '@material-ui/core/styles'
import {grey} from '@material-ui/core/colors'

const StyledReportTableContentRows = makeStyles((theme) => ({
      root: {
        width: '100%'
       },
      table: {
        minWidth: 750,
      },
      container: {
        '&::-webkit-scrollbar': {
          width: '0.4em'
        },
        '&::-webkit-scrollbar-track': {
          '-webkit-box-shadow': 'inset 0 0 6px rgba(0,0,0,0.00)'
        },
        '&::-webkit-scrollbar-thumb': {
          backgroundColor: '#782878'
        }
      },
      rowColor: { 
        color: grey[600],
        border:'0'
      },
      footer: {
        fontWeight: 'bold'
      }
}))


export default StyledReportTableContentRows