import { makeStyles } from '@material-ui/core/styles'

const StyledTableRows = makeStyles((theme) => ({
  
      root: {
        width: '100%',
      },
      paper: {
        width: '100%',
        marginBottom: theme.spacing(2),
      },
      table: {
        minWidth: 750,
      },
      
}))


export default StyledTableRows