function InputValidateRegister(event) {
    
    let ret={msg:"",isError:false}

    switch (event.target.name) {
        case 'name':
            if(event.target.value.length === 0) {
                ret.msg = 'Nome  obrigatrio'
                ret.isError=true
            }
            break
        case 'email':
            if(event.target.value.length === 0) {
                ret.msg = 'e-mail  obrigatrio'
                ret.isError=true
              }
            break
        case 'phone':
            if(event.target.value.length === 0) {
                ret.msg = 'Telefone  obrigatrio'
                ret.isError=true
            } else if (event.target.value.length < 14) {
                ret.msg = 'Telefone invlido'
                ret.isError=true
            }
            break
        case 'cpf':
            if(event.target.value.length === 0) {
                ret.msg = 'Cpf  obrigatrio'
                ret.isError=true
            }else if  (event.target.value.length < 14) {
                ret.msg = 'Cpf invlido'
                ret.isError=true
            }
            break
        case 'password':
                if(event.target.value.length === 0) {
                    ret.msg = 'Senha  obrigatrio'
                    ret.isError=true
                }else if  (event.target.value.length < 8) {
                    ret.msg = 'Senha invlido'
                    ret.isError=true
                }
                break
        default:
            ret.msg=""
            ret.isError=false
        }
        return ret
    }
   

    export default InputValidateRegister