import React from 'react'
import {useState,useEffect} from 'react'
import ViewReadMonitorOverfill from './ViewReadMonitorOverfill'
import {getApi} from '../../../../Assets/js/apiProxy'
import {infoError} from '../../../../Assets/js/infoError'

function ReadMonitorOverfill(props) {
    
    const [loading,setLoading] = useState(false)
    const [monitorOverfill,allMonitorOverfill] = useState([])
    const [infoErrors,setInfoErrors] = useState({msg:''})
    const [scanTimeMonitoringOverfill,setScanTimeMonitoringOverfill] = useState(3000)
    
    const [open, setOpen] = useState(false)
    const [loadingDialog,setLoadingDialog] = useState(false)
    const [compartmentNumber,setCompartmentNumber] = useState(null)
    const [selectedTruck,setSelectedTruck] = useState(null)
    const [id,setId] = useState(null)
    const [licenseNumber,setLicenseNumber] = useState(null)
    const [requiredFieldValueOnUpdate,setRequiredFieldValueOnUpdates] = useState(null)
    
    const api_overfill = getApi('api_overfill')
    const api = getApi('api_features')

    useEffect(()=> { 
       
      const scanTimer = setInterval(()=> {
        getAllBayData(api_overfill)
      },scanTimeMonitoringOverfill)

      //Este ruturn no hook  o equivalente ao componentWillUnmounted
      return ()=> clearInterval(scanTimer)
    
    },[scanTimeMonitoringOverfill,api_overfill,open])
   
    useEffect(()=> {
        setLoading(true)
        
        api.post(`/getConfigParameter`,{parameter:'scanTimeMonitoringOverfill'})
        .then(function (response) {
          if (response.data.registers  && response.data.registers.length > 0) { 
            setScanTimeMonitoringOverfill(parseInt(response.data.registers[0].parameterValue))
          }

          getAllBayData(api_overfill)

        }) 
        .catch(function (error) {  
          let infoErrorsState = infoError(error)
          console.log(infoErrorsState.msg)
          setInfoErrors(infoErrorsState)
        })

      },[api,api_overfill])
         
      function getAllBayData(api_overfill) { 
        api_overfill.get('/allBay')
        .then(function (response) {
              allMonitorOverfill(response.data.registers)
              setLoading(false)
        })
        .catch(function (error) {
          let infoErrorsState = infoError(error)
          console.log(infoErrorsState.msg)
          setInfoErrors(infoErrorsState)
           setLoading(false)
        })
      }


     function handleClose() { 
        setInfoErrors({msg:''})
     }

     function handleChange(e) { 
        setCompartmentNumber(e.target.value)
     }

     function handleDialogClose() { 
        setOpen(false)
        setLoadingDialog(false)
        setCompartmentNumber(null)
        setSelectedTruck(null)
        setId(null)
        setLicenseNumber(null)
        setRequiredFieldValueOnUpdates(null)
     }

     function handleClick(e,id,selectedTruck,licenseNumber,requiredFieldValue) { 
          setOpen(true)
          setSelectedTruck(selectedTruck)
          setId(id)
          setLicenseNumber(licenseNumber)
          setRequiredFieldValueOnUpdates(requiredFieldValue)
     }

     function handleSaveCompartmentNumber(e) { 
        setLoadingDialog(true)

        let objToUpdate = {id: id, at: licenseNumber }
        if (selectedTruck ===  1) { 
            objToUpdate.compartFirstTrain = compartmentNumber
            objToUpdate.compartSecondTrain = requiredFieldValueOnUpdate
        } else {
            objToUpdate.compartSecondTrain = compartmentNumber
            objToUpdate.compartFirstTrain = requiredFieldValueOnUpdate
        }

        api_overfill.put(`/updateTruck`,objToUpdate)    
        .then(function (response) {
          if (!response.data.bsApproved) {
          let infoErrorsState = {msg:response.data.msg}
          setInfoErrors(infoErrorsState)
          handleDialogClose()
         } else {
            handleDialogClose()
            getAllBayData(api_overfill)
         }
        }) 
        .catch(function (error) {  
          let infoErrorsState = infoError(error)
          console.log(infoErrorsState.msg)
          setInfoErrors(infoErrorsState)
          handleDialogClose()
        })
     }
      
    return (<ViewReadMonitorOverfill monitorOverfill={monitorOverfill} 
             loading={loading} 
             infoErrors={infoErrors} 
             handleChange={handleChange}
             compartmentNumber={compartmentNumber}
             selectedTruck={selectedTruck}
             open={open}
             handleSaveCompartmentNumber={handleSaveCompartmentNumber}
             loadingDialog={loadingDialog}
             handleClose={handleClose}
             handleDialogClose={handleDialogClose} 
             handleClick={handleClick}
             licenseNumber={licenseNumber}
             />)
}

export default ReadMonitorOverfill