import React from 'react'
import { Box, Button} from '@material-ui/core'
import SaveOutlinedIcon from '@material-ui/icons/SaveOutlined'
import ExitToAppOutlinedIcon from '@material-ui/icons/ExitToAppOutlined' 
import PropTypes from 'prop-types'
import { useHistory } from  'react-router-dom'    
const ViewFooterForm = (props) => {
    
    const {handleSubmit,urlBackCommand,showSaveButton,showBackButton} = props
    let history = useHistory()

    return(
        <Box style={{display:'flex',justifyContent:"flex-end"}} arial-label='form-footer'>
            <Box style={{display:'flex'}}>
                {showSaveButton  &&
                    <Box m={1}>
                        <Button id='save' type='submit' size='small' variant='contained'
                                color='primary' onClick={event=> handleSubmit(event)}
                                startIcon={<SaveOutlinedIcon/>}>
                            Salvar
                        </Button>
                    </Box>}
                {showBackButton  &&
                <Box m={1}>
                    <Button id='back' size='small' variant='contained'  
                        color='default' endIcon={<ExitToAppOutlinedIcon/>} 
                        arial-label='footer-commands'
                        onClick={()=>history.push(urlBackCommand)}>
                        Voltar
                    </Button>
                </Box>}
            </Box>
        </Box>

    )}

    ViewFooterForm.propTypes = {
        handleSubmit: PropTypes.func.isRequired
    }

export default ViewFooterForm
