import React from 'react'
import {TextField} from '@material-ui/core'
import StyledUpdateKeyHolder from './StyledUpdateKeyHolder'
import InputMask from 'react-input-mask'
import ScreenLockLandscapeIcon from '@material-ui/icons/ScreenLockLandscape'
import BodyForm from '../../../../ComponentsLibrary/Form/BodyForm/BodyForm'


const ViewUpdateKeyHolder =  (props)  => {

    const {handleSubmit,KeyHolder,
            handleChange,handleBlur,
            inputvalidation,infoErrors,
            serverValidation,handleCloseInfo,
            handleCloseErros,loading} = props

    const styledUpdateKeyHolder = StyledUpdateKeyHolder()

return( <BodyForm infoErrors={infoErrors} 
        serverValidation={serverValidation} 
        handleCloseInfo={handleCloseInfo}
        handleCloseErros={handleCloseErros}
        handleSubmit={handleSubmit}
        loading={loading}
        icone={<ScreenLockLandscapeIcon fontSize='large' className={styledUpdateKeyHolder.color} />}
        title='Cadastro de Claviculrio'
        urlBackCommand='/allKeyHolder'
        showSaveButton={true} showBackButton={true}
        paperMainWidth='40%'>
                <InputMask 
                    mask='999'
                    maskChar=''
                    value={KeyHolder.qtdeKeys} 
                    onChange={event=> handleChange(event,true)}
                    onBlur={event=> handleBlur(event)}>
                    {inputProps =>
                        <TextField
                        {...inputProps}
                        id='qtdeKeys' 
                        name='qtdeKeys'
                        label='Qtde. de Chaves'
                        autoComplete='off'
                        className={styledUpdateKeyHolder.smallTextField}
                        helperText={inputvalidation.keys.qtdeKeys !== '' ? inputvalidation.keys.qtdeKeys  : ''}
                        error={inputvalidation.keys.qtdeKeys !== '' ? true : false}
                        />}
                </InputMask>
                <InputMask 
                    mask='999'
                    maskChar=''
                    value={KeyHolder.number} 
                    onChange={event=> handleChange(event,true)}
                    onBlur={event=> handleBlur(event)}>
                    {inputProps =>
                        <TextField
                        {...inputProps}
                        id='number' 
                        name='number'
                        label='Nmero'
                        autoComplete='off'
                        className={styledUpdateKeyHolder.smallTextField}
                        helperText={inputvalidation.keys.number !== '' ? inputvalidation.keys.number  : ''}
                        error={inputvalidation.keys.number !== '' ? true : false}
                        />}
                </InputMask>
                <TextField 
                    id='identification' 
                    name='identification'
                    label='Identificao'
                    autoComplete='off'
                    className={styledUpdateKeyHolder.largeTextField}
                    value={KeyHolder.identification} 
                    onChange={event=> handleChange(event,false)}
                    onBlur={event=> handleBlur(event)}
                    helperText={inputvalidation.keys.identification !== '' ? inputvalidation.keys.identification  : ''}
                    error={inputvalidation.keys.identification !== '' ? true : false}
                    inputProps={{ maxLength: 50}}
                />
</BodyForm>
      
    )

}
export default ViewUpdateKeyHolder
