import React from 'react'
import {useState,useEffect} from 'react'
import ViewUpdateUser from './ViewUpdateUser'
import {getApi} from '../../../../Assets/js/apiProxy'
import InputValidateUser from './InputValidateUser'
import {useSelector} from 'react-redux'
import {infoError} from '../../../../Assets/js/infoError'

function UpdateUser(props) {
   
   const api = getApi('api_features')

   const { location: { state }} = props
   let prop_id = state === undefined ? 0 : state.id
   const userAuthenticated = useSelector(state => state.authenticated)

   let userState = {id:prop_id,name:'',
                    email:'',logonUser: '',
                    password:'',                    
                    lastLogin:null,
                    lastLogout:null,
                    status:0,
                    activated:1,
                    profile_id:0,
                    restartPassword:1,
                    rfid:''}

   const [user,editableUser] = useState(userState)
   const [profileList,getAllProfiles] = useState([])
   const [infoErrors,setInfoErrors] = useState({msg:''})
   const [profileNumber,setProfileNumber] = useState(-1)
   const [serverValidation,setServerValidation] = useState({msg:''})
   const [loading, setLoading] = useState(false)
   const [loadingRFID, setLoadingRFID] = useState(false)
   const [open, setOpen] = useState(false)
   const [RFID,setRFID] = useState(undefined)
   const [editNotPermited,setEditNotPermited] = useState(false)
      
   let validationState = {isError:false,
                          keys:{id:prop_id,
                                name:'',
                                email:'',
                                password:'',
                                logonUser:'',
                                profile_id: ''}}
   const [inputvalidation,setInputValidation] = useState(validationState)
  
   useEffect(()=> {
        if (userState.id !== 0) {
         api.get(`/editUser/${userState.id}`)
         .then(function (response) {
         
         if (response.data.registers !== undefined) { 
            let user_data = response.data.registers.shift()
            editableUser(user_data)


            //No permitir alterao dos dados de cadastro de usurio se este se referir ao prprio usurio logado.
            if (user_data.email === userAuthenticated.email || user_data.logonUser ===  userAuthenticated.logonUser) { 
               setEditNotPermited(true)
            } 

            api.get(`/editProfile/${user_data.profile_id}`)
            .then(function (response) {
                  if (response.data.registers !== undefined) { 
                        let profile = response.data.registers.shift()
                        setProfileNumber(profile.profileNumber)
                        
                  }
            })
            .catch(function (error) {  
               let infoErrorsState = infoError(error)
               console.log(infoErrorsState.msg)
               setInfoErrors(infoErrorsState)
            })

         }
         document.getElementById('name').focus()
         })
         .catch(function (error) {  
            let infoErrorsState = infoError(error)
            console.log(infoErrorsState.msg)
            setInfoErrors(infoErrorsState)
         })
     } 
      
      api.get(`/allProfile`)
      .then(function (response) {
      
      if (response.data.registers !== undefined) { 
         let profiles = response.data.registers
         getAllProfiles(profiles)}
      })
      .catch(function (error) {  
         let infoErrorsState = infoError(error)
         console.log(infoErrorsState.msg)
         setInfoErrors(infoErrorsState)
      })

      //Representa o ComponentDidUnmount quando utilizamos o hoook
      return ()=> { 
            setLoading(false)

      }

   },[userState.id,userAuthenticated,api])

   function handleCloseErros() { 
      setInfoErrors({msg:''})
   }

   function handleCloseInfo() { 
      setServerValidation({msg:''})
   }

   function handleChange(event,hasMask) {
      if (hasMask) {
            event.target.value = event.target.value.replace(/[, ]+/g,'').replace(/\(|\)/g, "") 
      }
      
      editableUser({...user,[event.target.name]:event.target.value})
   }

   function handleChangeActivated(event) {
      editableUser({...user,[event.target.name]:event.target.checked})
   }

   function handleChangeResetPassword(event) {
      editableUser({...user,[event.target.name]:event.target.checked})
   }
 

   function handleBlur(event) {
      let ret = InputValidateUser(event,editNotPermited)
      
      //Spread Operator - Converte o objeto inputValidation em Array, e atualiza o item do array 
      //identificado pelo valor originado por event.target.nameo com o novo valor ret.msg.
      const keys = {keys:{...inputvalidation.keys,[event.target.name]:ret.msg}}
      
      //Rest Operator - Pega o Objeto key e expande em propriedades passando como argumentos para o 
      //setInputValidation.
      setInputValidation({isError:ret.isError,...keys})
      
   }

   const clearRFID  = () => {
      api.put(`/clearRFID`)
      .catch(function (error) {  
         let infoErrorsState = infoError(error)
         console.log(infoErrorsState.msg)
         setInfoErrors(infoErrorsState)
         setLoadingRFID(false)
      })
   }

   const checkForRFID = ()=> {
      
      api.get(`/getRFID`)
      .then(function (response) {
      
         if (response.data.registers !== undefined) { 
            
            let RFID = response.data.registers.rfid
            if (RFID) { 
               setRFID(RFID)

               //Associa o RFID ao Usurio.
               api.put(`/AssociateUserAndRFID/${user.id}`)
               .then(function (response) {
                  if (!response.data.bsApproved) {
                     let infoErrorsState = infoError(response.data.msg)
                     console.log(infoErrorsState.msg)
                     setInfoErrors(infoErrorsState)
                     
                  } else {
                     
                     user.rfid = RFID
                     window.clearInterval(window.myInterval)
                     
                  }
                  setLoadingRFID(false)
               })
               .catch(function (error) {  
                  let infoErrorsState = infoError(error)
                  console.log(infoErrorsState.msg)
                  setInfoErrors(infoErrorsState)
                  setLoadingRFID(false)
               })
            }
         }
      })
      .catch(function (error) {  
         let infoErrorsState = infoError(error)
         console.log(infoErrorsState.msg)
         setInfoErrors(infoErrorsState)
         setLoadingRFID(false)
      })
   }

   const handleClickOpen = () => {
      clearRFID()
      setOpen(true)
      setLoadingRFID(true)
      checkForRFID()
      let interval_id = setInterval(checkForRFID,1000)
      window.myInterval = interval_id
    }
  
    const handleClose = () => {
      clearRFID()
      setOpen(false)
      setLoadingRFID(false)
      window.clearInterval(window.myInterval)

    }
     
  function handleSubmit(event) {
      setLoading(true)
     
      user.lastLogin = null
      user.lastLogout = null

      if (!inputvalidation.isError) {
         if (userState.id !== 0) {
            
            api.put('/updateUser',user)
            .then(function (response) {
               if (response.data.bsApproved) {
                  props.history.push('/allUser')
               } else {
                  let serverValidationState = {msg:response.data.msg}
                  setServerValidation(serverValidationState)
               }
            })
            .catch(function (error) {  
               let infoErrorsState = infoError(error)
               console.log(infoErrorsState.msg)
               setInfoErrors(infoErrorsState)
            })
            .finally(()=> {
               setLoading(false)
            })
         }else {
           api.post('/addUser',user)
               .then(function (response) {
                  if (response.data.bsApproved) {
                     props.history.push('/allUser')
                  } else {
                     let serverValidationState = {msg:response.data.msg}
                     setServerValidation(serverValidationState)
                  }
            })
            .catch(function (error) {  
               let infoErrorsState = infoError(error)
               console.log(infoErrorsState.msg)
               setInfoErrors(infoErrorsState)
            })
            .finally(()=> {
               setLoading(false)
            })
         }
      } else {
         setLoading(false)
      }
      event.preventDefault()
  }

   return (<ViewUpdateUser user={user}
          handleChange={handleChange}
          handleSubmit={handleSubmit}
          handleBlur={handleBlur}
          handleCloseErros={handleCloseErros}
          handleCloseInfo={handleCloseInfo}
          handleChangeActivated={handleChangeActivated}
          handleChangeResetPassword={handleChangeResetPassword}
          inputvalidation={inputvalidation}
          infoErrors={infoErrors}
          serverValidation={serverValidation}
          profileList={profileList}
          loading = {loading}
          profileNumber={profileNumber}
          open={open}
          handleClickOpen={handleClickOpen}
          handleClose={handleClose}
          RFID={RFID}
          loadingRFID={loadingRFID}
          editNotPermited={editNotPermited}
         />)
}

export default UpdateUser