import React from 'react'
import Routes from './Core/Routes/Routes'
import history from './Config/history'
import { BrowserRouter} from 'react-router-dom'
import { Provider } from 'react-redux'
import {PersistGate}  from 'redux-persist/integration/react'
import {mainStore,persistedStore} from './Core/Store/store'
import Menu from './Core/Menu/Menu'
import { ThemeProvider } from "@material-ui/styles"
import {CssBaseline,createMuiTheme} from "@material-ui/core"
import './Assets/css/app.css'

//Personalizando o Tema Padro utilizado no Material-UI
const theme = createMuiTheme({
    palette: {
      primary: {
        main: '#8a05be',
      }
    }
  })

function App(props) {

   return(
      <Provider store={mainStore}>
          <PersistGate persistor={persistedStore}>
                <ThemeProvider theme={theme}>
                    <CssBaseline />
                    {/* Quando o projeto for publicado em um servidor onde ele ficar em um subnvel,
                    exemplo. http://localhost/claviculario. o nome da pasta /claviculario dever 
                    ser definido no atributo  basename="/claviculario" do BrowserRoute e no package
                    .json dever ser definido a tag homepage:http://localhost/claviculario por exemplo. */}
                    <BrowserRouter history={history}>
                            <Menu/>
                            <Routes/>
                    </BrowserRouter>
                </ThemeProvider>
          </PersistGate>
      </Provider>
  )

}

export default App

