import React from 'react'
import {Box,TextField} from '@material-ui/core'
import StyledUpdateRoutePermission from './StyledUpdateRoutePermission'
import BodyForm from '../../../../ComponentsLibrary/Form/BodyForm/BodyForm'
import FeaturedPlayListIcon from '@material-ui/icons/FeaturedPlayList'


const ViewUpdateRoutePermission =  (props)  => {

    const {handleSubmit,route,
            handleChange,handleBlur,
            inputvalidation,infoErrors,
            serverValidation,handleCloseInfo,
            handleCloseErros,loading} = props

    const styledUpdateRoutePermission = StyledUpdateRoutePermission()

    return(<BodyForm infoErrors={infoErrors} 
                     serverValidation={serverValidation} 
                     handleCloseInfo={handleCloseInfo}
                     handleCloseErros={handleCloseErros}
                     handleSubmit={handleSubmit}
                     loading={loading}
                     icone={<FeaturedPlayListIcon fontSize='large'  className={styledUpdateRoutePermission.color}/>}
                     title='Funcionalidades'
                     urlBackCommand='/allRoute'
                     showSaveButton={true} showBackButton={true}
                     paperMainWidth='50%'>
            <Box>
                <TextField 
                    id='route' 
                    name='route'
                    label='Rota' 
                    autoComplete='off'
                    value={route.route} 
                    onChange={event=> handleChange(event,false)}
                    onBlur={event=> handleBlur(event)}
                    helperText={inputvalidation.keys.route !== '' ? inputvalidation.keys.route  : ''}
                    error={inputvalidation.keys.route !== '' ? true : false}
                    inputProps={{ maxLength: 45 }}
                />
            </Box>
            <Box>
                <TextField 
                            id='description' 
                            name='description'
                            label='Descrio'
                            autoComplete='off'
                            value={route.description} 
                            className={styledUpdateRoutePermission.largeTextField}
                            onChange={event=> handleChange(event,false)}
                            onBlur={event=> handleBlur(event)}
                            helperText={inputvalidation.keys.description !== '' ? inputvalidation.keys.description  : ''}
                            error={inputvalidation.keys.description !== '' ? true : false}
                            inputProps={{ maxLength: 100 }}
                />
            </Box>
    </BodyForm>)

}
export default ViewUpdateRoutePermission
