import React,{useRef} from 'react'
import {Box,Input} from '@material-ui/core'
import TableContainer from '@material-ui/core/TableContainer'
import Paper from '@material-ui/core/Paper'
import StyledReportViewer from '../ReportViewer/StyledReportViewer'
import SkipNextIcon from '@material-ui/icons/SkipNext'
import SkipPreviousIcon from '@material-ui/icons/SkipPrevious'
import NavigateBeforeIcon from '@material-ui/icons/NavigateBefore'
import NavigateNextIcon from '@material-ui/icons/NavigateNext'
import GridOnIcon from '@material-ui/icons/GridOn'
import PrintIcon from '@material-ui/icons/Print'
import { Tooltip } from '@material-ui/core'
import ReportTableContentRows from '../ReportTableContent/ReportTableContentRows/ReportTableContentRows'
import {useReactToPrint} from 'react-to-print'
import ReactHTMLTableToExcel from 'react-html-table-to-excel'
import PropTypes from 'prop-types'
import ExitToAppIcon from '@material-ui/icons/ExitToApp'
import IconButton from '@material-ui/core/IconButton'
import {useHistory} from 'react-router-dom'

const ViewReportViewer = (props) => {

     const {rows,allRows,headCells,titleReport,landscapeView,
     exportToExcel,goFirstPage,totalPages,page,
     goPreviousPage,goNextPage,goLastPage,goSpecificPage} = props

    const styledReportViewer = StyledReportViewer()
    let history = useHistory()

    const componentRef = useRef();
    const handlePrint = useReactToPrint( {
      content:()=>componentRef.current
    }) 
    
    window.onload = (e)=> {
      history.goBack()
    }

    return(
    <>
    <Box display='flex' justifyContent='center'>
        <TableContainer className={styledReportViewer.rptViewerHeader} component={Paper} >
          <Box display='flex' justifyContent='space-between' style={{height:'7vh'}}>
            <Box display='flex' justifyContent='flex-start' >
                <Box>
                  <Tooltip title="Ir para a primeira pgina" aria-label="SkipPrevious">
                    <IconButton className={styledReportViewer.headerPageControlCmd} color="primary" aria-label="Voltar para a primeira pgina" size="small" onClick={goFirstPage}>
                      <SkipPreviousIcon size="inherit" />
                    </IconButton>
                  </Tooltip>
                </Box>
                <Box>
                  <Tooltip title="Ir para a pgina Anterior" aria-label="NavigateBeforeIcon">
                    <IconButton className={styledReportViewer.headerPageControlCmd} color="primary" aria-label="Pgina anterior" size="small" onClick={goPreviousPage}>
                      <NavigateBeforeIcon size="inherit" />
                    </IconButton>
                  </Tooltip> 
                </Box>
                <Box  style={{width:'2vw'}}><Input className={styledReportViewer.headerPageInput}  type='text' value={page} inputProps={{ maxLength:"2",style:{textAlign:'center'}}}  onChange={goSpecificPage}> </Input></Box>
                <Box className={styledReportViewer.headerTextCmd}><b>de</b></Box>
                <Box className={styledReportViewer.headerTextCmd}>{totalPages}</Box>
                <Box className={styledReportViewer.styledReportViewer}>
                  <Tooltip title="Ir para a prxima pgina" aria-label="NavigateNextIcon">
                    <IconButton className={styledReportViewer.headerPageControlCmd} color="primary" aria-label="Prxima Pgina" size="small" onClick={goNextPage}>
                      <NavigateNextIcon size="inherit" />
                    </IconButton>
                  </Tooltip>
                </Box>
                <Box>
                  <Tooltip title="Ir para o ltima pgina" aria-label="SkipNextIcon">
                    <IconButton className={styledReportViewer.headerPageControlCmd} color="primary" aria-label="Ir para a ltima pgina" size="small" onClick={goLastPage}>
                      <SkipNextIcon size="inherit" />
                    </IconButton>
                  </Tooltip>
                </Box>
            </Box>
            <Box display='flex' justifyContent='flex-end'>
                <Box>
                  <Tooltip title="Exportar para Excel" aria-label="GridOnIcon">
                  
                    <IconButton className={styledReportViewer.headerPageControl} color="primary" aria-label="Exportar para Excel" size="small" onClick={exportToExcel}>
                      <GridOnIcon size="inherit" />
                    </IconButton>
                  </Tooltip>
                </Box>
                <Box style={{display:'none'}}>
                  <ReactHTMLTableToExcel 
                      id="table-xls-button"
                      className="download-table-xls-button"
                      table="reportContent"
                      filename={`R.A.I.S-${titleReport}`}
                      sheet="Relatrio"
                      buttonText="Download as XLS"/>  
                </Box>
                <Box>
                  <Tooltip title="Imprimir" aria-label="GridOnIcon">
                    <IconButton className={styledReportViewer.headerPageControl} color="primary" aria-label="Imprimir" size="small" onClick={handlePrint}>
                      <PrintIcon size="inherit" />
                    </IconButton>
                  </Tooltip>
                </Box>
                <Box>
                  <Tooltip title="Voltar" aria-label="GridOnIcon">
                    <IconButton className={styledReportViewer.headerPageControl} color="primary" aria-label="Voltar" size="small" onClick={(e)=> history.goBack()}>
                      <ExitToAppIcon size="inherit" />
                    </IconButton>
                  </Tooltip>
                </Box>
            </Box>
          </Box>
      </TableContainer>
    </Box>
    <Box display='flex' justifyContent='center'>    
      <TableContainer className={styledReportViewer.rptViewerHeader} component={Paper}  >
          <Box className={styledReportViewer.reportContent}>
              <ReportTableContentRows  
                id='reportContentPaged'
                rows={rows}
                landscapeView={landscapeView}
                headCells={headCells}
                titleReport={titleReport}/>
          </Box>  
          <Box  className={`${styledReportViewer.reportContent} ${styledReportViewer.reportContentNoPaging}`} ref={componentRef} >
              <ReportTableContentRows
               id='reportContent'  
                rows={allRows}
                landscapeView={landscapeView}
                headCells={headCells}
                titleReport={titleReport}/>
          </Box>  
        </TableContainer>
    </Box>
    </>
    )}
   
    ViewReportViewer.propTypes = {
      rows: PropTypes.array.isRequired,
      allRows: PropTypes.array.isRequired,
      headCells: PropTypes.array.isRequired,
      titleReport: PropTypes.string.isRequired,
      totalPages: PropTypes.number.isRequired,
      page: PropTypes.number.isRequired,
      landscapeView: PropTypes.bool.isRequired,
      exportToExcel: PropTypes.func.isRequired,
      goFirstPage: PropTypes.func.isRequired,
      goPreviousPage: PropTypes.func.isRequired,
      goNextPage: PropTypes.func.isRequired,
      goLastPage: PropTypes.func.isRequired,
      goSpecificPage: PropTypes.func.isRequired
      }
    
export default ViewReportViewer
