function InputValidateRoutePermission(event) {
    
    let ret={msg:"",isError:false}

    switch (event.target.name) {
        case 'route':
            if(event.target.value.length === 0) {
                ret.msg = 'Rota   obrigatrio.'
                ret.isError=true
            }
            break
        case 'description':
            if(event.target.value.length === 0) {
                ret.msg = 'Descrio  obrigatrio.'
                ret.isError=true
              }
            break
        default:
            ret.msg=""
            ret.isError=false
        }
        return ret
    }
   

    export default InputValidateRoutePermission