import React, {useEffect, useState} from 'react'
import ViewLogin from './ViewLogin'
import {login} from '../Store/userAuthenticated/userAuthenticated.actions'
import {getPermission} from '../Store/Permission/permission.actions'
import {useDispatch} from 'react-redux'
import {logout} from '../Store/userAuthenticated/userAuthenticated.actions'
import {getApi,setApiAuth} from '../../Assets/js/apiProxy'
import {infoError} from '../../Assets/js/infoError'

function Login(props) {

    let userState = {logonUser: '',email:'',password:''}
    const [user, setUser] = useState(userState)

    const [createUsersByAdmin,getParameter] = useState(false)
    const [infoErrors,setInfoErrors] = useState({msg:''})
    const [serverValidation,setServerValidation] = useState({msg:''})
    const [loading,setLoading] = useState(false)
    const dispatch = useDispatch()
    dispatch(logout())

    const api = getApi('api_features')

    useEffect(()=> {
       
        api.post('/getConfigParameter',{parameter:'createUsersByAdmin'})
        .then(function (response) {
            if (response.data.registers) {
                  let configParameter =  response.data.registers.shift()  
                  parseInt(configParameter.parameterValue) === 1 ?  getParameter(true) : getParameter(false)
            } else {
                let serverValidationState = {msg:response.data.msg}
                setServerValidation(serverValidationState)
            }
        })
        .catch(function (error) {  
            let infoErrorsState = infoError(error)
            console.log(infoErrorsState.msg)
            setInfoErrors(infoErrorsState)
        })

        return () => { setLoading(false)}

    },[api])

    function handleCloseErros() { 
        setInfoErrors({msg:''})
    }

    function handleCloseInfo() { 
        setServerValidation({msg:''})
    }

    const handleChange = (e) => {
            setUser({...user,[e.target.name]: e.target.value})
    }

    const handleSubmit = (event) => {
        setLoading(true)
        if (props.match.params.key !== undefined) { 
            api.post(`/firstLogin/${props.match.params.key}` ,user)
            .then(function (response) {
                if (response.data.bsApproved) {
                    dispatch(login({name: response.data.registers[0].name,
                                    email: response.data.registers[0].email,
                                    token: response.data.token,
                                    auth: response.data.auth,
                                    profile_id: response.data.registers[0].profile_id,
                                    restartPassword: response.data.registers[0].restartPassword,
                                    idNumber:response.data.registers[0].id}))
                    dispatch(getPermission(response.data.permission))
                    
                    setApiAuth(response.data.token)
                    
                    props.history.push('/home')
                } else {
                    let serverValidationState = {msg:response.data.msg}
                    setServerValidation(serverValidationState)
                }
            })
            .catch (function (error) {  
                let infoErrorsState = infoError(error)
                console.log(infoErrorsState.msg)
                setInfoErrors(infoErrorsState)
            })
            .finally(()=> {
                setLoading(false)
            })
        }
        else {
            api.post('/login',user)
            .then(function (response) {
                if (response.data.bsApproved) {
                    dispatch(login({name: response.data.registers[0].name,
                                    email: response.data.registers[0].email,
                                    logonUser: response.data.registers[0].logonUser,
                                    token: response.data.token, 
                                    auth: response.data.auth,
                                    profile_id: response.data.registers[0].profile_id,
                                    restartPassword: response.data.registers[0].restartPassword,
                                    idNumber: response.data.registers[0].id}))
                    dispatch(getPermission(response.data.permission))
                    
                    setApiAuth(response.data.token)

                    if (response.data.registers[0].restartPassword) { 
                        props.history.push(`/changePasswordUserByAdmin/${response.data.registers[0].id}`,user)    
                    } else {
                        props.history.push('/home')
                    }
                } else {
                    let serverValidationState = {msg:response.data.msg}
                    setServerValidation(serverValidationState)
                }
            })
            .catch(function (error) {  
                let infoErrorsState = infoError(error)
                console.log(infoErrorsState.msg)
                setInfoErrors(infoErrorsState)
            })
            .finally(()=> {
                setLoading(false)
            })
        }

        event.preventDefault()
    }

    return (<ViewLogin user={user}
            handleChange={handleChange} 
            handleSubmit={handleSubmit}
            handleCloseErros={handleCloseErros}
            handleCloseInfo={handleCloseInfo}
            infoErrors={infoErrors}
            serverValidation={serverValidation}
            createUsersByAdmin={createUsersByAdmin}
            loading={loading}
    />)
        
}

export default Login