import React, {useEffect, useState} from 'react'
import ViewToolbar from './ViewToolbar'
import IconButton from '@material-ui/core/IconButton'
//import Badge from '@material-ui/core/Badge'
import MenuItem from '@material-ui/core/MenuItem'
import Menu from '@material-ui/core/Menu'
import AccountCircle from '@material-ui/icons/AccountCircle'
//import NotificationsIcon from '@material-ui/icons/Notifications'
import List from '@material-ui/core/List'
import ListItem from '@material-ui/core/ListItem'
import ListItemIcon from '@material-ui/core/ListItemIcon'
import ListItemText from '@material-ui/core/ListItemText'
import ExitToAppIcon from '@material-ui/icons/ExitToApp'
import StyledUserLinks from './StyledToolbar'
import {getApi} from '../../Assets/js/apiProxy'
import { useSelector, useDispatch } from 'react-redux'
import {logout} from '../Store/userAuthenticated/userAuthenticated.actions'
import {clearPermission} from '../Store/Permission/permission.actions'
import {mnuMounted} from '../Store/mnuMounted/mnuMounted.actions'
import { Redirect } from 'react-router-dom'

function Toolbar(props) {
 
        const api = getApi('api_features')

        const classes = StyledUserLinks()
        
        const [anchorEl, setAnchorEl] = useState(null)
        const [mobileMoreAnchorEl, setMobileMoreAnchorEl] = useState(null)
        const isMenuOpen = Boolean(anchorEl)
        const [userAuth, setUserAuth] = useState(false)
        const [restartPasswordActivated, setRestartPasswordActivated] = useState(false)
        const isMobileMenuOpen = Boolean(mobileMoreAnchorEl)
        const dispatch = useDispatch()
       
        const userAuthenticated = useSelector(state => state.authenticated)

        useEffect(()=> {
          setUserAuth(false)
          setRestartPasswordActivated(false)
          dispatch(mnuMounted(false))
          if (userAuthenticated !== null) { 
              if (userAuthenticated.auth === true) { 
                  setUserAuth(true)
                  dispatch(mnuMounted(true))
              }
              if (userAuthenticated.restartPassword === 1) {
                 setRestartPasswordActivated(true)
              } 
          }
        },[userAuthenticated,dispatch])

        const handleProfileMenuOpen = (event) => {
          setAnchorEl(event.currentTarget)
        }
      
        const handleMobileMenuClose = () => {
          setMobileMoreAnchorEl(null)
        }
      
        const handleMenuClose = () => {
          setAnchorEl(null)
          handleMobileMenuClose()
        }

        const handleLogout = () => {
            if (userAuthenticated !== null) { 
                
                api.post('/logout',{email: userAuthenticated.email})
                .then(function (response) {
                    if (response.data.bsApproved) {
                      <Redirect to='/login'/>
                      dispatch(logout()) 
                      dispatch(clearPermission()) 
                    } else {
                        alert('No foi possivel realizar o logout. Tente novamente')
                    }
                })
                .catch(function (error) {  
                    console.log(error)
                })
              }
        }
      
        const handleMobileMenuOpen = (event) => {
          setMobileMoreAnchorEl(event.currentTarget)
        }
      
        const menuId = 'menu-conta-usuario';
        const renderMenu = (
          <Menu  arial-label='menu-direito' style={{marginTop:'35px'}}
            anchorEl={anchorEl}
            anchorOrigin={{ vertical: 'top', horizontal: 'right' }}
            id={menuId}
            keepMounted
            transformOrigin={{ vertical: 'top', horizontal: 'right' }}
            open={isMenuOpen}
            onClose={handleMenuClose}>
                  <MenuItem onClick={handleMenuClose}>
                          <List>
                            <ListItem className={classes.listItemStyled} onClick={handleLogout}>
                              <ListItemIcon>
                                  <ExitToAppIcon className={classes.color} />
                              </ListItemIcon>
                              <ListItemText primary='Sair' />
                            </ListItem>
                          </List>
                  </MenuItem>
          </Menu>
        )
      
        const mobileMenuId = 'menu-conta-usuario-mobile'
        const renderMobileMenu = (
          <Menu
            anchorEl={mobileMoreAnchorEl}
            anchorOrigin={{ vertical: 'top', horizontal: 'right' }}
            id={mobileMenuId}
            keepMounted
            transformOrigin={{ vertical: 'top', horizontal: 'right' }}
            open={isMobileMenuOpen}
            onClose={handleMobileMenuClose}
          >
            {/* <MenuItem>
              <IconButton aria-label="exibe XX novas notificaes" color="inherit">
                <Badge badgeContent={10} color="secondary">
                  <NotificationsIcon />
                </Badge>
              </IconButton>
              <p>Notificaes</p>
            </MenuItem> */}
            <MenuItem onClick={handleProfileMenuOpen}>
              <IconButton
                aria-label="conta do usurio logado"
                aria-controls="primary-search-account-menu"
                aria-haspopup="true"
                color="inherit"
              >
                <AccountCircle />
              </IconButton>
              <p>Minha Conta</p>
            </MenuItem>
          </Menu>
        )
            
  
    return(<ViewToolbar 
                mobileMenuId={mobileMenuId}
                restartPasswordActivated={restartPasswordActivated}
                userAuth={userAuth}
                menuId={menuId}
                handleMobileMenuOpen={handleMobileMenuOpen}
                handleProfileMenuOpen={handleProfileMenuOpen}
                toggleDrawer = {props.toggleDrawer}
                renderMenu = {renderMenu}
                renderMobileMenu = {renderMobileMenu} />)
}

export default Toolbar