import React from 'react'
import {TextField,Box,Button} from '@material-ui/core'
import StyledUpdateDriverSMS from './StyledUpdateDriverSMS'
import InputMask from 'react-input-mask'
import SmsIcon from '@material-ui/icons/Sms'
import BodyForm from '../../../../ComponentsLibrary/Form/BodyForm/BodyForm'
import Dialog from '@material-ui/core/Dialog'
import DialogActions from '@material-ui/core/DialogActions'
import DialogContent from '@material-ui/core/DialogContent'
import DialogContentText from '@material-ui/core/DialogContentText'
import DialogTitle from '@material-ui/core/DialogTitle'


const ViewUpdateDriverSMS =  (props)  => {

    const {handleSubmit,DriverSMS,
            handleChange,handleBlur,
            inputvalidation,infoErrors,
            serverValidation,handleCloseInfo,
            handleCloseErros,loading,registerPersisted,handleClose} = props

    const styledUpdateDriverSMS = StyledUpdateDriverSMS()


return( <BodyForm infoErrors={infoErrors} 
        serverValidation={serverValidation} 
        handleCloseInfo={handleCloseInfo}
        handleCloseErros={handleCloseErros}
        handleSubmit={handleSubmit}
        loading={loading}
        icone={<SmsIcon fontSize='large' className={styledUpdateDriverSMS.color} />}
        title='Interface com o Motorista'
        urlBackCommand='#'
        showSaveButton={true} showBackButton={false}
        paperMainWidth='60%'>
                    <Box display='flex' flexDirection='Column'  aria-label='flex-column-container-fields' >
                        <Box display='flex' justifyContent='center' mb={9}>
                            <InputMask 
                                mask='9999'
                                maskChar=''
                                value={DriverSMS.code} 
                                onChange={event=> handleChange(event,true)}
                                onBlur={event=> handleBlur(event)}>
                                {inputProps =>
                                    <TextField
                                    {...inputProps}
                                    id='code' 
                                    name='code'
                                    label='Cdigo'
                                    autoComplete='off'
                                    inputProps={{style:{textAlign:'center'}}}
                                    InputProps={{
                                        classes: {
                                            input: styledUpdateDriverSMS.resize
                                        },
                                    }}
                                    className={styledUpdateDriverSMS.smallTextField}
                                    helperText={inputvalidation.keys.code !== '' ? inputvalidation.keys.code  : ''}
                                    error={inputvalidation.keys.code !== '' ? true : false}
                                    />}
                            </InputMask>
                        </Box>
                        <Box  display='flex' justifyContent='center'>
                            <InputMask 
                                mask='(99)999999999'
                                maskChar=''
                                value={DriverSMS.phoneNumber} 
                                onChange={event=> handleChange(event,true)}
                                onBlur={event=> handleBlur(event)}>
                                {inputProps =>
                                    <TextField
                                    {...inputProps}
                                    id='phoneNumber' 
                                    name='phoneNumber'
                                    label='(DDD) + Celular'
                                    autoComplete='off'
                                    inputProps={{style:{textAlign:'left'}}}
                                    InputProps={{
                                        classes: {
                                            input: styledUpdateDriverSMS.resize
                                        },
                                    }}
                                    className={styledUpdateDriverSMS.mediumTextField}
                                    helperText={inputvalidation.keys.phoneNumber !== '' ? inputvalidation.keys.phoneNumber  : ''}
                                    error={inputvalidation.keys.phoneNumber !== '' ? true : false}
                                    />}
                            </InputMask>
                        </Box>
                        <Box>
                            <Dialog
                                open={registerPersisted}
                                onClose={handleClose}
                                aria-labelledby="Motorista Persistido"
                                aria-describedby="Motorista Persistido">
                        
                                <DialogTitle id="DriverPersisted">Ateno:</DialogTitle>
                                <DialogContent>
                                <DialogContentText id="alert-dialog-description">
                                   Fique atento ao recebimento de SMS enviado pelo sistema.
                                </DialogContentText>
                                </DialogContent>
                                <DialogActions>
                                <Button onClick={handleClose} color="primary" autoFocus>
                                    Fechar
                                </Button>
                                </DialogActions>
                            </Dialog>
                        </Box>
                </Box>
        </BodyForm>
      
    )

}
export default ViewUpdateDriverSMS
