import React from 'react'
import {useState,useEffect} from 'react'
import ViewKeyMonitorReport from './ViewKeyMonitorReport'
import {getApi} from '../../../Assets/js/apiProxy'
import moment from 'moment'
import formater from '../../../Assets/js/formater'
import {infoError} from '../../../Assets/js/infoError'


function KeyMonitorReport(props) {

    const api = getApi('api_features')

    const [reportFilters,setReportFilters] = useState({initialDate:  moment().format('DD/MM/YYYY') ,finalDate: moment().format('DD/MM/YYYY'),keyHolderId: null,user:''})
    const [keyHolderId,setKeyHolderId] = useState(-1)
    const [keyHolderList,getAllKeyHolder] = useState([])
    const [reportAvaliableKey,setReportAvaliableKey] = useState([])
    const [infoErrors,setInfoErrors] = useState({msg:''})
    const [serverValidation,setServerValidation] = useState({msg:''})
    const [loading,setLoading] = useState(false)

    const [snackInfo,setSnackInfo] = useState('')
    const [visibility, setVisibility] = useState(false)

    
    
   useEffect(()=> {
      api.get(`/allKeyHolder`)
      .then(function (response) {
         
      if (response.data.registers !== undefined) { 
         let keyHolders = response.data.registers
         getAllKeyHolder(keyHolders)}
      })
      .catch(function (error) {  
         let infoErrorsState = infoError(error)
         console.log(infoErrorsState.msg)
         setInfoErrors(infoErrorsState)
      })

      return ()=> setLoading(false)
   },[api])

    function getReportAvaliableKey(reportFilters,keyHolderId) {
        
        setReportAvaliableKey([])
        setLoading(true)
        
        let adjustedFilters = { 
            keyHolderId: keyHolderId,
            initialDate: formater(reportFilters.initialDate,'date'),
            finalDate: formater(reportFilters.finalDate,'date'),
            user: reportFilters.user
        }

        api.post(`/reportsKeyHolder`,adjustedFilters)
        .then(function (response) {
            if (response.data.bsApproved) {
                if (response.data.registers !== undefined) { 
                    let data = response.data.registers.map((e)=> { 
                                e.date = moment(e.date).format('DD/MM/YYYY hh:mm')
                                return(e)
                            })
                    setReportAvaliableKey(data)
                    setLoading(false)
                }
            } else { 
                setSnackInfo(response.data.msg)
                setVisibility(true)
                setLoading(false)
            }
        })                           
        .catch(function (error) {  
            let infoErrorsState = infoError(error)
            setSnackInfo(infoErrorsState.msg)
            setVisibility(true)
            setLoading(false)
            })
    }

    const  handleCloseSnack = ()=> {
        setVisibility(false)
     }

    function handleCloseErros() { 
        setInfoErrors({msg:''})
    }

    function handleChange(e) { 
            setReportFilters({...reportFilters,[e.target.name]: e.target.value})
    }

    function handleCloseInfo() { 
        setServerValidation({msg:''})
    }

    function handleChangeKeyHolder(e) {
        setKeyHolderId(e.target.value)
        reportFilters.keyHolderId = e.target.value
        setReportFilters(reportFilters) 
    }

    
    const handleFilter = ()=> {
        getReportAvaliableKey(reportFilters,keyHolderId)
        document.getElementById('fade').style.display = 'none'
    }

    return(<ViewKeyMonitorReport 
        reportAvaliableKey={reportAvaliableKey}
        keyHolderId={keyHolderId}
        keyHolderList={keyHolderList}
        infoErrors={infoErrors}
        serverValidation={serverValidation}
        reportFilters={reportFilters}
        handleChangeKeyHolder={handleChangeKeyHolder}
        handleCloseErros={handleCloseErros}
        handleCloseInfo={handleCloseInfo}
        handleChange={handleChange}
        handleFilter={handleFilter}
        handleCloseSnack={handleCloseSnack}
        visibility={visibility}
        snackInfo={snackInfo}
        loading={loading}

    />)
}

export default KeyMonitorReport