import React from 'react'
import PropTypes from 'prop-types'
import Button from '@material-ui/core/Button';
import DeleteIcon from '@material-ui/icons/DeleteOutline'


const ViewDeleteCommand = (props) => {
    const {id,api_url,handleDeleteCommand,titulo,isIcon,permission,userAuthenticated} = props
    
    let _click = !permission ? 
        null
    :
    userAuthenticated.idNumber !== id ?
        handleDeleteCommand 
    : 
        ()=> alert('Voc esta logado. No posso exclu-lo')
    
        let _style = !permission ? {fill:"gray",cursor:'not-allowed'} : {fill:"red",cursor:'pointer'}

    return(
        <div>
            {isIcon ===true ? 
                (<DeleteIcon id={id}  onClick={_click}  api_url={api_url} style={_style} arial-label='link-para-deleo'/>)
                :
                (<Button  variant="contained" color="primary" id={id} onClick={_click} api_url={api_url} disabled={permission} arial-label='boto-para-deleo'>{titulo}</Button>)
            }
            
        </div>
    )}

    ViewDeleteCommand.propTypes = {
    id: PropTypes.number.isRequired,
    handleDeleteCommand: PropTypes.func.isRequired,
    titulo: PropTypes.string.isRequired,
    isIcon: PropTypes.bool.isRequired
    }

    ViewDeleteCommand.defaultProps = {
        id:0,
        titulo:'Excluir'
    }

 export default ViewDeleteCommand
