import React from 'react'
import { Route,Switch} from 'react-router-dom'
import PrivateRoute from './PrivateRoute'
import DeleteCommand from '../../ComponentsLibrary/DeleteCommand/DeleteCommand'

//#region - Import System Core Routes
    import Home from '../Home/Home'
    import RegisterConfirmation from '../RegisterConfirmation/RegisterConfirmation'
    import RecoveryPassConfirmation from '../RecoveryPassConfirmation/RecoveryPassConfirmation'
    import RecoveryPass from '../RecoveryPass/RecoveryPass'
    import ChangePassword from '../../Core/ChangePassword/ChangePassword'
    import ChangePasswordUserByAdmin from '../../Core/ChangePasswordUserByAdmin/ChangePasswordUserByAdmin'
    
    import Menu from '../Menu/Menu'
    import Login from '../Login/Login'
    import Register from '../Register/Register'
    import NotFound from '../NotFound/NotFound'
    import PermissionNotFound from '../PermissionNotFound/PermissionNotFound'
//#endregion

//#region - Import Administrative Routes 
    import ReadUser from '../../Features/Administrative/User/Read/ReadUser'
    import UpdateUser from '../../Features/Administrative/User/Update/UpdateUser'

    import ReadConfigParameter from '../../Features/Administrative/ConfigParameter/Read/ReadConfigParameter'
    import UpdateConfigParameter from '../../Features/Administrative/ConfigParameter/Update/UpdateConfigParameter'

    import ReadProfile from '../../Features/Administrative/Profile/Read/ReadProfile'
    import UpdateProfile from '../../Features/Administrative/Profile/Update/UpdateProfile'

    import ReadRoutePermission from '../../Features/Administrative/RoutePermission/Read/ReadRoutePermission'
    import UpdateRoutePermission from '../../Features/Administrative/RoutePermission/Update/UpdateRoutePermission'

    import UpdatePermission from '../../Features/Administrative/Permission/Update/UpdatePermission'
//#endregion

//#region - Import KeyHolder Routes
    import ReadKeyHolder from '../../Features/Operational/KeyHolder/Read/ReadKeyHolder'
    import UpdateKeyHolder from '../../Features/Operational/KeyHolder/Update/UpdateKeyHolder'
    import AvaliableKey from '../../Features/Operational/AvaliableKey/Update/UpdateAvaliableKey'
    import MonitorKey from '../../Features/Operational/MonitorKey/MonitorKey'
   
//#endregion

//#region - Import Pager and SMS Routes
    import DriverSMS from '../../Features/Operational/DriverSMS/Update/UpdateDriverSMS'
    
    import ReadDriver from '../../Features/Operational/Driver/Read/ReadDriver'
    import UpdateDriver from '../../Features/Operational/Driver/Update/UpdateDriver'
//#endregion

//#region - Import Monitor Overfill 
    import ReadTruck from '../../Features/Operational/Truck/Read/ReadTruck'
    import UpdateTruck from '../../Features/Operational/Truck/Update/UpdateTruck'

    import ReadMonitorOverfill from '../../Features/Operational/MonitorOverFill/Read/ReadMonitorOverfill'
//#endregion

//#region - Import Multiload Control
    import ReadMultiloadControl from '../../Features/Operational/MultiloadControl/Read/ReadMultiloadControl'
//#endregion

//#region - Import Report Routes
    import KeyMonitorReport from '../../Features/Report/KeyMonitorReport/KeyMonitorReport'
    import OverfillMonitorReport from '../../Features/Report/OverfillMonitorReport/OverfillMonitorReport'
    import MultiloadControlReport from '../../Features/Report/MultiloadControlReport/MultiloadControlReport.js'
    import GeneralReport from '../../Features/Report/GeneralReport/GeneralReport'
//#endregion

function Routes(props) {
    
    const routes = (
        <div>
             <Switch>
                     
                     {/* Administrative  Routes*/}   
                     <PrivateRoute path='/addUser' routeName='User' component={UpdateUser} />
                     <PrivateRoute path='/allUser' routeName='User' component={ReadUser} />
                     <PrivateRoute path='/updateUser' routeName='User' component={UpdateUser} />
                     <PrivateRoute path='/deleteUser' routeName='User' component={DeleteCommand} />

                     <PrivateRoute path='/addConfigParameter' routeName='ConfigParameter' component={UpdateConfigParameter} />
                     <PrivateRoute path='/allConfigParameter' routeName='ConfigParameter' component={ReadConfigParameter} />
                     <PrivateRoute path='/updateConfigParameter' routeName='ConfigParameter' component={UpdateConfigParameter} />
                     <PrivateRoute path='/deleteConfigParameter' routeName='ConfigParameter' component={DeleteCommand} />

                     <PrivateRoute path='/addProfile' routeName='Profile' component={UpdateProfile} />
                     <PrivateRoute path='/allProfile' routeName='Profile' component={ReadProfile} />
                     <PrivateRoute path='/updateProfile' routeName='Profile' component={UpdateProfile} />
                     <PrivateRoute path='/deleteProfile' routeName='Profile' component={DeleteCommand} />

                     <PrivateRoute path='/addRoute' routeName='Route' component={UpdateRoutePermission} />
                     <PrivateRoute path='/allRoute' routeName='Route' component={ReadRoutePermission} />
                     <PrivateRoute path='/updateRoute' routeName='Route' component={UpdateRoutePermission} />
                     <PrivateRoute path='/deleteRoute' routeName='Route' component={DeleteCommand} />

                     <PrivateRoute path='/updatePermission' routeName='Permission' component={UpdatePermission} />

                     {/* KeyHolder Routes */}  
                     <PrivateRoute path='/addKeyHolder' routeName='KeyHolder' component={UpdateKeyHolder} />
                     <PrivateRoute path='/allKeyHolder' routeName='KeyHolder' component={ReadKeyHolder} />
                     <PrivateRoute path='/updateKeyHolder' routeName='KeyHolder' component={UpdateKeyHolder} />
                     <PrivateRoute path='/deleteKeyHolder' routeName='KeyHolder' component={DeleteCommand} />

                     <PrivateRoute path='/updateAvaliableKey' routeName='AvaliableKey' component={AvaliableKey} />

                     <PrivateRoute path='/MonitorKey' routeName='MonitorKey' component={MonitorKey} />

                    {/* Pager and SMS  Routes  */}  
                    <Route path='/DriverSMS' routeName='DriverSMS' component={DriverSMS} />
                    
                    <PrivateRoute path='/addDriver' routeName='Driver' component={UpdateDriver} />
                    <PrivateRoute path='/allDriver' routeName='Driver' component={ReadDriver} />
                    <PrivateRoute path='/updateDriver' routeName='Driver' component={UpdateDriver} />
                    <PrivateRoute path='/deleteDriver' routeName='Driver' component={DeleteCommand} />

                    {/* Control Overfill - Monitor Overfill  */}  
                    <PrivateRoute path='/addTruck' routeName='Truck' component={UpdateTruck} />
                    <PrivateRoute path='/allTruck' routeName='Truck' component={ReadTruck} />
                    <PrivateRoute path='/updateTruck' routeName='Truck' component={UpdateTruck} />
                    <PrivateRoute path='/deleteTruck' routeName='Truck' component={DeleteCommand} />
                    <PrivateRoute path='/allBay' routeName='MonitorOverfill' component={ReadMonitorOverfill} />

                    {/* Multiload Control  */}  
                    <PrivateRoute path='/allMultiload' routeName='MultiloadControl' component={ReadMultiloadControl} />

                    {/* Report Routes */}  
                    <PrivateRoute path='/KeyMonitorReport' routeName='KeyMonitorReport' component={KeyMonitorReport} />
                    <PrivateRoute path='/OverfillMonitorReport' routeName='OverfillMonitorReport' component={OverfillMonitorReport} />
                    <PrivateRoute path='/MultiloadControlReport' routeName='MultiloadControlReport' component={MultiloadControlReport} />
                    <PrivateRoute path='/GeneralReport' routeName='GeneralReport' component={GeneralReport} />
                   
                    {/* Core Routes */}
                    <Route exact={true}path="/register" component={Register} />
                    <PrivateRoute exact={true} path="/menu" routeName='Menu' component={Menu} />
                    <PrivateRoute exact={true} path="/home" routeName='Home' component={Home} />
                    <Route exact={true} path="/registerConfirmation"  component={RegisterConfirmation} />
                    <Route exact={true} path="/recoveryPassConfirmation" component={RecoveryPassConfirmation} />
                    <Route exact={true} path="/recoveryPass" component={RecoveryPass} />
                    <Route exact={true} path="/changePassword/:key/:id" component={ChangePassword} />
                    <Route exact={true} path="/changePasswordUserByAdmin/:id" component={ChangePasswordUserByAdmin} />
                    <Route exact={true} path="/login/:key?" component={Login} />
                    <Route exact={true} path="/" component={Login} />
                    <Route path="/PermissionNotFound" component={PermissionNotFound} routeName='Permission' /> 
                    <Route path="*" component={NotFound} routeName='NotFound' /> 

            </Switch>
        </div>)

 return(routes)

}
export default Routes
