const  verifyPermissions = (route,type,userAuthenticated,permissionList) =>  {
    try {
        
        if (permissionList !== undefined && userAuthenticated !== null) {
            
            switch (type) {
                case 'read':
                    return permissionList.find((e)=> e.profile_id === userAuthenticated.profile_id && e.route === route).read === 1 ? true : false     
                case 'write':
                    return permissionList.find((e)=>  e.profile_id === userAuthenticated.profile_id && e.route === route).write === 1 ? true : false                             
                case 'all':
                        let r = permissionList.find((e)=>  e.profile_id === userAuthenticated.profile_id && e.route === route).read
                        let w = permissionList.find((e)=>  e.profile_id === userAuthenticated.profile_id && e.route === route).write 
                        return r ===1 && w ===1 ? true : false 
                default:
                    return false
            }
        } else { 
            return false
        }

    }
    catch (error) {
        console.error(`Certifique-se que o valor do campo route tabela route,'
        esta configurado com o mesmo nome da propriedade routeName na tag LINK de cada Menu.
        Veja se os outros componentes que tambm utilizam o atributo routeName esto com os'
        mesmo valores do banco de dados.
        Estes valores devem ser iguais. - Profile_id = ${userAuthenticated.profile_id} -
        routeName= ${route}` )        
        return false 
    }
}

export default verifyPermissions 