function InputValidateKeyHolder(event) {
    
    let ret={msg:"",isError:false}

    switch (event.target.name) {
        case 'number':
            if(isNaN(event.target.value) || event.target.value === '') {
                ret.msg = 'Nmero  obrigatrio.'
                ret.isError=true
            }
            break
        case 'identification':
            if(event.target.value.length === 0) {
                ret.msg = 'A Identificao do claviculrio  obrigtorio.'
                ret.isError=true
            }
            break
        case 'qtdeKeys':
            if(event.target.value.length === 0) {
                ret.msg = 'A qtde.  obrigatria.'
                ret.isError=true
              }
            break
        default:
            ret.msg=""
            ret.isError=false
        }
        return ret
    }
   

    export default InputValidateKeyHolder