function InputValidateConfigParameter(event) {
    
    let ret={msg:"",isError:false}

    switch (event.target.name) {
        case 'parameter':
            if(event.target.value.length === 0) {
                ret.msg = 'Nome do parmetro  obrigatrio.'
                ret.isError=true
            }
            break
        case 'description':
            if(event.target.value.length === 0) {
                ret.msg = 'Descrio  obrigatrio.'
                ret.isError=true
              }
            break
        case 'parameterValue':
            if(event.target.value.length === 0) {
                ret.msg = 'Valor do parmetro  obrigatrio.'
                ret.isError=true
                }
            break
        default:
            ret.msg=""
            ret.isError=false
        }
        return ret
    }
   

    export default InputValidateConfigParameter