//#region - imports
    import React from 'react'
    import {Box,Paper,InputLabel,Select} from '@material-ui/core'
    import StyledUpdateAvaliableKey from './StyledUpdateAvaliableKey'
    import HeaderForm from '../../../../ComponentsLibrary/Form/HeaderForm/HeaderForm'
    import DisplayErrosMessage from '../../../../ComponentsLibrary/DisplayErrorsMessage/DisplayErrorsMessage'
    import DisplayInfoMessage from '../../../../ComponentsLibrary/DisplayInfoMessage/DisplayInfoMessage'
    import VpnKeyIcon from '@material-ui/icons/VpnKey'
    import LinearProgress from '@material-ui/core/LinearProgress'
    import KeyTableRows from '../KeyTable/keyTableRows/KeyTableRows'
    import Snackbar from '@material-ui/core/Snackbar'
    import IconButton from '@material-ui/core/IconButton'
    import CloseIcon from '@material-ui/icons/Close'
    import FormControl from '@material-ui/core/FormControl'
//#endregion

const ViewUpdateAvaliableKey =  (props)  => {

    const { keyHolderId,
            keyHolderList,
            avaliableKeyList,
            handleChangeKeyHolder,
            infoErrors,serverValidation,handleCloseInfo,
            handleCloseErros,loading,handleChange,handleSave,
            profileList,handleCloseSnack,snackInfo,visibility} = props

    const styledUpdateAvaliableKey = StyledUpdateAvaliableKey()

    //Colunas da Tabela 
    const headCells = [
        { id: 'number',disablePadding: false, label: 'Chave', align:'left', width:'10%'},
        { id: 'profile_id',disablePadding: false, label: 'Perfil', align:'left', width:'30%'},     
        { id: 'description',disablePadding: false, label: 'Descrio', align:'left',width:'40%'},
        { id: 'sealNumber',disablePadding: false, label: 'Id. do Lacre', align:'left',width:'20%'}
    ]

    const headCellsReport = [
        { id: 'number', disablePadding: false, label: 'Chave', align:'left', width:'10%'},
        { id: 'profileDescription',disablePadding: false, label: 'Perfil', align:'left', width:'30%'},     
        { id: 'description',disablePadding: false, label: 'Descrio', align:'left',width:'40%'},
        { id: 'sealNumber',disablePadding: false, label: 'Id. do Lacre', align:'left',width:'20%'}
    ]

    return( 
           <Box className={styledUpdateAvaliableKey.MainBox}>
                <Paper elevation={3} className={styledUpdateAvaliableKey.PaperMain}>
                    <HeaderForm icon={<VpnKeyIcon fontSize='large' className={styledUpdateAvaliableKey.color} />} titulo={'Configurao de Chaves'}/>
                        <Box display='flex' justifyContent='center' style={{marginBottom:'5vh'}}>
                            <Box>
                                <FormControl>
                                        <InputLabel htmlFor="profile-native-simple">Claviculrio</InputLabel>
                                        <Select native
                                        value={keyHolderId}
                                        onChange={e=>handleChangeKeyHolder(e)}
                                        inputProps={{
                                            name: 'keyHolderId',
                                            id: 'keyHolder-select',
                                        }}
                                        >
                                        <option value="-1">Opes...</option>
                                        {
                                            keyHolderList.map((e,i) =>  { 
                                                return (<option key={i} value={e.id}>{e.identification}</option>)
                                            })
                                        }   
                                    </Select>
                                </FormControl>
                            </Box>
                        </Box>
                        <KeyTableRows 
                            rows={avaliableKeyList}
                            headCells={headCells}
                            headCellsReport={headCellsReport}
                            titleReport='Configurao de Chaves'
                            landscapeView={true}
                            loading={loading}
                            handleChange={handleChange}
                            profileList={profileList}
                            handleSave={handleSave}>
                        </KeyTableRows>                          
                        <Snackbar  anchorOrigin={{vertical: 'bottom',horizontal: 'center'}}
                                   open={visibility}  autoHideDuration={500} onClose={handleCloseSnack} message={snackInfo}
                                  action={
                                    <React.Fragment>
                                        <IconButton size="small" aria-label="close" color="inherit" onClick={handleCloseSnack}>
                                        <CloseIcon fontSize="small" />
                                        </IconButton>
                                    </React.Fragment>
                         }
                         />                
                        <DisplayErrosMessage infoErrors={infoErrors} handleCloseErros={handleCloseErros}  />
                        <DisplayInfoMessage serverValidation={serverValidation} handleCloseInfo={handleCloseInfo}/>
                        {loading && 
                            <LinearProgress />      
                        }
                </Paper>
        </Box>
    )

}
export default ViewUpdateAvaliableKey
