import React from 'react'
import ViewReportTableContentRows from './ViewReportTableContentRows'

function ReportTableContentRows(props) {

    const {rows,allRows,headCells,titleReport,landscapeView,id} = props
    
    const [order, setOrder] = React.useState('asc')
    const [orderBy, setOrderBy] = React.useState('id')
    
   
    const handleRequestSort = (event, property) => {
      const isAsc = orderBy === property && order === 'asc'
      setOrder(isAsc ? 'desc' : 'asc')
      setOrderBy(property)
    }
      
    //#region - Controle de Comparao e Ordenao das Colunas 
        function descendingComparator(a, b, orderBy) {
            if (b[orderBy] < a[orderBy]) {
            return -1;
            }
            if (b[orderBy] > a[orderBy]) {
            return 1;
            }
            return 0;
        }
        function getComparator(order, orderBy) {
            return order === 'desc'
            ? (a, b) => descendingComparator(a, b, orderBy)
            : (a, b) => -descendingComparator(a, b, orderBy);
        }
        function stableSort(array, comparator) {
            const stabilizedThis = array.map((el, index) => [el, index]);
            stabilizedThis.sort((a, b) => {
            const order = comparator(a[0], b[0]);
            if (order !== 0) return order;
            return a[1] - b[1];
            });
            return stabilizedThis.map((el) => el[0]);
        }
    //#endregion


    return(<ViewReportTableContentRows
            id={id}
            handleRequestSort={handleRequestSort}
            getComparator={getComparator}
            stableSort={stableSort}
            rows={rows}
            allRows={allRows}
            order={order}
            orderBy={orderBy}
            headCells={headCells}
            titleReport={titleReport}
            landscapeView={landscapeView}
    />)
}

export default ReportTableContentRows