import React from 'react'
import ViewReportTableContentHead from './ViewReportTableContentHead'
  
function ReportTableContentHead(props) {
   
    const {order, orderBy, onRequestSort,headCells,titleReport} = props;

    return(<ViewReportTableContentHead
        titleReport={titleReport}
        order={order}
        orderBy={orderBy}
        onRequestSort={onRequestSort}
        headCells={headCells}/>)
}

export default ReportTableContentHead