import React from 'react'
import { Box,Typography} from '@material-ui/core'
import StyledHeaderForm from './StyledHeaderForm'

const ViewHeaderForm = (props) => {

    const styledHeader = StyledHeaderForm()
    
    return(
      <Box  display='flex'  arial-label='form-header'>
        <Box display='flex' justifyContent='flex-start' className={styledHeader.boxMainHeader}  arial-label='header-description'>
            {props.icon}
            <Box fontWeight='medium'>
              <Typography id="formTitle" className={styledHeader.formTitle}>
                {props.titulo}
              </Typography>
            </Box>
        </Box>
      </Box>
    )}
export default ViewHeaderForm
