import React, { useState } from 'react'
import ViewEnhancedTableToolbar from './ViewEnhancedTableToolbar'


function EnhancedTableToolbar(props) {
    
    const {tableTitle,pathAddComponentForUpdate,handleSearching,icon} = props
    
    const [searchValue, setSearch] = useState('')

    const handleOnChange = (event) => { 
        let val = event.target.value
        setSearch(val)
        handleSearching(val)
    }
   
    return(<ViewEnhancedTableToolbar  
          tableTitle={tableTitle}
          icon={icon}
          searchValue={searchValue} 
          handleOnChange={handleOnChange}
          pathAddComponentForUpdate={pathAddComponentForUpdate}/>)
}

export default EnhancedTableToolbar