import { makeStyles } from '@material-ui/core/styles'

const StyledMenu = makeStyles((theme) => ({
    menuInitialItem: {
        backgroundColor: theme.palette.primary.main,
        minHeight: 56, 
        [theme.breakpoints.up('xs')]: { 
            minHeight: 48, 
        }, 
        [theme.breakpoints.up('sm')]: { 
          minHeight: 64, 
        }
    }
}))

export default StyledMenu