 import React,{useState} from 'react'
import {Link} from "react-router-dom"
import List from '@material-ui/core/List'
import ListItem from '@material-ui/core/ListItem'
import SmsIcon from '@material-ui/icons/Sms'
import PersonIcon from '@material-ui/icons/Person'
import ListItemText from '@material-ui/core/ListItemText'
import styledLinks from './StyledLinks'
import { useSelector } from 'react-redux'
import verifyPermissions from '../../../Assets/js/verifyPermissions'
import Typography from '@material-ui/core/Typography'
import ExpandLess from '@material-ui/icons/ExpandLess'
import ExpandMore from '@material-ui/icons/ExpandMore'
import Collapse from '@material-ui/core/Collapse'

const PagerAndSMSLinks = (props) => {
    
    const classes = styledLinks()
    
    const permissions = useSelector(state=> state.permission)
    const userAuthenticated = useSelector(state=> state.authenticated)

    const [openPagerAndSMS,setOpenPagerAndSMSLinks] = useState(false)
    const handleClick = () =>  setOpenPagerAndSMSLinks(!openPagerAndSMS)
      

    let permission=false
   

    return(
    <div className={classes.drawerWidth} >
        <List component="nav">
            <ListItem button={true} onClick={handleClick} className={classes.ListItem}>
            <ListItemText><Typography color="primary">SMS</Typography></ListItemText>
            {openPagerAndSMS ? <ExpandLess /> : <ExpandMore />}
            </ListItem>
            <Collapse in={openPagerAndSMS} timeout="auto" unmountOnExit>
                {permission = verifyPermissions('Driver','read',userAuthenticated,permissions)}
                <Link to={permission ? "/allDriver" : "#"} onClick={event=> props.toggleDrawer(verifyPermissions('Driver','read',userAuthenticated,permissions) ? false : true,event)} className={classes.linkDecoration}  >
                        <ListItem button className={classes.nested}>
                        <PersonIcon className={classes.color} /><ListItemText  className={permission ? classes.menuText : classes.colorDisable} primary='Cadastro de Motoristas' />
                    </ListItem>
                </Link>

                {permission = verifyPermissions('DriverSMS','read',userAuthenticated,permissions)}
                <Link to={permission ? "/DriverSMS" : "#"} onClick={event=> props.toggleDrawer(verifyPermissions('DriverSMS','read',userAuthenticated,permissions) ? false : true,event)} className={classes.linkDecoration}  >
                        <ListItem button className={classes.nested}>
                        <SmsIcon className={classes.color} /><ListItemText  className={permission ? classes.menuText : classes.colorDisable} primary='Interface com os Motoristas' />
                    </ListItem>
                </Link>
            </Collapse>
        </List>
    </div>
    )}

export default PagerAndSMSLinks
