import React,{useState} from 'react'
import {Box,Select,TextField,InputLabel,Paper, Typography,Button} from '@material-ui/core'
import ReportViewer from '../../../ComponentsLibrary/ReportViewer/ReportViewer'
import DisplayErrosMessage from '../../../ComponentsLibrary/DisplayErrorsMessage/DisplayErrorsMessage'
import DisplayInfoMessage from '../../../ComponentsLibrary/DisplayInfoMessage/DisplayInfoMessage'
import styledKeyMonitorReport from './StyledKeyMonitorReport'
import FormControl from '@material-ui/core/FormControl'
import InputMask from 'react-input-mask'
import Fade from '@material-ui/core/Fade'
import SearchIcon from '@material-ui/icons/Search'
import ExpandMoreIcon from '@material-ui/icons/ExpandMore'
import Snackbar from '@material-ui/core/Snackbar'
import IconButton from '@material-ui/core/IconButton'
import CloseIcon from '@material-ui/icons/Close'
import CircularProgress from '@material-ui/core/CircularProgress'

const ViewKeyMonitorReport = (props) => {
    const { reportAvaliableKey,reportFilters,
            keyHolderId,keyHolderList,infoErrors,serverValidation,
            handleCloseInfo,handleCloseErros,loading,
            handleChange,handleChangeKeyHolder,handleFilter,visibility,handleCloseSnack,snackInfo} = props 

    const classes = styledKeyMonitorReport()

     //Colunas da Tabela 
     const headCells = [
        { id: 'date',type:'date', disablePadding: false, label: 'Registrado em', align:'left', width:'10%'},
        { id: 'userLogon',disablePadding: false, label: 'Usurio', align:'left',width:'10%'},
        { id: 'name',disablePadding: false, label: 'Nome', align:'left', width:'50%'},
        { id: 'keyNumber',disablePadding: false, label: 'Nmero da Chave', align:'center', width:'10%'},
        { id: 'status',type:'keyStatus', disablePadding: false, label: 'Situao das Chaves', align:'center', width:'10%'},
        { id: 'exception',type:'bool', disablePadding: false, label: 'Pertence ao Perfil', align:'center', width:'10%'}
    ]

    const [faded,setFaded] = useState(false)
  
    const handleOpenFilter = ()=> { 
        setFaded(true)
         document.getElementById('fade').style.display = 'flex'
        
    }
    const handleCloseFilter = ()=> { 
        document.getElementById('fade').style.display = 'none'
    }
    return(
    <Box display='flex' flexDirection='Column'>
        <Paper  className={classes.paperMain}>
            <Box display='flex' justifyContent='center'>
                <Box display='flex' justifyContent='center'  onMouseOver={handleOpenFilter} style={{cursor:'pointer'}}>
                    <Typography className={classes.filter}>Filtros</Typography>
                </Box>
                <Box>
                    <ExpandMoreIcon onMouseOver={handleOpenFilter} style={{cursor:'pointer'}}/>
                </Box>
            </Box>
        </Paper>
        <Fade id='fade' display='none' in={faded} >
            <Box  display='flex' justifyContent='center' m={1} >
                <Paper style={{padding:'1vh 0.5vw 1vh 0.5vw',}}>
                                <Box display='flex' justifyContent='flex-start' m={1} >
                                            <FormControl>
                                                    <InputLabel htmlFor="profile-native-simple">Perfil</InputLabel>
                                                    <Select native
                                                    value={keyHolderId}
                                                    onChange={e=>handleChangeKeyHolder(e)}
                                                    inputProps={{
                                                        name: 'keyHolderId',
                                                        id: 'keyHolder-select',
                                                    }}
                                                    >
                                                    <option value="-1">Opes...</option>
                                                    {
                                                        keyHolderList.map((e,i) =>  { 
                                                            return (<option key={i} value={e.id}>{e.identification}</option>)
                                                        })
                                                    }   
                                                </Select>
                                        </FormControl>
                                </Box>
                                <Box display='flex' justifyContent='flex-start'  m={1}>
                                    <FormControl>
                                        <InputMask mask='99/99/9999'
                                                    maskChar=''
                                                    value={reportFilters.initialDate} 
                                                    onChange={event=> handleChange(event)}
                                                    >
                                                    {(inputProps) =>
                                                        <TextField  {...inputProps}
                                                                    id='initialDate' 
                                                                    name='initialDate'
                                                                    label='Data Inicial'
                                                                    autoComplete='off'
                                                                    className={classes.mediumTextField} />
                                                    }
                                                    
                                        </InputMask>
                                    </FormControl>
                                    <FormControl>
                                        <InputMask mask='99/99/9999'
                                                    maskChar=''
                                                    value={reportFilters.finalDate} 
                                                    onChange={event=> handleChange(event)}>
                                                    {(inputProps) =>
                                                        <TextField {...inputProps}
                                                                    id='finalDate' 
                                                                    name='finalDate'
                                                                    label='Data Final'
                                                                    autoComplete='off'
                                                                    className={classes.mediumTextField} />
                                                    }
                                        </InputMask>
                                    </FormControl>
                                </Box>
                                <Box display='flex' justifyContent='flex-start'  m={1}>
                                    <FormControl>
                                        <TextField 
                                            id='user' 
                                            name='user'
                                            label='Usurio'
                                            autoComplete='off'
                                            className={classes.mediumTextField}
                                            value={reportFilters.user} 
                                            onChange={event=> handleChange(event)}
                                            inputProps={{ maxLength: 20}}/>
                                    </FormControl>
                                </Box>
                                <Box display='flex' justifyContent='flex-end'>
                                    <Box m={1}>
                                        <Button id='filter' type='submit' size='small' variant='contained'
                                                color='primary' onClick={event=> handleFilter(event) }
                                                startIcon={<SearchIcon/>}>
                                                Pesquisar
                                        </Button>
                                    </Box>
                                    <Box m={1}>
                                        <Button id='close' type='submit' size='small' variant='contained'
                                                onClick={event=> handleCloseFilter(event)}
                                                startIcon={<CloseIcon/>}>
                                                Fechar
                                        </Button>
                                    </Box>
                                </Box>
                </Paper>
            </Box>
        </Fade>
        <Snackbar  anchorOrigin={{vertical: 'bottom',horizontal: 'center'}}
                open={visibility}  autoHideDuration={3000} onClose={handleCloseSnack} message={snackInfo}
                action={
                <React.Fragment>
                    <IconButton size="small" aria-label="close" color="inherit" onClick={handleCloseSnack}>
                    <CloseIcon fontSize="small" />
                    </IconButton>
                </React.Fragment>
        }
        />    
        <Box>
            {!loading && (
            <ReportViewer
                    titleReport='Movimentao de Chaves'
                    headCells={headCells}
                    landscapeView={true}
                    allRows={reportAvaliableKey}/>
            )}
        </Box>
        {loading && (
            <Box display="flex" justifyContent="center" m={1} p={1} >
            <Box p={1}>
                <CircularProgress/>
            </Box>
            </Box>
        )}
       
        <Box>
            <DisplayErrosMessage infoErrors={infoErrors} handleCloseErros={handleCloseErros}  />
            <DisplayInfoMessage serverValidation={serverValidation} handleCloseInfo={handleCloseInfo}/>
        </Box> 
    </Box>
    )}

 export default ViewKeyMonitorReport
