import React from 'react'
import {useState} from 'react'
import ViewMultiloadControlReport from './ViewMultiloadControlReport'
import moment from 'moment'
import formater from '../../../Assets/js/formater'
import {getApi} from '../../../Assets/js/apiProxy'
import {infoError} from '../../../Assets/js/infoError'

function MultiloadControlReport(props) {

    const api_feature = getApi('api_features')
    const api_overfill = getApi('api_multiloadControl')

    const [reportFilters,setReportFilters] = useState({initialDate:  moment().format('DD/MM/YYYY') ,finalDate: moment().format('DD/MM/YYYY'),
                                                       bayName:null,
                                                       user:null,
                                                       action:null})
    
    const [reportMultiloadData,setReportMultiloadData] = useState([])
    const [infoErrors,setInfoErrors] = useState({msg:''})
    const [serverValidation,setServerValidation] = useState({msg:''})
    const [loading,setLoading] = useState(false)

    const [snackInfo,setSnackInfo] = useState('')
    const [visibility, setVisibility] = useState(false)

      
    function getreportMultiloadData(reportFilters) {
        
        setReportMultiloadData([])
        setLoading(true)
        
        let adjustedFilters = { 
            initialDate: formater(reportFilters.initialDate,'date'),
            finalDate: formater(reportFilters.finalDate,'date'),
            bayName: reportFilters.bayName,
            user: reportFilters.user,
            action: reportFilters.action
        }

        api_feature.post(`/getConfigParameter`,{"parameter": "PurgeReport"})
        .then((reg)=> {

                    let purgeDays = reg.data.registers.length > 0 ? parseInt(reg.data.registers[0].parameterValue) : 100

                    api_overfill.post(`/reportMultiloadControl/${purgeDays}`,adjustedFilters)
                    .then(function (response) {
                        if (response.data.bsApproved) {
                            if (response.data.registers !== undefined) { 
                                let data = response.data.registers.map((e)=> { 
                                            e.date = moment(e.date).format('DD/MM/YYYY hh:mm')
                                            e.actionDateON  = e.actionDateON ? e.actionDateON = moment(e.actionDateON).format('DD/MM/YYYY hh:mm') : null
                                            e.actionDateOFF = e.actionDateOFF ? e.actionDateOFF = moment(e.actionDateOFF).format('DD/MM/YYYY hh:mm') : null
                                            return(e)
                                        })
                                        setReportMultiloadData(data)
                            }
                        } else { 
                            setSnackInfo(response.data.msg)
                            setVisibility(true)
                        }
                        setLoading(false)
                    })                           
                    .catch(function (error) {  
                        let infoErrorsState = infoError(error)
                        console.log(infoErrorsState.msg)
                        setSnackInfo(infoErrorsState.msg)
                        setVisibility(true)
                        setLoading(false)
                    })
        })
        .catch((error)=> { 
            let infoErrorsState = infoError(error)
            console.log(infoErrorsState.msg)
            setSnackInfo(infoErrorsState.msg)
            setVisibility(true)
            setLoading(false)
         })

    }
        
    const  handleCloseSnack = ()=> {
        setVisibility(false)
     }

    function handleCloseErros() { 
        setInfoErrors({msg:''})
    }

    function handleChange(e) { 
            setReportFilters({...reportFilters,[e.target.name]: e.target.value})
    }

    function handleCloseInfo() { 
        setServerValidation({msg:''})
    }
    
    
    const handleFilter = ()=> {
        getreportMultiloadData(reportFilters)
        document.getElementById('fade').style.display = 'none'
    }

    return(<ViewMultiloadControlReport 
        reportMultiloadData={reportMultiloadData}
        infoErrors={infoErrors}
        serverValidation={serverValidation}
        reportFilters={reportFilters}
        handleCloseErros={handleCloseErros}
        handleCloseInfo={handleCloseInfo}
        handleChange={handleChange}
        handleFilter={handleFilter}
        handleCloseSnack={handleCloseSnack}
        visibility={visibility}
        snackInfo={snackInfo}
        loading={loading}
    />)
}

export default MultiloadControlReport