import { makeStyles } from '@material-ui/core/styles'
import {grey} from '@material-ui/core/colors'

const StyledUpdatePermission = makeStyles((theme) => ({
  root: {
    "& .MuiTextField-root": {
      margin: theme.spacing(2),
    }
  },
  MainBox :{
    display:'flex',
    justifyContent:'center',
    margin: theme.spacing(1),
    padding: theme.spacing(1),
  },
  PaperMain: { 
    width:'50%',
    paddingBottom: theme.spacing(2),
    [theme.breakpoints.down('md')]: { 
      width: '100%' 
  }},
  color: {
    color: theme.palette.primary.main
  },
  ContaineFormControls : {
    display:'Flex',
    justifyContent:'center',
    flexWrap:'wrap',
    flexDirection:'column',
  },
  SwitchTitle: {
    color: grey[900],
    fontSize: '1rem',
    [theme.breakpoints.down('md')]: {
      fontSize: '0.8rem',
    },
  },
  SwitchValue: {
    color: grey[700],
    fontSize: '1rem',
    [theme.breakpoints.down('md')]: {
      fontSize: '0.8rem',
    },
  },
    SwitchContainerTitle: { 
    display:'flex',
    justifyContent:'center',
    marginTop: theme.spacing(1),
    backgroundColor:  grey[200],
    padding: theme.spacing(1),
  },
  SwitchContainerValue: { 
    display:'flex',
    justifyContent:'center',
    marginTop: theme.spacing(1),
    marginLeft: theme.spacing(1),
  },
    SwitchBox: {
    display:'flex',
    flexDirection:'row',
  }
}))


export default StyledUpdatePermission




