import React from 'react'
import StyledReadRoutePermission from './StyledReadRoutePermission'
import {Box,Container} from '@material-ui/core'
import EnhancedTableRows from '../../../../ComponentsLibrary/EnhancedTable/EnhancedTableRows/EnhancedTableRows'
import Snackbar from '@material-ui/core/Snackbar'
import IconButton from '@material-ui/core/IconButton'
import CloseIcon from '@material-ui/icons/Close'
import FeaturedPlayListIcon from '@material-ui/icons/FeaturedPlayList';
import DisplayErrosMessage from '../../../../ComponentsLibrary/DisplayErrorsMessage/DisplayErrorsMessage'

  const ViewReadRoutePermission = (props) => {
        const {route,DeletedRegister,loading,handleClose,infoErrors,snackInfo,
               handleCloseSnack,visibility} = props
         
        const styledReadRoutePermission=StyledReadRoutePermission()

        //Colunas da Tabela 
        const headCells = [
            { id: 'route',disablePadding: false, label: 'Rota', align:'left', width:'40%'},
            { id: 'description',disablePadding: false, label: 'Descrio', align:'left',width:'60%'},
        ]
     
      return(
        <Container maxWidth="lg">
            <Box className={styledReadRoutePermission.MainBox}>
              <EnhancedTableRows 
                  rows={route}
                  headCells={headCells}
                  tableTitle={'Funcionalidades'}
                  DeletedRegister={DeletedRegister}
                  api_url='/deleteRoute'
                  icon={<FeaturedPlayListIcon fontSize='large' className={styledReadRoutePermission.color}/>}
                  pathEditComponentForUpdate = '/UpdateRoute'
                  pathAddComponentForUpdate = '/addRoute'
                  routeName='Route'
                  showAddRegisterCommand={true}  
                  showDeleteRegisterCommand={true}
                  showEditRegisterCommand={true}
                  titleReport='Cadastro de Funcionalidades'
                  landscapeView={true}
                  loading={loading}>
                </EnhancedTableRows> 
            </Box>
            <Box className={styledReadRoutePermission.MainBox}>
              <DisplayErrosMessage infoErrors={infoErrors} handleCloseErros={handleClose}  />
            </Box>
            <Snackbar  anchorOrigin={{vertical: 'bottom',horizontal: 'center'}}
                      open={visibility}  autoHideDuration={3000} onClose={handleCloseSnack} message={snackInfo.message}
                      action={
                            <React.Fragment>
                                <IconButton size="small" aria-label="close" color="inherit" onClick={handleCloseSnack}>
                                <CloseIcon fontSize="small" />
                                </IconButton>
                            </React.Fragment>
                         }
                         />
            
        </Container>
      )}

export default ViewReadRoutePermission
